<template>
  <div class="special-detail">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click.prevent="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox">{{ row ? row.spname : '' }}</div>
    </header>

    <div class="header-box">
      <div class="avatar-box" v-if="row">
        <img v-lazy="row.coverpic">
        <dl>
          <dt>
            <h1>{{ row.spname }} <em>{{ row.cup|getCup }}</em></h1>
            <h2>{{ row.intro }}</h2>
          </dt>
          <dd>
            <em @click="specialUp">{{ row.upnum }}</em>
          </dd>
        </dl>
      </div>
    </div>
    <div class="rowbox">
      <VodItem2 :rows="vodrows" :addmore="false"></vodItem2>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import VodItem2 from '@/components/vod-item2.vue';

export default {
  name: 'SpecialDetail',
  components: {VodItem2},
  data() {
    return {
      row: null,
      vodrows: [],
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    specialUp(e){
      var self = this;
      HttpApi.specialUp(self.row['spid'])
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        let upnum = parseInt(e.target.innerText);
        e.target.innerText = upnum + 1;
      });
    }
  },
  filters: {
    getCup: function(cup){
      var cups = ['', 'A', 'B', 'C', 'D', 'E', 'F', 'G'];
      return cups[cup];
    }
  },
  mounted() {
    var self = this;
    HttpApi.getSpecialDetail(this.$route.params.spid)
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        return;
      }
      self.row = res.json['data']['row'];
      self.vodrows = res.json['data']['vodrows'];

    });

    window.onscroll = function(){
      var header = document.querySelector('.special-detail header');
      var titlebox = document.querySelector('.titlebox');
      if(!header || !titlebox){
        return;
      }
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset;
      if(scrollTop > 50.0){
        header.style.backgroundColor = '#26292f';
        titlebox.style.display = 'block';
      }else{
        header.style.backgroundColor = 'transparent';
        titlebox.style.display = 'none';
      }
    };
  },
}
</script>

<style scoped>
.special-detail{overflow: hidden;}
.special-detail header.navbar{background: transparent;}
.special-detail header .titlebox{display: none;}
.header-box{position: relative; width: 100%; height: 0; padding-top: 61%;}
.avatar-box{position: absolute; left: 0; top: 0; width: 100%; height: 100%;}
.avatar-box img{width: 100%; height: 100%;}
.avatar-box dl{position: absolute; left: 0; bottom: 0; width: 100%;}
.avatar-box dt{padding: 0 60px 10px 10px; color: #fff; line-height: 22px;}
.avatar-box dt h1{font-size: 20px; font-weight: 800;}
.avatar-box dt h1 em{display: inline-block; margin-left: 2px; padding: 1px 10px; border: 1px solid #ff3d6f; border-radius: 20px; color: #ff3d6f; font-size: 14px;}
.avatar-box dt h2{margin-top: 5px; font-size: 14px; color: #fff; font-weight: 300; line-height: 18px;}
.avatar-box dd{position: absolute; min-width: 40px; right: 10px; bottom: 10px;}
.avatar-box dd em{display: block; height: 40px; padding-top: 22px; text-align: center; color: #fff; font-size: 14px; background: url('/static/icons/icon_special_zan.png') center top no-repeat; background-size: 26px 24px;}

.rowbox{margin: 15px 5px 10px 5px;}
</style>



