<template>
  <div class="mybox">
    <div class="avatar-container">
      <header class="navbar">
        <div class="actionbox">
          <router-link to="/ucp/setting/index" class="search"><img src="/static/icons/icon_setting.png"></router-link>
        </div>
      </header>
      <div class="avatarbox">
        <dl>
          <dt><img :src="getAvatar(user.avatar_url)" v-if="user"></dt>
          <dd>
            <h1>{{ (user == null || user.uid == 0) ? '' : (user.nickname ? user.nickname : user.username) }}</h1>
            <h2 v-if="user && user.uid > 0">{{ user.uniqkey }}</h2>
          </dd>
        </dl>
        <div class="medalbox" v-if="user">
          <a href="#" v-if="user.isvip"><img src="/static/icons/gcion_isvip.png"></a>
          <a href="#" v-if="user.gicon=='V1'||user.gicon=='V2'||user.gicon=='V3'||user.gicon=='V4'||user.gicon=='V5'"><img :src="'/static/icons/gicon_'+user.gicon+'.png'"></a>
        </div>
      </div>
    </div>

    <div class="walletbox">
      <div class="coinshow">
        <h5><img src="/static/icons/icon_bi.png"> <i></i> <em>{{ user ? user.goldcoin : 0 }}</em></h5>
        <h6></h6>
      </div>
      <div class="coinbox">
        <h5><router-link to="/ucp/account/index"></router-link></h5>
        <h6>101</h6>
      </div>
      <span></span>
    </div>

    <div class="usercenter">
      <h2 class="list-caption">
        <em></em>
        <span><router-link to="/ucp/affcenter" v-if="uinfo">{{ uinfo.next_upgrade_need }}</router-link></span>
      </h2>
      <div class="bannerbox">
        <router-link to="/ucp/affcenter"><img src="/static/icons/pic_tshydj.png"></router-link>
      </div>
      <div class="infobox">
        <div class="viewbox">
          <h5><em>{{ uinfo ? uinfo.play_daily_remainders : 0 }}</em></h5>
          <h6></h6>
        </div>
        <div class="signbox">
          <h5><a href="#" @click.prevent="taskSign" :class="{signed: signed==1}">{{ signed==1 ? '' : '' }}</a></h5>
          <h6></h6>
        </div>
        <span></span>
      </div>
    </div>

    <div class="swiper-bannerbox">
      <div class="swiper-bannerbox-wrapper">
        <swiper :options="swiperOption" v-if="swiperSlides.length">
          <swiper-slide v-for="(slide, index) in swiperSlides" :key="index">
            <a href="#" @click.prevent="slideClick(slide)"><img :src="slide.pic"></a>
          </swiper-slide>
        </swiper>
      </div>
      <div class="swiper-pagination-box"><div class="swiper-pagination" slot="pagination"></div></div>
    </div>

    <ul class="gridbox">
      <li><router-link to="/ucp/vippkg/index"><span><img src="/static/icons/icon_hycz.png"><h6></h6></span></router-link></li>
      <li><router-link to="/ucp/vippkg/coinorder"><span><img src="/static/icons/icon_hydh.png"><h6></h6></span></router-link></li>
      <li><router-link to="/ucp/task"><span><img src="/static/icons/icon_rwzx.png"><h6></h6></span></router-link></li>
      <li><router-link to="/favorite/listing"><span><img src="/static/icons/icon_wdsc.png"><h6></h6></span></router-link></li>
      <li><router-link to="/playlog/listing"><span><img src="/static/icons/icon_ucp_gkjl.png"><h6></h6></span></router-link></li>
      <li><router-link to="/ucp/feedback"><span><img src="/static/icons/icon_jyfk.png"><h6></h6></span></router-link></li>
      <li><router-link to="/ucp/message"><span><img src="/static/icons/icon_xxzx.png"><h6></h6></span></router-link></li>
      <li><a href="https://potato.im/joinchat/2801709e288c43b2ad02a1dd914b1795" target="_blank"><span><img src="/static/icons/potato.png"><h6></h6></span></a></li>
      <li><router-link to="/ucp/account/index"><span><img src="/static/icons/icon_xjtx.png"><h6></h6></span></router-link></li>
    </ul>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import Util from '@/util.js';

export default {
  name: 'index',
  components: {},
  data() {
    return {
      swiperOption: {
        loop: true,
        speed: 500,
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        },
        pagination: {
          el: '.swiper-pagination'
        }
      },
      swiperSlides: [],
      user: null,
      uinfo: null,
      signed: 0, //
    }
  },
  methods: {
    getAvatar(avatar_url){
      return Util.getAvatar(avatar_url);
    },
    slideClick(slide){
      if(slide['scene'] == 'vod.show'){
        return this.$router.push('/vod/show/' + slide['vodid']);
      }
      if(slide['scene'] == 'vod.listing'){
        return this.$router.push('/vod/listing-' + slide['params']);
      }
      if(slide['scene'] == 'web.url'){
        return window.open(slide['url'], '_blank');
      }
      if(slide['scene'] == 'special.detail'){
        return this.$router.push('/special/detail/' + slide['spid']);
      }
      if(slide['scene'] == 'taskbox.index'){
        return this.$router.push('/ucp/taskbox/index');
      }
      if(slide['scene'] == 'promotion.index'){
        return this.$router.push('/ucp/promotion/index');
      }
    },
    taskSign(e){
      var self = this;
      HttpApi.taskSign()
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        e.target.innerText = '';
        self.fetchData();
      });
    },

    fetchData(){
      var self = this;
      HttpApi.ucpIndex()
      .then(function(res){
        if(res.errcode != 0 || res.json['retcode'] != 0){
          return;
        }
        self.user = res.json['data']['user'];
        self.uinfo = res.json['data']['uinfo'];
        self.signed = res.json['data']['signed'];
      });
    }
  },
  mounted() {
    this.fetchData();
    this.swiperSlides = [
      {scene: 'taskbox.index', pic: '/static/icons/baoxiang-01.png'},
      {scene: 'promotion.index', pic: '/static/icons/uplevel.png'}
    ];
  },
  activated(){ //keep-alive 
    this.fetchData();
  }
}
</script>

<style scoped>
.mybox{margin-bottom: 66px; color: #fff;}
.mybox header.navbar{background: transparent; position: relative;}

.avatar-container{padding: 0 0 40px 0; background-image: url('/static/icons/my_bg.png'); background-repeat: no-repeat; background-size: cover;}
.avatarbox{margin: 20px 10px 0 10px; position: relative;}
.avatarbox dl{overflow: hidden; margin-left: 5px;}
.avatarbox dt{width: 50px; height: 50px; float: left;}
.avatarbox dt img{width: 50px; height: 50px; border-radius: 50%;}
.avatarbox dd{height: 50px; margin-left: 60px;}
.avatarbox dd h1{font-size: 16px; line-height: 25px; color: #fff;}
.avatarbox dd h2{font-size: 14px; line-height: 25px; color: #928f95;}
.medalbox{height: 50px; position: absolute; right: 10px; top: 5px; text-align: right;}
.medalbox a{display: inline-block; margin-left: 5px; font-size: 0; line-height: 0;}
.medalbox a img{width: 40px; height: 40px; font-size: 0; line-height: 0;}

.walletbox{position: relative; height: 70px; background: #191a1f;}
.walletbox .coinshow{width: 50%; float: left; height: 70px;}
.walletbox .coinbox{width: 50%; float: left; height: 70px;}
.walletbox .coinshow h5{margin-top: 10px; height: 25px; line-height: 25px; text-align: center; color: #9b9b9c; font-size: 14px;}
.walletbox .coinshow h5 i{color: #9b9b9c; margin: 0 5px; font-weight: 300;}
.walletbox .coinshow h5 em{font-size: 18px; color: #fff; vertical-align: bottom;}
.walletbox .coinshow h5 img{width: 20px; height: 20px; vertical-align: middle;}
.walletbox .coinshow h6{height: 25px; line-height: 25px; text-align: center; color: #9b9b9c; font-size: 12px; font-weight: 300;}
.walletbox .coinbox h5{margin-top: 12px; height: 25px; line-height: 25px; text-align: center; color: #ffbb26;}
.walletbox .coinbox h5 a{display: inline-block; padding: 0 15px; font-size: 12px; line-height: 24px; border: 1px solid #ffbb26; border-radius: 15px; color: #ffbb26;}
.walletbox .coinbox h6{height: 25px; line-height: 25px; text-align: center; color: #9b9b9c; font-size: 12px; font-weight: 300;}
.walletbox span{position: absolute; left: 50%; top: 15px; width: 1px; height: 40px; background: #2f3236;}

.usercenter{background: #212429;}
.list-caption{padding-top: 10px; height: 40px; position: relative; font-size: 12px; text-indent: 10px; line-height: 30px;}
.list-caption em{font-size: 14px; color: #fff;}
.list-caption span{position: absolute; right: 10px;}
.list-caption span a{color: #929397; padding: 0 12px; font-size: 12px; font-weight: 300; background: url('/static/icons/arrow.png') right center no-repeat; background-size: 7px 12px;}

.bannerbox{padding: 10px 0;}
.bannerbox a{font-size: 0;}
.bannerbox img{width: 100%; display: block;}

.swiper-bannerbox{margin: 10px 0; position: relative; width: 100%; height: 0; padding-top: 23%;}
.swiper-bannerbox-wrapper{position: absolute; left: 0; top: 0; right: 0; bottom: 0;}
.swiper-bannerbox .swiper-slide img{display: block; width: 100%; height: 100%; border-radius: 10px;}
.swiper-bannerbox .swiper-pagination-box{position: absolute; bottom: 0; z-index: 1; width: 100%; height: 30px; line-height: 30px; text-align: center;}


.infobox{position: relative; height: 70px;}
.infobox .viewbox{width: 50%; float: left; height: 70px;}
.infobox .signbox{width: 50%; float: left; height: 70px;}
.infobox .viewbox h5{margin-top: 10px; height: 25px; line-height: 25px; text-align: center; color: #9b9b9c; font-size: 14px;}
.infobox .viewbox h5 em{font-size: 20px; color: #fff;}
.infobox .viewbox h6{height: 25px; line-height: 25px; text-align: center; color: #9b9b9c; font-size: 12px; font-weight: 300;}
.infobox .signbox h5{margin-top: 12px; height: 25px; line-height: 25px; text-align: center; color: #ff3d6f; font-size: 14px;}
.infobox .signbox h5 a{display: inline-block; padding: 0 15px; line-height: 22px; font-size: 12px; border: 1px solid #90314c; border-radius: 15px; color: #ff3d6f;}
.infobox .signbox h5 a.signed{color: #aaa; border-color: #aaa;}
.infobox .signbox h6{height: 25px; line-height: 25px; text-align: center; color: #9b9b9c; font-size: 12px; font-weight: 300;}
.infobox span{position: absolute; left: 50%; top: 15px; width: 1px; height: 40px; background: #2f3236;}

.gridbox{overflow: hidden; position: relative; background: #212429;}
.gridbox li{float: left; width: 33.33%; padding-top: 28%; position: relative; border-left: 1px solid #2f3236; border-top: 1px solid #2f3236;}
.gridbox li:nth-child(3n-2){border-left: 0;}
.gridbox li:nth-child(-n+3){border-top: 0;}
.gridbox li a{position: absolute; top:0; left: 0; width: 100%; height: 100%; color: #aaaaaa; display: flex; justify-content: center; align-items: center;}
.gridbox li a img{width: 35px; height: 35px;}
.gridbox li a span{line-height: 1.5; text-align: center;}
.gridbox li a span h6{font-weight: normal; font-size: 12px; margin-top: 5px; font-weight: 300;}
.gridbox li a span i{font-size: 12px; color: #aaaaaa;}

</style>



