<template>
  <div class="bonuslog-listing">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
    </header>
    <div class="pad56"></div>

    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
    <div class="listbox">
      <table>
        <thead>
          <tr><th></th><th></th><th></th><th></th></tr>
        </thead>
        <tbody>
          <tr v-for="row in logrows" :key="row.logid">
            <td>{{ row.addtime.split(' ')[0] }}</td>
            <td>{{ row.invited_username }}</td>
            <td>{{ row.cointype }}</td>
            <td>{{ row.coinnum }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    </mt-loadmore>

    <div class="statsbar-pad"></div>
    <div class="statsbar" v-if="addinfo">
      <h2><em>{{ addinfo.inviteTotal}}</em></h2>
      <h2><em>{{ addinfo.activeTotal}}</em></h2>
      <span><em>{{ addinfo.bonusTotal }}</em></span>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'coinlogbonuslog',
  data() {
    return {
      user: globalData.user,
      pageInfo: null,
      logrows: [],
      addinfo: null,
      allLoaded: false,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    loadTop(){
      var self = this;
      HttpApi.coinlogBonuslog(1)
      .then(function(res){
        self.$refs.loadmore.onTopLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        var newIds = [];
        var keyId = 'logid';
        for(let i = 0; i < 20; i++){
          if(i >= self.logrows.length){
            break;
          }
          newIds.push(self.logrows[i][keyId]);
        }

        let newRows = res.json['data']['logrows'].reverse();
        for(let i = 0; i < newRows.length; i++){
          var isNew = true;
          for(let j = 0; j < newIds.length; j++){
            if(newRows[i][keyId] == newIds[j]){
              isNew = false;
            }
          }
          if(isNew){
            self.logrows.unshift(newRows[i]);
          }
        }
      });
    },
    loadBottom(){
      var self = this;
      HttpApi.coinlogBonuslog(self.pageInfo['page']+1)
      .then(function(res){
        self.$refs.loadmore.onBottomLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.logrows.push(...res.json['data']['logrows']);
        self.pageInfo = res.json['data']['pageinfo'];

        if(self.pageInfo['page'] == self.pageInfo['totalpage']){
          self.allLoaded = true;
        }
      });
    }
  },
  mounted() {
    var self = this;
    HttpApi.coinlogBonuslog(1)
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.pageInfo = res.json['data']['pageinfo'];
      self.logrows = res.json['data']['logrows'];
      self.addinfo = res.json['data']['addinfo'];

      if(self.pageInfo['page'] == self.pageInfo['totalpage']){
        self.allLoaded = true;
      }
    });
  },
}
</script>

<style scoped>
.bonuslog-listing{color: #fff; height:100vh; overflow-y: scroll; -webkit-overflow-scrolling: touch;}

.listbox{min-height: 300px;}
.listbox table{width: 100%; font-size: 12px; border: none; border-collapse: collapse; border-spacing: 0;}
.listbox table th, .listbox table td{border: none; padding: 10px 10px;}
.listbox table th{text-align: center;}
.listbox thead tr{background: #212429; color: #ff3d6f; font-size: 14px;}
.listbox tbody td{text-align: center; color: #aaa;}
.listbox tbody td.blink{color: #ffa800;}

.statsbar-pad{height: 56px;}
.statsbar{width: 100%; height: 56px; font-weight: 300; position: fixed; z-index: 99; bottom: 0; font-size: 16px; line-height: 56px; color: #fff; background: linear-gradient(to right, #ad2e5a, #ad4047);}
.statsbar h2{padding-left: 15px; height: 23px; line-height: 23px; font-size: 14px; color: #fff; font-weight: 300;}
.statsbar h2:first-child{margin-top: 5px;}
.statsbar h2 em{font-size: 18px; padding: 0 3px;}
.statsbar span{position: absolute; top: 0; bottom: 0; right: 15px; font-size: 14px; color: #fff; font-weight: 300;}
.statsbar span em{margin-left: 3px;}
</style>



