<template>
  <div class="specialbox">
   

    <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
    <div class="starbox">

        <div class="staritem" v-for="row in actorRows" :key="row.id" >
            <img v-lazy="row.cover" @click="toCateList(row)">
            <div style="text-align:center;">{{ row.name}}</div>
        </div>

  
    </div>
    </mt-loadmore>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import VodItem2 from '@/components/vod-item2.vue';
import api from '@/api.js';

export default {
  name: 'Special',
  components: {VodItem2},
  data() {
    return {
      pageInfo: null,
      actorRows: [],
      tagRows: [],
      allLoaded: false
    }
  },
  computed: {

  },
  filters: {
    getCup: function(cup){
      var cups = ['', 'A', 'B', 'C', 'D', 'E', 'F', 'G'];
      return cups[cup];
    }
  },
  mounted() {
    this.fetchData();
  },

  methods: {

    fetchData(){

      api.getStars().then((res)=>{
          this.actorRows=res.data.data;
      })
      var self = this;
     


     
    },
    toCateList(item){
      window.$catetitle=item.name;
      window.$catepath="actors/"+item.id+"/videos"
      this.$router.push("catevideolist")
    },
    loadBottom(){
      var self = this;
    
    },
  }
}
</script>

<style scoped>
.specialbox{padding: 56px 0 66px 0; height:100vh; overflow: scroll; -webkit-overflow-scrolling: touch;}
.actorbox{background: #212429; padding: 10px 0;}
.actorbox ul{overflow: hidden; white-space: nowrap; overflow-x:scroll; overflow-y: hidden; -webkit-overflow-scrolling: touch;}
.actorbox ul::-webkit-scrollbar{display: none;}
.actorbox li{display: inline-block; margin: 0 10px;}
.actorbox li:first-child{margin-left: 15px;}
.actorbox li a{display: block;}
.actorbox li a>div{display: block; position: relative; width: 65px; height: 65px; padding: 2px; color: #aaaaaa;}
.actorbox li a img{display: block; width: 100%; height: 100%; border-radius: 50%;}
.actorbox li a cite{position: absolute; left: 0; right: 0; top: 0; bottom: 0; background: transparent url('/static/icons/actor_avatar_bg.png') center center no-repeat; background-size: 65px 65px;}
.actorbox li a i{position: absolute; right: 0; bottom: 0; width: 17px; height: 17px; line-height: 17px; text-align: center; color: #fff; font-weight: bold; font-size: 12px;}
.actorbox li a h6{margin-top: 5px; font-weight: 300; font-size: 12px; color: #fff; text-align: center; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;}

.tag-container{overflow: hidden; margin-top: 5px;}
.tagbox{padding: 10px 5px;}
.vodbox{margin-top: 10px;}
.list-caption{height: 30px; position: relative; color: #fff; font-size: 14px; text-indent: 20px; line-height: 30px; background: url('/static/icons/icon_xmfh.png') left center no-repeat; background-size: 15px 15px;}
.list-caption em{font-size: 16px}
.list-caption span{position: absolute; right: 10px;}
.list-caption span a{color: #929397; padding: 0 12px; background: url('/static/icons/arrow.png') right center no-repeat; background-size: 7px 12px;}

.starbox{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.staritem {
    width: 20%;
    
}
.staritem img {
    width: 80%;
    height: auto;
    border-radius: 50%;
}

</style>



