<template>
  <div class="msgbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
    </header>
    <div class="showbox" v-if="row">
      <dl v-if="row.payid==0">
        <dt>
          <h1>{{ user.nickname ? user.nickname : user.username }}</h1>
          <h2>{{ row.ctimestamp }}</h2>
          <div class="textblock">{{ row.content }}</div>
        </dt>
        <dd v-if="row.replytime"><label>:</label> {{ row.replytext }}</dd>
      </dl>
      <div class="showlist" v-if="row.payid>0">
        <ul>
          <li>
            <label></label>
            <span>{{ getType(row.cid) }}</span>
          </li>
          <li>
            <label></label>
            <span>{{ row.payid }}</span>
          </li>
          <li>
            <label></label>
            <span>{{ row.itemname }}</span>
          </li>
          <li>
            <label></label>
            <span>{{ row.paidtime ? row.paidtime : row.ctimestamp }}</span>
          </li>
          <li>
            <label></label>
            <span>{{ row.payname }}</span>
          </li>
          <li>
            <label></label>
            <span>{{ row.payaccount }}</span>
          </li>
        </ul>
        <div class="introbox">{{ row.content }}</div>
        <div class="uploadbox">
          <img :src="picurl" v-for="(picurl, index) in picurls" :key="index">
        </div>
        <div class="replytext" v-if="row.replytime"><label>:</label> {{ row.replytext }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'feedback-detail',
  data() {
    return {
      user: globalData.user,
      row: null,
      picurls: [],
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    getType(cid){
      let types = ['', '', '', '', '', '', '', ''];
      return types[cid];
    }
  },
  mounted() {
    var self = this;
    HttpApi.feedbackDetail(this.$route.query.id)
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        return;
      }
      self.row = res.json['data']['row'];
      self.picurls = res.json['data']['picurls'];
    });
  },
}
</script>

<style scoped>
.detailbox{color: #fff;}

.showbox{margin: 66px 0 0 0;}
.showbox dl{margin-top: 10px; padding: 10px 10px 0 10px; background: #212429;}
.showbox dt h1{height: 18px; line-height: 18px; font-size: 12px; color: #fff; padding-top: 10px;}
.showbox dt h2{height: 18px; line-height: 18px; font-size: 12px; color: #aaaaaa; margin-top: 10px;}
.showbox dt .textblock{padding: 5px 0 10px 0; line-height: 22px; font-size: 14px; color: #fff;}
.showbox dd{padding: 10px 0; border-top: 1px solid #2f3236; color: #ff3d6f; line-height: 22px; font-size: 14px;}

.showlist ul{padding: 0 15px; background: #212429;}
.showlist li{overflow: hidden; font-size: 14px; border-top: 1px solid #2f3236;}
.showlist li:first-child{border-top: 0;}
.showlist li label{float: left; width: 4.5em; height: 45px; line-height: 45px; color: #fff;}
.showlist li span{display: block; margin-left: 4.5em; height: 45px; line-height: 45px; color: #aaa; text-align: right;}
.showlist li span em{display: inline-block; padding-right: 10px; background: url('/static/icons/arrow.png') right center no-repeat; background-size: 7px 13px;}
.showlist .introbox{margin-top: 10px; padding: 10px 15px; font-size: 14px; color: #aaa; background: #212429;}

.uploadbox{padding-top: 10px; padding-bottom: 10px; overflow: hidden; background: #212429;}
.uploadbox img{width: 100px; height: 100px; float: left; margin-left: 15px;}
.replytext{padding: 10px 15px; color: #ff3d6f; line-height: 22px; font-size: 14px;}

</style>



