<template>
  <div class="taskbox-index">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
    </header>
    <div class="introbox">
      <div class="annbox">
        <swiper :options="swiperOption" v-if="logrows.length">
          <swiper-slide v-for="logrow in logrows" :key="logrow.logid">
            {{ logrow.nickname ? logrow.nickname : logrow.username }}{{ logrow.tasktype }} <em>{{ logrow.addcoin }}</em>
          </swiper-slide>
        </swiper>
      </div>
      <ul class="taskbox1">
        <li v-for="taskrow in taskrows1" :key="taskrow.taskid" @click.prevent="taskboxOpen(taskrow)">
          <img :src="taskrow.taskstatus==2 ? '/static/icons/dkbx.png' : '/static/icons/bhbx.png'">
          <h2>{{ taskrow.taskname }}</h2>
          <h3>
            <a href="#" :class="{todo: taskrow.taskstatus==0, opened: taskrow.taskstatus==2}">{{ taskrow.taskstatus==0 ? '' : (taskrow.taskstatus==1 ? '' : '') }}</a>
          </h3>
        </li>
      </ul>

      <ul class="taskbox2">
        <li v-for="taskrow in taskrows2" :key="taskrow.taskid" @click.prevent="taskboxOpen(taskrow)">
          <div class="imgbox">
            <img :src="taskrow.taskstatus==2 ? '/static/icons/dkbx.png' : (taskrow.taskid == 1022 ? '/static/icons/mrsmbx.png' : '/static/icons/mzsmbx.png')">
            <em v-if="taskrow.taskstatus==1">{{ countdown() }}</em>
          </div>
          <h2>{{ taskrow.taskname }}</h2>
          <h3>
            <a href="#" :class="{todo: taskrow.taskstatus==0, opened: taskrow.taskstatus==2}">{{ taskrow.taskstatus==0 ? '' : (taskrow.taskstatus==1 ? '' : '') }}</a>
          </h3>
        </li>
      </ul>
      <h6 class="tipsbox">5</h6>
    </div>

    <div class="bottombar">
      <dl>
        <dt></dt>
        <dd><router-link to="/ucp/promotion/qrpic"></router-link></dd>
      </dl>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'taskbox-index',
  data() {
    return {
      swiperOption: {
        direction: 'vertical',
        loop: true,
        speed: 500,
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        }
      },
      taskrows: [],
      logrows: [],
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    countdown(){
      let date = new Date();
      let h = date.getHours(); //
      let m = date.getMinutes(); //
      let s = date.getSeconds(); //

      if(h == 22 && m < 5){
        return (m < 10 ? '0'+m : m) + ':' + (s < 10 ? '0'+s : s);
      }
      return null;
    },
    taskboxOpen(taskrow){
      if(taskrow['taskstatus'] != 1){
        return;
      }
      var self = this;
      HttpApi.taskboxOpen(taskrow['taskid'])
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        //
        self.$toast('');
        setTimeout(function(){
          self.$router.replace('/ucp/coinlog/bonuslog');
        }, 1500);

      });
    }
  },
  computed: {
    taskrows1: function(){
      return this.taskrows.filter(function(taskrow){
        return taskrow.taskid < 100;
      });
    },
    taskrows2: function(){
      return this.taskrows.filter(function(taskrow){
        return taskrow.taskid > 100;
      });
    }
  },
  mounted() {
    var self = this;
    HttpApi.taskboxIndex()
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.taskrows = res.json['data']['taskrows'];
      self.logrows = res.json['data']['logrows'];
    });
  },
}
</script>

<style scoped>
.taskbox-index{color: #fff; position: absolute; left: 0; top: 0; width: 100%; height: 100%; background: url('/static/icons/taskbox-bg.png') center center no-repeat; background-size: 100% 100%;}
.taskbox-index header.navbar{background: transparent;}

.introbox{position: absolute; top: 28%; left: 5%; right: 5%; bottom: 12.5%; color: #aaa; overflow-y: scroll; -webkit-overflow-scrolling: touch;}
.annbox{margin: 0 5px; height: 36px; padding-left: 12px; line-height: 36px; color: #fff; background: rgba(61, 1, 124, 0.75) url('/static/icons/icon_gg.png') 5px center no-repeat; background-size: 14px 13px; overflow: hidden; border-radius: 5px;}
.annbox .swiper-slide{height: 36px; padding: 0 10px; font-size: 14px; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;}
.annbox .swiper-slide em{display: inline-block; margin-left: 3px; padding-left: 18px; background: url('/static/icons/icon_bi.png') left center no-repeat; background-size: 14px 14px;}

.taskbox1{margin-top: 10px; overflow: hidden;}
.taskbox1 li{margin-top: 20px; float: left; width: 33.33%; text-align: center;}
.taskbox1 li:nth-child(-n+3){margin-top: 5px;}
.taskbox1 li img{display: inline-block; width: 75px; height: 56px; font-size: 0; line-height: 0;}
.taskbox1 li h2{margin-top: 0px; height: 26px; line-height: 26px; font-size: 14px; color: #fff;}
.taskbox1 li h3{margin-top: 5px; height: 28px; line-height: 28px; font-size: 14px; color: #fff;}
.taskbox1 li h3 a{display: inline-block; width: 80px; padding: 0 15px; text-align: center; color: #fff; font-size: 14px; border-radius: 20px; background: linear-gradient(to right, #ff3c87, #ff9054);}
.taskbox1 li h3 a.opened{background: rgba(50, 0, 101, 0.9);}
.taskbox1 li h3 a.todo{background: rgba(108, 0, 217, 0.9);}

.taskbox2{margin-top: 30px; padding-bottom: 10px; overflow: hidden; border-top: 1px dashed #9c5cdc;}
.taskbox2 li{margin-top: 30px; float: left; width: 50%; text-align: center;}
.taskbox2 li .imgbox{position: relative; display: inline-block; width: 75px; height: 56px; font-size: 0; line-height: 0;}
.taskbox2 li img{display: inline-block; width: 75px; height: 56px; font-size: 0; line-height: 0;}
.taskbox2 li .imgbox em{position: absolute; right: 0; bottom: 0; width: 40px; height: 20px; line-height: 20px; font-size: 12px; text-align: center; color: #fff; background: rgba(33,33,33,0.75); border-radius: 10px 10px 10px 0;}
.taskbox2 li h2{margin-top: 0px; height: 26px; line-height: 26px; font-size: 14px; color: #fff;}
.taskbox2 li h3{margin-top: 5px; height: 28px; line-height: 28px; font-size: 14px; color: #fff;}
.taskbox2 li h3 a{display: inline-block; width: 80px; padding: 0 15px; text-align: center; color: #fff; font-size: 14px; border-radius: 20px; background: linear-gradient(to right, #ff3c87, #ff9054);}
.taskbox2 li h3 a.opened{background: rgba(50, 0, 101, 0.9);}
.taskbox2 li h3 a.todo{background: rgba(108, 0, 217, 0.9);}
.tipsbox{height: 30px; line-height: 30px; font-size: 12px; color: #9475b4; text-align: center;}

.bottombar{position: absolute; bottom: 0; width: 100%;}
.bottombar dl{margin: 0 15px 20px 15px; overflow: hidden;}
.bottombar dt{float: left; width: 45px; height: 45px; background: url('/static/icons/icon_lw.png') center center no-repeat; background-size: 45px 45px;}
.bottombar dd{margin-left: 55px; height: 45px; line-height: 45px;}
.bottombar dd a{display: block; color: #fff; line-height: 45px; text-align: center; background: linear-gradient(to right, #c10fd1, #f66b4e); border-radius: 5px;}

@media only screen and (max-width: 375px){
  .bottombar dl{margin-bottom: 10px;}
}
</style>



