<template>
  <div class="withdraw-listing">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
    </header>
    <div class="pad56"></div>

    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
    <div class="listbox">
      <table>
        <thead>
          <tr><th></th><th></th><th></th><th></th></tr>
        </thead>
        <tbody>
          <tr v-for="row in rows" :key="row.wdid">
            <td>{{ row.createtime.split(' ')[0] }}</td>
            <td>{{ row.bankname }}</td>
            <td :class="{blink: row.wdstatus==1}">{{ getStatus(row.wdstatus) }}</td>
            <td>{{ row.withdraw_amount }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    </mt-loadmore>

    <div class="statsbar-pad"></div>
    <h2 class="statsbar"> {{ withdrawTotal }}</h2>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'myfeedback',
  data() {
    return {
      user: globalData.user,
      pageInfo: null,
      rows: [],
      withdrawTotal: '0.00',
      allLoaded: false,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    getStatus(wdstatus){
      let wdstatuss = ['', '', '', '', '', ''];
      return wdstatuss[wdstatus];
    },
    loadTop(){
      var self = this;
      HttpApi.withdrawListing(1)
      .then(function(res){
        self.$refs.loadmore.onTopLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        var newIds = [];
        var keyId = 'wdid';
        for(let i = 0; i < 20; i++){
          if(i >= self.rows.length){
            break;
          }
          newIds.push(self.rows[i][keyId]);
        }

        let newRows = res.json['data']['rows'].reverse();
        for(let i = 0; i < newRows.length; i++){
          var isNew = true;
          for(let j = 0; j < newIds.length; j++){
            if(newRows[i][keyId] == newIds[j]){
              isNew = false;
            }
          }
          if(isNew){
            self.rows.unshift(newRows[i]);
          }
        }
      });
    },
    loadBottom(){
      var self = this;
      HttpApi.withdrawListing(self.pageInfo['page']+1)
      .then(function(res){
        self.$refs.loadmore.onBottomLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.rows.push(...res.json['data']['rows']);
        self.pageInfo = res.json['data']['pageinfo'];

        if(self.pageInfo['page'] == self.pageInfo['totalpage']){
          self.allLoaded = true;
        }
      });
    }
  },
  mounted() {
    var self = this;
    HttpApi.withdrawListing(1)
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        return;
      }
      self.pageInfo = res.json['data']['pageinfo'];
      self.rows = res.json['data']['rows'];
      self.withdrawTotal = res.json['data']['withdrawTotal'];

      if(self.pageInfo['page'] == self.pageInfo['totalpage']){
        self.allLoaded = true;
      }
    });
  },
}
</script>

<style scoped>
.withdraw-listing{color: #fff; height:100vh; overflow-y: scroll; -webkit-overflow-scrolling: touch;}
.listbox{min-height: 300px;}
.listbox table{width: 100%; font-size: 12px; border: none; border-collapse: collapse; border-spacing: 0;}
.listbox table th, .listbox table td{border: none; padding: 10px 10px;}
.listbox table th{text-align: center;}
.listbox thead tr{background: #212429; color: #ff3d6f; font-size: 14px;}
.listbox tbody td{text-align: center; color: #aaa;}
.listbox tbody td.blink{color: #ffa800;}

.statsbar-pad{height: 46px;}
.statsbar{width: 100%; height: 46px; text-align: center; font-weight: 300; position: fixed; z-index: 99; bottom: 0; font-size: 14px; line-height: 46px; color: #fff; background: linear-gradient(to right, #ad2e5a, #ad4047);}

</style>



