<template>
  <div class="affcenter">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
      <div class="actionbox">
        <router-link to="/ucp/myaff" class="text"></router-link>
      </div>
    </header>
    <div class="headerbox">
      <div class="cardbox">
        <div class="userbox" v-if="user">
          <dl>
            <dt>
              <img :src="getAvatar(user.avatar_url)">
              <h1>{{ user.uid == 0 ? '' : (user.nickname ? user.nickname : user.username) }}</h1>
              <h2>{{ user.uniqkey }}</h2>
            </dt>
            <dd>
              <img src="/static/icons/logo_login.png">
            </dd>
          </dl>
        </div>
        <div class="levelbox">
          <ul v-if="uinfo">
            <li class="curr-group"><em class="V1"></em></li>
            <li class="levelbar">{{ uinfo.next_upgrade_need }}</li>
            <li class="next-group"><em class="V1"></em></li>
          </ul>
        </div>
      </div>
    </div>
    <div class="permbox">
      <h2><img src="/static/icons/tsfl_left.png">  <img src="/static/icons/tsfl_right.png"></h2>
      <ul>
        <li :class="group.gicon" v-for="(group, index) in newGroups" :key="index">
          <h3 v-if="group.minup == 0"></h3>
          <h3 v-else><em>{{ group.minup }}</em></h3>
          <h4><em>{{ group.play_daynum }}</em><em>{{ group.down_daynum }}</em><em>{{ group.comment_daynum }}</em></h4>
        </li>
      </ul>
    </div>

    <div class="pad56"></div>
    <div class="bottombar">
      <h6><router-link to="/ucp/promotion/qrpic"></router-link></h6>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';
import Util from '@/util.js';

export default {
  name: 'affcenter',
  data() {
    return {
      user: null,
      uinfo: null,
      groups: [],
    }
  },
  methods: {
    getAvatar(avatar_url){
      return Util.getAvatar(avatar_url);
    },
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
  },
  computed: {
    newGroups: function(){
      return this.groups.filter(function(group){
        return group.gicon == 'V1' || group.gicon == 'V2' || group.gicon == 'V3' || group.gicon == 'V4' || group.gicon == 'V5';
      });
    },
  },
  mounted() {
    var self = this;
    HttpApi.affcenter()
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        return;
      }
      self.user = res.json['data']['user'];
      self.uinfo = res.json['data']['uinfo'];
    });
    HttpApi.ucpIndex()
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        return;
      }
      self.groups = res.json['data']['groups'];
    });
  },
}
</script>

<style scoped>
.affcenter{color: #fff;}

.headerbox{height: 200px; background: url('/static/icons/affcenter-bg.png') center top no-repeat; background-size: 100% 270px;}
.cardbox{margin: 66px 15px 0 15px; height: 160px; color: #fff; background: linear-gradient(to right, #4d00cc, #cc4193); border-radius: 10px;}
.cardbox .userbox{height: 80px; padding: 15px 10px; position: relative;}
.cardbox .userbox img{float: left; width: 50px; height: 50px; border-radius: 50%;}
.cardbox .userbox h1{margin-left: 60px; font-size: 16px; line-height: 25px; color: #fff;}
.cardbox .userbox h2{margin-left: 60px; font-size: 14px; line-height: 25px; color: #928f95;}
.cardbox .userbox dd{position: absolute; right: 10px; top: 20px; text-align: center; background: url('/static/icons/affcenter-arrow.png') right center no-repeat; background-size: 7px 12px;}
.cardbox .userbox dd img{width: 40px; height: 40px; margin-right: 15px;}
.cardbox .levelbox{height: 80px;}
.cardbox .levelbox ul{display: flex; margin-top: 18px;}
.cardbox .levelbox li.curr-group, .cardbox .levelbox li.next-group{width: 64px; height: 44px;}
.cardbox .levelbox li.levelbar{flex: 1; position: relative; padding-top: 17px; font-size: 14px; color: #ffb3e4; text-align: center;}
.cardbox .levelbox li.levelbar::before{position: absolute; left: 0; top: 10px; width: 100%; height: 3px; background: #e9b043; border-radius: 3px; content: '.'; font-size: 0;}
.cardbox .levelbox em{margin-left: 10px; display: inline-block; width: 44px; height: 44px;}
.cardbox .levelbox .V1{background: url('/static/icons/v1.png') center center no-repeat; background-size: 44px 44px;}
.cardbox .levelbox .V2{background: url('/static/icons/v2.png') center center no-repeat; background-size: 44px 44px;}
.cardbox .levelbox .V3{background: url('/static/icons/v3.png') center center no-repeat; background-size: 44px 44px;}
.cardbox .levelbox .V4{background: url('/static/icons/v4.png') center center no-repeat; background-size: 44px 44px;}
.cardbox .levelbox .V5{background: url('/static/icons/v5.png') center center no-repeat; background-size: 44px 44px;}
.cardbox .levelbox .V6{}
.cardbox .levelbox .V7{}

.permbox{margin-top: 20px;}
.permbox h2{line-height: 22px; font-size: 14px; color: #FFF; text-align: center;}
.permbox h2 img{width: 15px; height: 12px;}
.permbox li{padding-left: 75px; min-height: 50px; margin-top: 20px;}
.permbox .V1{background: url('/static/icons/icon_v1.png') 15px center no-repeat; background-size: 50px 50px;}
.permbox .V2{background: url('/static/icons/icon_v2.png') 15px center no-repeat; background-size: 50px 50px;}
.permbox .V3{background: url('/static/icons/icon_v3.png') 15px center no-repeat; background-size: 50px 50px;}
.permbox .V4{background: url('/static/icons/icon_v4.png') 15px center no-repeat; background-size: 50px 50px;}
.permbox .V5{background: url('/static/icons/icon_v5.png') 15px center no-repeat; background-size: 50px 50px;}
.permbox .V6{}
.permbox .V7{}
.permbox li h3{font-size: 14px; color: #fff; line-height: 25px;}
.permbox li h4{font-size: 12px; color: #aaa; line-height: 25px;}
.permbox li em{color: #ff3d6f;}

.bottombar{position: fixed; z-index: 99; bottom: 0; width: 100%;}
.bottombar h6{height: 45px; line-height: 45px;}
.bottombar h6 a{display: block; line-height: 45px; font-size: 14px; color: #fff; text-align: center; background: linear-gradient(to right, #c10fd1, #f66b4e);}

</style>



