<template>
  <div class="promotionbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
    </header>
    <div class="introbox">
      <dl>
        <dt>
          <h2>{{ promotion_earn_dscr ? promotion_earn_dscr.point1_text : '' }}<em>{{ promotion_earn_dscr ? promotion_earn_dscr.point1_coin : '0' }}</em></h2>
          <h2>{{ promotion_earn_dscr ? promotion_earn_dscr.point2_text : '' }}<em>{{ promotion_earn_dscr ? promotion_earn_dscr.point2_coin : '0' }}</em></h2>
        </dt>
        <dd>
          <p v-if="promotion_earn_dscr">{{ promotion_earn_dscr.illustrate }}</p>
          <p v-else>1020010300500</p>
          <em></em>
        </dd>
      </dl>
      <h1><router-link to="/ucp/promotion/qrpic"></router-link></h1>
    </div>

    <div class="bottombar">
      <h5><router-link to="/ucp/coinlog/bonuslog"></router-link></h5>
      <h6></h6>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'promotion-index',
  data() {
    return {
      promotion_earn_dscr: globalData.allData ? globalData.allData.promotion_earn_dscr : null
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
  },
  mounted() {

  },
}
</script>

<style scoped>
.promotionbox{color: #fff; position: absolute; left: 0; top: 0; width: 100%; height: 100%; background: url('/static/icons/promotion-bg.png') center center no-repeat; background-size: cover;}
header.navbar{background: transparent;}

.introbox{position: absolute; top: 42%; left: 13%; right: 13%; bottom: 16%; color: #aaa;}
.introbox dt{color: #333333;}
.introbox dt h2{height: 28px; padding-left: 32px; line-height: 28px; font-size: 18px; font-weight: bold; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;}
.introbox dt h2:first-child{background: url('/static/icons/1.png') left center no-repeat; background-size: 28px 28px;}
.introbox dt h2:last-child{margin-top: 15px; background: url('/static/icons/2.png') left center no-repeat; background-size: 28px 28px;}
.introbox dt h2 em{display: inline-block; margin-left: 3px; padding-left: 23px; color: #f94776; background: url('/static/icons/icon_bi.png') left center no-repeat; background-size: 20px 20px;}
.introbox dd{position: relative; padding: 5px; margin-top: 15px;}
.introbox dd p{display: block; padding: 25px 10px 5px 10px; min-height: 50px; background: #ffeff3; border-radius: 5px; color: #b73658; font-size: 14px; line-height: 22px;}
.introbox dd em{position: absolute; left: -3px; top: 10px; width: 70px; height: 25px; background: url('/static/icons/srjl.png') left top no-repeat; background-size: 70px 25px;}
.introbox h1{position: absolute; bottom: 0; width: 100%; height: 60px;}
.introbox h1 a{display: block; width: 100%; height: 100%; padding-top: 12px; font-size: 16px; color: #fff; text-align: center; background: url('/static/icons/bt.png') center center no-repeat; background-size: 235px 60px;}
@media only screen and (max-width: 375px){
  .introbox dt h2{font-size: 14px;}
  .introbox dd{margin-top: 5px; font-size: 12px;}
}
.bottombar{position: absolute; bottom: 2%; width: 100%;}
.bottombar h5{width: 100%; text-align: center;}
.bottombar h5 a{display: inline-block; padding: 5px 15px; color: #ffa93f; border: 1px solid #ffa93f; border-radius: 15px;}
.bottombar h6{margin-top: 15px; width: 100%; font-size: 12px; color: #fff; font-weight: 300; text-align: center;}
</style>



