<template>
  <div class="regbox">
    <header class="navbar">
      <div class="leadingbox">
        <router-link to="/" class="back"><img src="/static/icons/back.png"></router-link>
      </div>
      <div class="titlebox"></div>
    </header>
    <div class="regform">
      <div class="formbox">
        <form @submit.prevent="submit">
          <ul>
            <li><em class="user">+86<sup>*</sup></em><input type="text" name="mobi" v-model="mobi" value="" placeholder="" class="text" autocomplete="off"></li>
            <li class="smsbox">
              <em class="user"><sup>*</sup></em><input type="text" name="smscode" v-model="smscode" value="" placeholder="6" class="text" autocomplete="off">
              <a href="#" @click.prevent="sendSms">{{ countdown==-1 ? '' : (countdown == 0 ? '' : countdown + '') }}</a>
            </li>
            <li class="captcha">
              <em><sup>*</sup></em><input type="text" name="captcha_code" v-model="captcha_code" value="" placeholder="" class="text">
              <img :src="picurl" v-if="picurl" @click.prevent="reqCaptcha">
            </li>
            <li><em class="pass"><sup>*</sup></em><input type="password" name="password" v-model="password" value="" placeholder="" class="text"></li>
            <li><em></em><input type="text" name="invitecode" v-model="invitecode" value="" placeholder="" class="text"></li>
            <li class="submitbox"><input type="submit" value="" class="submit"></li>
            <li class="tosbox"><label><input type="checkbox" name="aup" v-model="aup" value="1"><i></i></label><a href="#"></a></li>
            <li class="logtips"><a @click.prevent="login"></a></li>
          </ul>
        </form>
      </div>
      <!--
      <div class="formbox">
          <ul>
            <li>APP</li>
            <li class="downloadbox"><a class="submit2" :href="appdownurl" target="_blank">APP</a></li>
            <li class="logtips"><a @click.prevent="login"></a></li>
          </ul>
      </div>
      -->
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'register',
  components: {},
  data() {
    return {
      mobiprefix: "+86",
      mobi: "",
      smscode: "",
      password: "",
      invitecode: "",
      aup: 1,
      sysavatar: '',
      countdown: -1,
      appdownurl: '',
      picurl: '',
      captcha_key: '',
      captcha_code: '',
    }
  },
  methods: {
    login(){
      this.$router.replace('/login?fw='+ this.$route.query.fw)
    },
    reqCaptcha(){
      var self = this;
      HttpApi.reqCaptcha().then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        let picurl = res.json['data']['picurl'];
        self.captcha_key = picurl.split('?')[1];
        self.picurl = HttpApi.currHost + picurl;
      });
    },
    sendSms(){
      var self = this;
      if(self.countdown > 0){
        return ;
      }
      if(self.captcha_code == ''){
          self.$toast('');
          return;
      }
      HttpApi.smsSendv(this.mobiprefix, this.mobi)
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        self.sendCountdown(60);
      });
    },
    sendCountdown(sec){
      var self = this;
      self.countdown = sec;
      setTimeout(function(){
        if(sec > 0){
          self.sendCountdown(sec-1);
        }
      }, 1000);
    },
    submit(){
      var self = this;
      if(!self.aup){
        return self.$toast('');
      }
      if(self.mobi==''){
        return self.$toast('');
      }
      if(self.smscode==''){
        return self.$toast('');
      }
      if(self.password==''){
        return self.$toast('');
      }

      HttpApi.register(self.mobiprefix, self.mobi, self.smscode, self.password, self.invitecode, self.sysavatar)
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        globalData.user = res.json['data']['user'];

      });
    }
  },
  beforeRouteEnter (to, from, next) {
    if(globalData.user.uid > 0){
      next(vm => {
        vm.$router.push('/');
      })
    }else{
      next();
    }
  },
  mounted() {
    var self = this;
    HttpApi.sysavatar()
    .then(function(res){
      if(res.errcode != 0){
        self.$toast('');
        return;
      }
      if(res.json['retcode'] != 0){
        self.$toast(res.json['errmsg']);
        return;
      }
      let sysavatars = res.json['data']['sysavatar']['man'];
      let random = Math.floor(Math.random() * sysavatars.length);
      self.sysavatar = sysavatars[random];
    });
    self.reqCaptcha();

    if(globalData.allData){
      self.appdownurl = globalData.allData['appdownurl'];
    }
  },
}
</script>

<style scoped>
.regbox{position: absolute; left: 0; top: 0; right: 0; bottom: 0; width: 100%; height: 100%; color: #fff; background-image: url('/static/icons/login_bg.png'); background-repeat: no-repeat; background-size: 100%;}
header.navbar{background: transparent;}

.regform{margin-top: 66px;}
.regform .formbox{margin-top: 30px; padding: 0 50px;}
.regform .formbox li{margin-top: 10px; height: 40px; position: relative;}
.regform .formbox li em{position: absolute; left: 10px; top: 10px; display: block; width: 60px; height: 21px; line-height: 21px;}
.regform .formbox li em.user{color: #fff;}
.regform .formbox li em.pass{color: #fff;}
.regform .formbox li em sup{margin-left: 2px; color: #f00; vertical-align: top;}
.regform .formbox input.text{width: 100%; height: 40px; padding: 0 10px 0 65px; outline: none; appearance:none; -webkit-appearance: none; border: 1px solid #1f2128; background: rgba(31, 33, 40, 0.75); color: #fff; caret-color: #ff3d6f; font-size: 14px; border-radius: 3px;}
.regform .formbox input.text:focus{border-color: #ff3d6f;}
.regform .formbox input.submit{width: 100%; height: 40px; outline: none; appearance:none; -webkit-appearance: none; background: #ff3d6f; border: none; color: #17191e; text-align: center; font-size: 16px; border-radius: 5px;}

.regform .formbox li.smsbox a{position: absolute; top: 0; right: 0; padding: 0 10px; line-height: 40px; color: #aaa;}
.regform .formbox li.captcha img{position: absolute; top: 0; right: 0; width: 80px; height: 40px;}

.regform .formbox li.submitbox{margin-top: 20px;}
.regform .formbox li.tosbox{margin-top: 0; line-height: 40px; font-size: 12px; color: #AAA;}
.regform .formbox li.tosbox a{color: #8d2d49; font-size: 12px;}
.regform .formbox li.tosbox label{display: inline-block; font-size: 12px; position: relative; height: 100%; line-height: 40px;}
.regform .formbox li.tosbox label input{position: absolute; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; background-color: transparent; border: 0; opacity: 0; cursor: pointer;}
.regform .formbox li.tosbox label input + i{float: left; position: relative; margin-top: 13px; margin-right: 5px; width: 14px; height: 14px; border: 1px solid #545558; border-radius: 50%; white-space: nowrap; overflow: visible; line-height: 20px; text-indent: 23px;}
.regform .formbox li.tosbox label input + i::before{position: absolute; display: none; left: 2px; top: 2px; width: 8px; height: 8px; content: '.'; font-size:0; background: #f00; border-radius: 50%;}
.regform .formbox li.tosbox label input:checked + i{border: 1px solid #ff3d6f; color: #fff;}
.regform .formbox li.tosbox label input:checked + i::before{display: block;}
.regform .formbox li.logtips{text-align: center;}
.regform .formbox li.logtips a{font-size: 14px; color: #ff3d6f;}

.regform .formbox li.downloadbox{margin-top: 20px; color: #fff; margin-bottom: 50px;}
.regform .formbox a.submit2{display: block; width: 100%; height: 40px; line-height: 40px; outline: none; appearance:none; -webkit-appearance: none; background: #ff3d6f; border: none; color: #fff; text-align: center; font-size: 16px; border-radius: 20px;}

</style>



