<template>
<div class="indexbox">
    <ul class="searchbox">
        <li class="logo" v-if=false><img src="/static/icons/logo_home.png"></li>
        <li class="schinput">
            <input type="text" value="" placeholder="" @click.prevent="goToSearch">
            <i></i>
        </li>
        <li v-if="false" class="gkjl">
            <router-link to="/playlog/listing"><img src="/static/icons/icon_gkjl2.png"></router-link>
        </li>
    </ul>
    <div class="pad56"></div>

    <mt-loadmore :top-method="loadTop" :auto-fill="false" ref="loadmore" v-if="top_videos">
        <div class="swiper-box">
            <div class="swiper-box-wrapper">
                <swiper :options="swiperOption" ref="mySwiper" v-if="swiperSlides.length">
                    <swiper-slide v-for="(slide, index) in swiperSlides" :key="index">
                        <a href="#" @click.prevent="slideClick(slide)"><img :src="slide.img"></a>
                    </swiper-slide>
                </swiper>
            </div>
        </div>
        <div class="swiper-pagination-box">
            <div class="swiper-pagination" slot="pagination"></div>
        </div>

        <h2 class="list-caption">
            <em>{{top_videos.title}}</em>
            <span @click='toMore(top_videos)'></span>
        </h2>
        <div class="todays">
            <VodItem100 :rows="top_videos.videos" />
        </div>

        <h2 class="list-caption">
            <em>{{latest_videos.title}}</em>
            <span @click='toMore(latest_videos)'></span>
        </h2>
        <div class="todays">
            <VodItem100 :rows="latest_videos.videos" />
        </div>

        <div class="paster-box" v-if="adRow1">
            <a :href="adRow1.clickurl" target="_blank"><img :src="adRow1.img"></a>
        </div>

        <div v-for="item in topics" :key="item.title">
            <h2 class="list-caption mtop10">
                <em>{{item.title}}</em>
                <span></span>
            </h2>
            <div class="latests">
                <VodItem1 :rows="item.videos" :addmore="true"></VodItem1>
            </div>
            <div class="paster-box" v-if="adRow2" :key="item.title">
                <a :href="adRow2.url" target="_blank"><img :src="adRow2.img"></a>
            </div>
        </div>

    </mt-loadmore>
</div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import VodItem2 from '@/components/vod-item2.vue';
import VodItem1 from '@/components/video_item.vue';

import VodItem100 from '@/components/video_item_100.vue'

import globalData from '@/global-data.js';
import api from '@/api.js';

export default {
    name: 'index',
    components: {
        VodItem2,
        VodItem1,
        VodItem100
    },
    data() {
        return {
            swiperOption: {
                slidesPerView: 1.2,
                centeredSlides: true,
                loop: true,
                autoplay: {
                    delay: 2000,
                    disableOnInteraction: false
                },
                pagination: {
                    el: '.swiper-pagination'
                }
            },
            swiperSlides: [],

            adRow1: null,
            adRow2: null,
        }
    },
    computed: {
        swiper() {
            return this.$refs.mySwiper.swiper
        },
        topics() {
            return this.$store.state.topics
        },
        latest_videos() {
            return this.$store.state.latest_videos;
        },
        top_videos() {
            return this.$store.state.top_videos;
        },
      

    },
    methods: {
        goToSearch() {
            this.$router.push('/search/index');
        },
        slideClick(slide) {

            return window.open(slide.clickurl);

        },
        toMore(item) {
           window.$catetitle=item.title;
           window.$catepath=item.path;
           this.$router.push("catevideolist")
        },
        loadTop() {

            this.swiperSlides = [];
            this.swiperSlides = window.$config.adlist.filter((item) => {
                return item.atype == ''
            })

            return;

        },

        likeChange() {
            var self = this;

        }
    },
    mounted() {

        this.swiperSlides = [];
        this.swiperSlides = this.$store.state.adlist2;

        this.adRow1 = this.$store.state.ad2;

    },
}
</script>

<style scoped>
.indexbox {
    color: #fff;
    margin-bottom: 66px;
    background: url('/static/icons/index_bg.png') left top no-repeat;
    background-size: 100%;
}

.searchbox {
    padding: 0 20px;
    display: flex;
    height: 50px;
    align-items: center;
    justify-content: center;
    position: fixed;
    z-index: 999;
    width: 100%;
    background: url('/static/icons/index_bg.png') left top no-repeat;
    background-size: 100%;
}

.searchbox li.logo {
    padding: 10px 10px;
}

.searchbox li.logo img {
    width: 30px;
    height: 30px;
    display: block;
}

.searchbox li.schinput {
    flex: 1;
    position: relative;
}

.searchbox li.gkjl {
    padding: 10px 10px;
}

.searchbox li.gkjl img {
    width: 30px;
    height: 30px;
    display: block;
}

.searchbox li.schinput input {
    display: block;
    height: 30px;
    padding-left: 30px;
    border: none;
    background: #9b3663;
    width: 100%;
    border-radius: 10px;
    font-size: 14px;
    color: #efa6c6;
}

.searchbox li.schinput input::-webkit-input-placeholder {
    color: #efa6c6;
}

.searchbox li.schinput i {
    position: absolute;
    left: 10px;
    top: 7px;
    width: 16px;
    height: 16px;
    background: url('/static/icons/icon_search2.png') center center no-repeat;
    background-size: 100%;
}

.index-grid {
    margin-top: 10px;
    overflow: hidden;
    position: relative;
}

.index-grid li {
    float: left;
    width: 25%;
    padding-top: 25%;
    position: relative;
}

.index-grid li a {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: #686a6c;
    display: flex;
    justify-content: center;
    align-items: center;
}

.index-grid li a img {
    width: 50px;
    height: 50px;
}

.index-grid li a span {
    line-height: 1.5;
    text-align: center;
}

.index-grid li a span h6 {
    font-weight: normal;
    font-size: 14px;
}

.index-grid li a span i {
    font-size: 14px;
    color: #686a6c;
}

.list-caption {
    height: 30px;
    position: relative;
    font-size: 14px;
    text-indent: 20px;
    line-height: 30px;
    background: url('/static/icons/icon_caption.png') left center no-repeat;
    background-size: 15px 15px;
}

.list-caption em {
    font-size: 16px
}

.list-caption span {
    position: absolute;
    right: 10px;
}

.list-caption span a {
    color: #929397;
    font-size: 14px;
    padding: 0 12px;
    background: url('/static/icons/arrow.png') right center no-repeat;
    background-size: 7px 12px;
}

.list-caption span a.likechange {
    padding: 0 0 0 20px;
    background: url('/static/icons/icon_hyp.png') left center no-repeat;
    background-size: 16px 16px;
}

.list-item-1 {
    overflow: hidden;
}

.list-item-1 li {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 61%;
    margin-top: 20px;
}

.list-item-1 li:first-child {
    margin-top: 0;
}

.list-item-1 li a {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}

.list-item-1 li img {
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 15px;
}

.list-item-1 li h3 {
    position: absolute;
    left: 15px;
    bottom: 5px;
    right: 15px;
    height: 26px;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.list-item-1 li i {
    position: absolute;
    bottom: 9px;
    right: 10px;
    height: 18px;
    line-height: 18px;
    padding: 0 6px;
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;
    font-size: 12px;
    border-radius: 9px 0 9px 0;
}

.list-item-1 li cite {
    position: absolute;
    left: 0;
    top: 0;
    width: 50px;
    height: 17px;
}

.list-item-1 li cite.p720 {
    background: url('/static/icons/720p.png') right center no-repeat;
    background-size: 50px 17px;
}

.list-item-1 li cite.p1080 {
    background: url('/static/icons/1080p.png') right center no-repeat;
    background-size: 50px 17px;
}

.list-item-1 li strong {
    position: absolute;
    right: 0;
    top: 0;
    width: 38px;
    height: 38px;
    background: url('/static/icons/label_xm.png') right center no-repeat;
    background-size: 38px 38px;
}

.list-item-1 li em {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 55px;
    height: 55px;
    transform: translate(-50%, -50%);
    background: url('/static/icons/icon_play.png') center center no-repeat;
    background-size: 55px 55px;
}

.todays {
    margin: 10px 10px 0 10px;
}

.latests {
    margin: 10px 5px 0 5px;
}

.paster-box {
    padding: 10px;
}

.paster-box img {
    width: 100%;
    border-radius: 10px;
}
</style>

<style>
.swiper-box {
    margin-top: 10px;
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 40%;
}

.swiper-box-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.swiper-box .swiper-container {
    width: 100%;
    height: 100%;
}

.swiper-box .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.swiper-pagination-box {
    position: relative;
    width: 100%;
    height: 30px;
    line-height: 30px;
    text-align: center;
}

.swiper-pagination-box .swiper-pagination {
    width: 100%;
}

.swiper-pagination-box .swiper-pagination-bullet {
    background: #9b919a;
    margin-left: 6px;
    margin-right: 6px;
    width: 8px;
    height: 8px;
    opacity: 1;
}

.swiper-pagination-box .swiper-pagination-bullet-active {
    width: 30px;
    height: 8px;
    background: #FFF;
    border-radius: 10px;
}

.swiper-box .swiper-slide {
    transform: scale(0.9);
}

.swiper-box .swiper-slide-active,
.swiper-box .swiper-slide-duplicate-active {
    transform: scale(1);
}
</style>



