    static resetUrl = 'https://ios.fuliapps.com/getHosts';
    static allHosts = {
        'https://android.fuliapps.com': {errCount: 0, errTime: 0},
        'https://android.xiangjiaoapps.com': {errCount: 0, errTime: 0},
        'https://android.guoguoapps.com': {errCount: 0, errTime: 0},
    };
    static currHost = null;

   

    //
    static async resetHost(){
        var allHosts = null;
        let res = await fetch(HttpApi.resetUrl);
        if(res.ok){
            allHosts = {};
            let text = await res.text();
            let hosts = text.split('\n');
            for(let i = 0; i < hosts.length; i++){
                allHosts[hosts[i]] = {errCount: 0, errTime: 0};
            }
        }
        return allHosts;
    }


  


    static async getHosts() {
        var allHosts = localStorage.getItem('allHosts');
        if(!allHosts){ //JSON
            allHosts = HttpApi.allHosts;
            localStorage.setItem('allHosts', JSON.stringify(allHosts));
        }else{
            allHosts = JSON.parse(allHosts);
        }
        var timestamp = Date.now();

        var newHosts = [];
        for(let keyHost in allHosts){
            if(allHosts[keyHost]['errCount'] < 10 || allHosts[keyHost]['errTime'] + 43200*1000 < timestamp){
                newHosts.push(keyHost);
            }
        }

        if(newHosts.length == 0){ // 
            allHosts = await HttpApi.resetHost(HttpApi.resetUrl);
            if(allHosts){
                localStorage.setItem('allHosts', JSON.stringify(allHosts));
                for(let keyHost in allHosts){
                    if(allHosts[keyHost]['errCount'] < 10 || allHosts[keyHost]['errTime'] + 43200*1000 < timestamp){
                        newHosts.push(keyHost);
                    }
                }
            }
        }

        return newHosts;
    }
    static networkError(host){
        var allHosts = localStorage.getItem('allHosts');
        if(!allHosts){
            return;
        }
        allHosts = JSON.parse(allHosts);
        if(!allHosts[host]){
            return;
        }
        var timestamp = Date.now();
        if(allHosts[host]['errTime'] + 600*1000 > timestamp){ // 10
            return ;
        }
        allHosts[host]['errTime'] = timestamp;
        allHosts[host]['errCount'] = allHosts[host]['errCount'] + 1;
        localStorage.setItem('allHosts', JSON.stringify(allHosts));
    }

    static getAuth(){
        return localStorage.getItem('xxx_api_auth') || '';
    }
    static saveAuth(json){
        if(json == null){
            return;
        }
        json.then(function(retJson){
            try{
                if(retJson['retcode'] == 0 && typeof retJson['data']['xxx_api_auth'] == 'string'){
                    localStorage.setItem('xxx_api_auth', retJson['data']['xxx_api_auth'])
                }
            }catch(e){
                return;
            }
        });
    }
    static cleanAuth(){
        localStorage.setItem('xxx_api_auth', null);
        localStorage.removeItem('xxx_api_auth');
    }

    static getOptions(options){
        let defOptions = {
            mode: 'cors',
            headers: {
                'X-Cookie-Auth': HttpApi.getAuth(),
            }
        };
        return Object.assign(defOptions, options);
    }

    //errcode = 0 [http20x] -1  -2 json  >0 http 404/403/500/502/504
    static async resCallback(json, statusCode){
        HttpApi.saveAuth(json);
        var promise = new Promise(function(resolve, reject){
            if(statusCode == 0){
                resolve({json: null, errcode: -1});
            }else if(statusCode >= 200 && statusCode < 300){
                json.then(function(retJson){
                    resolve({json: retJson, errcode: 0});
                }).catch(function(){ //JSON
                    resolve({json: null, errcode: 1});
                });
            }else{
                resolve({json: null, errcode: statusCode});
            }
        });
        return promise;
    }
    //
    static async blobCallback(blob, statusCode){
        var promise = new Promise(function(resolve, reject){
            if(statusCode == 0){
                resolve({blob: null, errcode: -1});
            }else if(statusCode >= 200 && statusCode < 300){
                blob.then(function(retBlob){
                    resolve({blob: retBlob, errcode: 0});
                }).catch(function(){ //blob
                    resolve({blob: null, errcode: 1});
                });
            }else{
                resolve({blob: null, errcode: statusCode});
            }
        });
        return promise;
    }

    static async getInit(allHosts){
        if(!allHosts){
            allHosts = await HttpApi.getHosts();
        }
        let host = allHosts.shift();
        if(!host){
            return HttpApi.resCallback(null, 0);
        }
        let url = host + '/init?_='+Math.random();
        let options = HttpApi.getOptions();

        return fetch(url, options).then(function(res){ //200
            return HttpApi.resCallback(res.json(), res.status);
        }).catch(function(){ // TypeError
            HttpApi.networkError(host);
            return HttpApi.getInit(allHosts);
        });
    }

