    static async login(username, password, mobiprefix, allHosts){
        if(!allHosts){
            allHosts = await HttpApi.getHosts();
        }
        let host = allHosts.shift();
        if(!host){
            return HttpApi.resCallback(null, 0);
        }
        let url = host + '/login?_='+Math.random();
        
        var postData = {
            username: username,
            password: password,
            mobiprefix: mobiprefix,
            mobi: ''
        };
        if(/^[0-9]+$/.test(username)){
            postData['username'] = '';
            postData['mobi'] = username;
        }
        var formData = new FormData();
        formData.append('username', postData['username']);
        formData.append('password', postData['password']);
        formData.append('mobiprefix', postData['mobiprefix']);
        formData.append('mobi', postData['mobi']);

        var options = HttpApi.getOptions();
        options['method'] = 'POST';
        //options['headers']['content-type'] = 'application/x-www-form-urlencoded';
        options['body'] = formData;

        return fetch(url, options).then(function(res){ //200
            return HttpApi.resCallback(res.json(), res.status);
        }).catch(function(){ // TypeError
            HttpApi.networkError(host);
            return HttpApi.login(username, password, mobiprefix, allHosts);
        });
    }
