<template>
  <div class="vodlistbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click.prevent="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
      <div class="actionbox">
        <router-link to="/search/index" class="search"><img src="/static/icons/icon_search2.png"></router-link>
      </div>
    </header>
    <div class="pad56"></div>

    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
      <div class="vodlist-header">
        <ul>
          <li>
            <a href="#" :class="{active: params['cateid']==0}" @click.prevent="setFilter('cateid', 0)"></a>
            <a href="#" :class="{active: params['cateid']==category.cateid}" @click.prevent="setFilter('cateid', category.cateid)" v-for="category in categories" :key="category.cateid">{{ category.catename }}</a>
          </li>
          <li>
            <a href="#" :class="{active: params['areaid']==0}" @click.prevent="setFilter('areaid', 0)"></a>
            <a href="#" :class="{active: params['areaid']==area.areaid}" @click.prevent="setFilter('areaid', area.areaid)" v-for="area in areas" :key="area.areaid">{{ area.areaname }}</a>
          </li>
          <li>
            <a href="#" :class="{active: params['yearid']==0}" @click.prevent="setFilter('yearid', 0)"></a>
            <a href="#" :class="{active: params['yearid']==year.yearid}" @click.prevent="setFilter('yearid', year.yearid)" v-for="year in years" :key="year.yearid">{{ year.yearname }}</a>
          </li>
          <li>
            <a href="#" :class="{active: params['definition_keyid']==0}" @click.prevent="setFilter('definition_keyid', 0)"></a>
            <a href="#" :class="{active: params['definition_keyid']==definition.keyid}" @click.prevent="setFilter('definition_keyid', definition.keyid)" v-for="definition in definitions" :key="definition.keyid">{{ definition.value }}</a>
          </li>
          <li>
            <a href="#" :class="{active: params['duration_keyid']==0}" @click.prevent="setFilter('duration_keyid', 0)"></a>
            <a href="#" :class="{active: params['duration_keyid']==duration.keyid}" @click.prevent="setFilter('duration_keyid', duration.keyid)" v-for="duration in durations" :key="duration.keyid">{{ duration.value }}</a>
          </li>
          <!--
          <li>
            <a href="#" :class="{active: params['freeType_keyid']==0}" @click.prevent="setFilter('freeType_keyid', 0)"></a>
            <a href="#" :class="{active: params['freeType_keyid']==freetype.keyid}" @click.prevent="setFilter('freeType_keyid', freetype.keyid)" v-for="freetype in freetypes" :key="freetype.keyid">{{ freetype.value }}</a>
          </li>
          -->
          <li>
            <a href="#" :class="{active: params['mosaic_keyid']==0}" @click.prevent="setFilter('mosaic_keyid', 0)"></a>
            <a href="#" :class="{active: params['mosaic_keyid']==mosaic.keyid}" @click.prevent="setFilter('mosaic_keyid', mosaic.keyid)" v-for="mosaic in mosaics" :key="mosaic.keyid">{{ mosaic.value }}</a>
          </li>
          <li>
            <a href="#" :class="{active: params['langvoice_keyid']==0}" @click.prevent="setFilter('langvoice_keyid', 0)"></a>
            <a href="#" :class="{active: params['langvoice_keyid']==langvoice.keyid}" @click.prevent="setFilter('langvoice_keyid', langvoice.keyid)" v-for="langvoice in langvoices" :key="langvoice.keyid">{{ langvoice.value }}</a>
          </li>
          <li>
            <a href="#" :class="{active: params['order_keyid']==0}" @click.prevent="setFilter('order_keyid', 0)"></a>
            <a href="#" :class="{active: params['order_keyid']==order.keyid}" @click.prevent="setFilter('order_keyid', order.keyid)" v-for="order in orders" :key="order.keyid">{{ order.value }}</a>
          </li>
        </ul>
      </div>
      <div class="vodrow-box">
        <VodItem2 :rows="realVodrows" :addmore="false"></vodItem2>
      </div>
    </mt-loadmore>

  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import VodItem2 from '@/components/vod-item2.vue';
import globalData from '@/global-data.js';

export default {
  name: 'VodList',
  components: {VodItem2},
  data() {
    return {
      pageInfo: null,
      vodrows: [],
      realVodrows: [],
      orders: [],
      categories: [],
      areas: [],
      years: [],
      definitions: [],
      durations: [],
      freetypes: [],
      mosaics: [],
      langvoices: [],

      params: {"cateid": 0, "areaid": 0, "yearid": 0, "definition_keyid": 0, "duration_keyid": 0, "freeType_keyid": 0, "mosaic_keyid": 0, "langvoice_keyid": 0, "order_keyid": 0, "page": 1},
      paramsIndex: ['cateid', 'areaid', 'yearid', 'definition_keyid', 'duration_keyid', 'freeType_keyid', 'mosaic_keyid', 'langvoice_keyid', 'order_keyid', 'page'],
      allLoaded: false,
    }
  },
  computed: {

  },
  methods: {
    goBack () {
      //this.$destroy();
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    insertAdRows(){ //
      var self = this;
      var realVodrows = [];
      for(let i = 0; i < self.vodrows.length; i++){
        realVodrows.push(self.vodrows[i]);
        if(i == 6-1 || i == 12-1 || i == 18-1){
          if(globalData.allData){
            try{
              var adList = [];
              if(i == 6-1){
                adList = globalData.allData['iOS_adgroups']['global_adgroup_ad4'];
              }else if(i == 12-1){
                adList = globalData.allData['iOS_adgroups']['global_adgroup_ad5'];
              }else if(i == 18-1){
                adList = globalData.allData['iOS_adgroups']['global_adgroup_ad6'];
              }
              var random = Math.floor(Math.random() * adList.length);
              var adRow = adList[random];
              if(adRow){
                adRow['vodid'] = 'ad-' + i;
                adRow['isad'] = 1;
                realVodrows.push(adRow);
              }
            }catch(e){

            }
          }
        }
      }
      self.realVodrows = realVodrows;
    },
    loadTop(){
      var self = this;
      var params = self.params;
      params['page'] = 0;
      var uriParams = [];
      self.paramsIndex.forEach(function(key){
        uriParams.push(params[key]);
      });
      var uri = '/vod/listing-'+uriParams.join('-');

      HttpApi.getListing(uri)
      .then(function(res){
        self.$refs.loadmore.onTopLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }

        var newIds = [];
        var keyId = 'vodid';
        for(let i = 0; i < 20; i++){
          if(i >= self.vodrows.length){
            break;
          }
          newIds.push(self.vodrows[i][keyId]);
        }
        let newRows = res.json['data']['vodrows'].reverse();
        for(let i = 0; i < newRows.length; i++){
          var isNew = true;
          for(let j = 0; j < newIds.length; j++){
            if(newRows[i][keyId] == newIds[j]){
              isNew = false;
            }
          }
          if(isNew){
            self.vodrows.unshift(newRows[i]);
          }
        }
        self.insertAdRows();
      });
    },
    loadBottom(){
      var self = this;
      self.params['page'] = self.params['page'] == 0 ? 2 : self.params['page'] + 1;
      var uriParams = [];
      self.paramsIndex.forEach(function(key){
        uriParams.push(self.params[key]);
      });
      var uri = '/vod/listing-'+uriParams.join('-');

      HttpApi.getListing(uri)
      .then(function(res){
        self.$refs.loadmore.onBottomLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.pageInfo = res.json['data']['pageinfo'];
        self.vodrows = self.vodrows.concat(res.json['data']['vodrows']);

        if(self.pageInfo['page'] == self.pageInfo['totalpage']){
          self.allLoaded = true;
        }
        self.insertAdRows();
      });
    },
    setFilter(paramKey, paramVal){
      var self = this;
      self.params['page'] = 0;
      self.params[paramKey] = paramVal;
      var uriParams = [];
      self.paramsIndex.forEach(function(key){
        uriParams.push(self.params[key]);
      });
      var uri = '/vod/listing-'+uriParams.join('-');

      self.$indicator.open({spinnerType: 'fading-circle'});
      HttpApi.getListing(uri)
      .then(function(res){
        self.$indicator.close();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.pageInfo = res.json['data']['pageinfo'];
        self.vodrows = res.json['data']['vodrows'];

        if(self.pageInfo['page'] == self.pageInfo['totalpage']){
          self.allLoaded = true;
        }
        self.insertAdRows();
      });
    }
  },
  mounted() {
    var self = this;
    var params = self.$route.params.params;
    var paramIndexes = params.split('-');
    paramIndexes.forEach(function(paramVal, index){
      var paramKey = self.paramsIndex[index];
      self.params[paramKey] = paramVal;
    });
    
    self.$indicator.open({spinnerType: 'fading-circle'});
    HttpApi.getListing('/vod/listing-' + params)
    .then(function(res){
      self.$indicator.close();
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.pageInfo = res.json['data']['pageinfo'];
      self.vodrows = res.json['data']['vodrows'];
      self.orders = res.json['data']['orders'];
      self.categories = res.json['data']['categories'];
      self.areas = res.json['data']['areas'];
      self.years = res.json['data']['years'];
      self.definitions = res.json['data']['definitions'];
      self.durations = res.json['data']['durations'];
      self.freetypes = res.json['data']['freetypes'];
      self.mosaics = res.json['data']['mosaics'];
      self.langvoices = res.json['data']['langvoices'];

      if(self.pageInfo['page'] == self.pageInfo['totalpage']){
        self.allLoaded = true;
      }
      self.insertAdRows();
    });
  },

  //  /vod/show/... 
  beforeRouteLeave(to, from, next) {
    if (/^\/vod\/show\//i.test(to.path)) {
      from.meta.keepAlive = true;
    }else{
      from.meta.keepAlive = false;
    }
    next();
  }
}
</script>

<style scoped>
.vodlistbox{color: #fff; height:100vh; overflow-y: scroll; -webkit-overflow-scrolling: touch;} /*  height:100vh  <mt-loadmore>overflow:scroll*/

.vodlist-header{background: #272727; padding: 10px 0; overflow: hidden;}
.vodlist-header ul{overflow: hidden;}
.vodlist-header li{width: 100%; height: 30px; line-height: 30px; margin-top: 10px; white-space: nowrap; overflow-x:scroll; overflow-y: hidden; font-size: 14px;}
.vodlist-header li:first-child{margin-top: 0;}
.vodlist-header li::-webkit-scrollbar{display: none;}
.vodlist-header li a{display: inline-block; margin: 0 10px; padding: 0 5px; color: #aaaaaa;}
.vodlist-header li a.active{padding: 0 15px; background: #7a2e45; border-radius: 15px; color: #fff;}

.vodrow-box{margin: 20px 5px 10px 5px;}

</style>



