<template>
  <div class="msgbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
    </header>
    <div class="showbox" v-if="row">
      <h1>{{ row.content|getTitle }}</h1>
      <h2>{{ row.sendtime|getTime('MM-dd hh:mm') }}</h2>
      <div class="textblock">{{ row.content|getIntro }}</div>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'msg-show',
  data() {
    return {
      page: 0,
      row: null,
    }
  },
  filters: {
    getTime(timestamp, fmt){
      let unixTimestamp = new Date(timestamp * 1000)
      let o = {
        "M+": unixTimestamp.getMonth() + 1, //
        "d+": unixTimestamp.getDate(), //
        "h+": unixTimestamp.getHours(), //
        "m+": unixTimestamp.getMinutes(), //
        "s+": unixTimestamp.getSeconds(), //
        "q+": Math.floor((unixTimestamp.getMonth() + 3) / 3), //
        "S": unixTimestamp.getMilliseconds() //
      };
      if(/(y+)/.test(fmt)){
        fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
      }
      for (var k in o){
        if (new RegExp("(" + k + ")").test(fmt)){
          fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        }
      }
      return fmt;
    },
    getTitle(content){
      var title = content.split('\n')[0];
      return title;
    },
    getIntro(content){
      var intro = content.split('\n')[1];
      return intro;
    },
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
  },
  mounted() {
    var self = this;
    HttpApi.getMsgShow(this.$route.query.cid, self.page)
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        return;
      }
      self.row = res.json['data']['rows'][0];
    });
  },
}
</script>

<style scoped>
.detailbox{color: #fff;}
.showbox{margin: 66px 10px 0 10px;}
.showbox h1{font-size: 16px; color: #ff3d6f; padding-top: 10px;}
.showbox h2{font-size: 12px; color: #aaaaaa; margin-top: 10px;}
.showbox .textblock{font-size: 14px; color: #aaaaaa; margin-top: 15px; padding-top: 15px; border-top: 1px solid #aaa;}
</style>



