<template>
  <div class="paymentbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
    </header>
    <div class="listbox">
      <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
      <ul v-if="rows.length">
        <li v-for="row in rows" :key="row.payid">
          <dl>
            <dt>{{ row.itemname }}</dt>
            <dd>
              <h2><label>:</label> {{ row.paidtime ? row.paidtime.split(' ')[0] : row.createtime.split(' ')[0] }}</h2>
              <h2><label>:</label> {{ row.paycode_name }}</h2>
              <h2><label>:</label> {{ row.payid }}</h2>
              <h3>{{ row.pay_amount }} <span>{{ row.ispaid == 1 ? '' : (row.ispaid == 2 ? '' : '') }}</span></h3>
            </dd>
          </dl>
        </li>
      </ul>
      </mt-loadmore>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'payment-listing',
  data() {
    return {
      pageInfo: null,
      rows: [],
      allLoaded: false,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    loadTop(){
      var self = this;
      HttpApi.paymentListing(1)
      .then(function(res){
        self.$refs.loadmore.onTopLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        var newIds = [];
        var keyId = 'payid';
        for(let i = 0; i < 20; i++){
          if(i >= self.rows.length){
            break;
          }
          newIds.push(self.rows[i][keyId]);
        }

        let newRows = res.json['data']['rows'].reverse();
        for(let i = 0; i < newRows.length; i++){
          var isNew = true;
          for(let j = 0; j < newIds.length; j++){
            if(newRows[i][keyId] == newIds[j]){
              isNew = false;
            }
          }
          if(isNew){
            self.rows.unshift(newRows[i]);
          }
        }
      });
    },
    loadBottom(){
      var self = this;
      HttpApi.paymentListing(self.pageInfo['page']+1)
      .then(function(res){
        self.$refs.loadmore.onBottomLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.rows.push(...res.json['data']['rows']);
        self.pageInfo = res.json['data']['pageinfo'];

        if(self.pageInfo['page'] == self.pageInfo['totalpage']){
          self.allLoaded = true;
        }
      });
    }
  },
  mounted() {
    var self = this;
    HttpApi.paymentListing(1)
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.pageInfo = res.json['data']['pageinfo'];
      self.rows = res.json['data']['rows'];

      if(self.pageInfo['page'] == self.pageInfo['totalpage']){
        self.allLoaded = true;
      }
    });
  },
}
</script>

<style scoped>
.paymentbox{color: #fff; height:100vh; overflow-y: scroll; -webkit-overflow-scrolling: touch;}

.listbox{margin-top: 56px;}
.listbox ul{overflow: hidden; min-height: 300px;} /* 0 */
.listbox li{margin-top: 10px; padding: 10px 15px; background: #212429;}
.listbox dt{width: 100%; height: 30px; padding-left: 28px; line-height: 30px; background: url('/static/icons/icon_zgvip.png') left center no-repeat; background-size: 23px 20px;}
.listbox dd h2{height: 22px; line-height: 22px; color: #aaaaaa; font-weight: 300; font-size: 14px; text-align: left; overflow: hidden; white-space: nowrap; text-overflow: ellipsis;}
.listbox dd h2 label{font-weight: 300;}
.listbox dd h3{position: relative; margin-top: 10px; padding-top: 5px; height: 32px; line-height: 32px; color: #fff; font-size: 14px; overflow: hidden; white-space: nowrap; text-overflow: ellipsis; border-top: 1px solid #2f3236;}
.listbox dd h3 span{position: absolute; right: 0; color: #ff3d6f;}
</style>



