<template>
  <div id="app">
    
    <keep-alive>
     
      <router-view v-if="$route.meta.keepAlive" v-keep-scroll-position></router-view>
      
    </keep-alive>
    
    <router-view v-if="!$route.meta.keepAlive" v-keep-scroll-position></router-view>
    
  
  </div>
</template>

<script>
import globalData from '@/global-data.js';

// (navigator.standalone) iOS PWAtruefalse Androidundefine
const isInStandaloneMode = () => (window.matchMedia('(display-mode: standalone)').matches) || (window.navigator.standalone);
if(isInStandaloneMode()) {
  globalData.isInStandaloneMode = true;
}

export default {
  name: 'App',
  data(){
    return {

    }
  }
}
</script>

<style>

</style>



