<template>
  <div class="playlogbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
      <div class="actionbox">
        <a href="#" class="text" @click.prevent="toggleCheckbox">{{ checkboxOpened == 0 ? '' : '' }}</a>
      </div>
    </header>
    <header class="tabbar">
      <ul>
        <li @click="selectTab(0)">
          <h6 :class="{active: tabIndex==0}"></h6>
        </li>
        <li @click="selectTab(1)">
          <h6 :class="{active: tabIndex==1}"></h6>
        </li>
        <li @click="selectTab(2)">
          <h6 :class="{active: tabIndex==2}"></h6>
        </li>
      </ul>
    </header>
    <div class="pad102"></div>

    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
    <div class="listbox">
      <ul v-if="rows.length">
        <li v-for="row in rows" :key="row.vodid">
          <router-link :to="'/vod/show/'+row.vodid">
            <dl :class="{checkboxOpened: checkboxOpened == 1}">
              <dt><img :src="row.coverpic"><i>{{ row.duration }}</i></dt>
              <dd>
                <h2>{{ row.title }}</h2>
                <h4>{{ row.playtime ? row.playtime.split(' ')[0] : row.updatetime.split(' ')[0] }}</h4>
              </dd>
            </dl>
          </router-link>
          <div class="checkbox" :class="{checkboxOpened: checkboxOpened == 1}">
            <input type="checkbox" :value="row.vodid" v-model="selectedIds">
            <i></i>
          </div>
        </li>
      </ul>
    </div>
    </mt-loadmore>

    <div class="checkpad" v-if="checkboxOpened"></div>
    <div class="checkbar" v-if="checkboxOpened">
      <span></span>
      <a href="#" @click.prevent="cancel"></a>
      <a href="#" @click.prevent="remove">{{ selectedIds.length ? '('+ selectedIds.length +')' : '' }}</a>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'playlog',
  data() {
    return {
      tabIndex: 0,
      pageInfo: null,
      rows: [],
      checkboxOpened: 0, // Safari {{ checkboxOpened == 0 ? '' : '' }}
      selectedIds: [],
      allLoaded: false,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    selectTab: function(tabIndex){
      if(tabIndex != this.tabIndex){
        this.page = 0;
        this.rows = [];
      }
      this.tabIndex = tabIndex;
      this.getPlayLog();
      this.selectedIds = [];
    },

    getPlayLog(){
      var self = this;
      var timeline = this.tabIndex + 1;
      return;
      HttpApi.getPlayLog(timeline, 1)
      .then(function(res){
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.pageInfo = res.json['data']['pageinfo'];
        self.rows = res.json['data']['rows'];

        if(self.pageInfo['page'] == self.pageInfo['totalpage']){
          self.allLoaded = true;
        }
      });
    },

    toggleCheckbox(){
      this.checkboxOpened = this.checkboxOpened == 0 ? 1 : 0;
    },
    cancel(){
      this.selectedIds = [];
    },
    remove(){
      var self = this;
      HttpApi.delPlayLog(self.selectedIds)
      .then(function(res){
        self.$refs.loadmore.onBottomLoaded();
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }

        var inArray = function(id, arrays){
          for(let i = 0; i < arrays.length; i++){
            if(arrays[i] == id){
              return true;
            }
          }
          return false;
        };
        var newRows = [];
        for(let i = 0; i < self.rows.length; i++){
          if(inArray(self.rows[i]['vodid'], self.selectedIds)){
            continue;
          }
          newRows.push(self.rows[i]);
        }
        self.rows = newRows;
        self.checkboxOpened = 0;
        self.selectedIds = [];
      });
    },

    loadTop(){
      var self = this;
      HttpApi.getPlayLog(self.tabIndex + 1, 1)
      .then(function(res){
        self.$refs.loadmore.onTopLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        var newIds = [];
        var keyId = 'vodid';
        for(let i = 0; i < 20; i++){
          if(i >= self.rows.length){
            break;
          }
          newIds.push(self.rows[i][keyId]);
        }

        let newRows = res.json['data']['rows'].reverse();
        for(let i = 0; i < newRows.length; i++){
          var isNew = true;
          for(let j = 0; j < newIds.length; j++){
            if(newRows[i][keyId] == newIds[j]){
              isNew = false;
            }
          }
          if(isNew){
            self.rows.unshift(newRows[i]);
          }
        }
      });
    },
    loadBottom(){
      var self = this;
      HttpApi.getFavorite(self.tabIndex + 1, self.pageInfo['page']+1)
      .then(function(res){
        self.$refs.loadmore.onBottomLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.rows.push(...res.json['data']['rows']);
        self.pageInfo = res.json['data']['pageinfo'];

        if(self.pageInfo['page'] == self.pageInfo['totalpage']){
          self.allLoaded = true;
        }
      });
    }
  },
  mounted() {
    this.getPlayLog();
  },
}
</script>

<style scoped>
.playlogbox{height:100vh; overflow-y: scroll; -webkit-overflow-scrolling: touch;}

header.tabbar{position: fixed; left: 0; top: 56px; right: 0; z-index: 999; width: 100%; height: 46px; background: #1b1e23;}
header.tabbar ul{overflow: hidden; text-align: center;}
header.tabbar li{display: inline-block; height: 46px; position: relative; padding: 0 15px; text-align: center; line-height: 56px;}
header.tabbar li h6{margin-top: 4px; height: 40px; line-height: 40px; font-size: 14px; color: #aaaaaa;}
header.tabbar li h6.active{color: #ffffff; border-bottom: 3px solid #ff3d6f;}
.pad102{height: 102px;}

.listbox{min-height: 300px;}
.listbox ul{overflow: hidden; margin: 0 10px;}
.listbox li{margin-top: 10px; position: relative;}
.listbox li .checkbox{display: none; position: absolute; top:0; left:0; z-index: 1; width: 100%; height: 100%;}
.listbox li .checkbox.checkboxOpened{display: block;}
.listbox li .checkbox input{position: absolute; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; background-color: transparent; border: 0; opacity: 0; cursor: pointer;}
.listbox li .checkbox input + i{display: block; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; background: url('/static/icons/check_nom.png') left center no-repeat; background-size: 20px 20px;}
.listbox li .checkbox input:checked + i{background-image: url('/static/icons/check_sel.png');}

.listbox dl{height: 90px; overflow: hidden; margin-left: 0; transition: margin-left 0.1s linear;}
.listbox dl.checkboxOpened{margin-left: 40px; transition: margin-left 0.1s linear;}
.listbox dt{width: 150px; height: 90px; position: relative; float: left;}
.listbox dt img{display: block; width: 100%; height: 100%;  border-radius: 15px;}
.listbox dt i{position: absolute; bottom: 5px; right: 5px; height: 18px; line-height: 18px; padding: 0 6px; background-color: rgba(0, 0, 0, 0.5); color: #fff; font-size: 12px; border-radius: 9px 0 9px 0;}
.listbox dd{margin-left: 160px;}
.listbox dd h2{height: 66px; line-height: 22px; color: #fff; font-size: 14px; text-align: left; overflow: hidden; text-overflow: ellipsis;}
.listbox dd h4{height: 24px; line-height: 24px; color: #aaaaaa; font-size: 14px;}

.checkpad{height: 40px;}
.checkbar{width: 100%; height: 40px; line-height: 40px; position: fixed; z-index: 10; bottom: 0; text-align: center; background: #212429;}
.checkbar a{float: left; width: 50%; line-height: 40px; color: #aaaaaa; font-weight: 300;}
.checkbar a:last-child{color: #ff3d6f;}
.checkbar span{position: absolute; left: 50%; top: 50%; width: 1px; height: 30px; margin-top: -15px; background: #2f3236;}

</style>



