<template>
  <ul class="list-item-1">
    <li v-for="row in rows" :key="row.id" :row="row">
       
       <router-link :to="'/vod/show/' + row.id">
          <dt>
            <img v-lazy="row.cover2" >
            <i>{{ row.duration }} </i>
            
          </dt>
          <dd>
            <h3>
              <em>{{ row.title }}</em>
             
            </h3>
          </dd>
        </dl>
     </router-link >
    </li>
  </ul>
</template>

<script>

export default {
  name: 'TabItem',
  props: ['rows'],
  methods:{
    toPlayer(item){
      console.log(item);
       this.$router.push(
         {
         name:"player",
         params:{
           vodid:item.id
         }
       })
    }
  }
}
</script>

<style scoped>
.list-item-1{overflow: hidden;}
.list-item-1 li{margin-top: 20px;}
.list-item-1 li:first-child{margin-top: 0;}
.list-item-1 li dt{position: relative; width: 100%; height: 0; padding-top: 61%;}
.list-item-1 li dt img{position: absolute; left: 0; top: 0; width: 100%; height: 100%; display: block; border-radius: 15px;}
.list-item-1 li dt i{position: absolute; top: 10px; right: 10px; height: 18px; line-height: 18px; padding: 0 6px; background-color: rgba(0, 0, 0, 0.5); color: #fff; font-size: 12px; border-radius: 9px 0 9px 0;}
.list-item-1 li dt cite{position: absolute; left: 0; top: 0; width: 50px; height: 17px;}
.list-item-1 li dt cite.p720{background: url('/static/icons/720p.png') right center no-repeat; background-size: 50px 17px;}
.list-item-1 li dt cite.p1080{background: url('/static/icons/1080p.png') right center no-repeat; background-size: 50px 17px;}
.list-item-1 li dt em{position: absolute; left: 50%; top: 50%; width: 55px; height: 55px; transform: translate(-50%, -50%); background: url('/static/icons/icon_play.png') center center no-repeat; background-size: 55px 55px;}
.list-item-1 li dd{margin-top: 10px;}
.list-item-1 li dd h3{display: flex; height: 26px; color: #fff; font-size: 16px; line-height: 26px;}
.list-item-1 li dd h3 em{flex: 1; font-weight: bold; overflow: hidden; white-space: nowrap; text-overflow: ellipsis;}
.list-item-1 li dd h3 span{display: inline-block; margin-top: 5px; height: 16px; text-align: right; font-size: 14px; line-height: 16px; color: #aaaaaa;}
.list-item-1 li dd h3 span time{display: inline-block; padding-right: 10px; border-right: 1px solid #45474b;}
.list-item-1 li dd h3 span cite{display: inline-block; margin-left: 10px; padding-left: 20px; height: 16px; background: url('/static/icons/icon_dzs.png') left center no-repeat; background-size: 15px 15px;}
</style>
