<template>
  <div class="msgbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
    </header>
    <div class="showbox" v-if="row">
      <h1>{{ row.title }}</h1>
      <h2>{{ row.addtime }}</h2>
      <div class="textblock">{{ row.intro }}</div>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'announce-detail',
  data() {
    return {
      row: null,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
  },
  mounted() {
    var self = this;
    HttpApi.getAnnounceDetail(this.$route.query.artid)
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        return;
      }
      self.row = res.json['data']['row'];
    });
  },
}
</script>

<style scoped>
.detailbox{color: #fff;}

.showbox{margin: 66px 10px 0 10px;}
.showbox h1{font-size: 16px; color: #ff3d6f; padding-top: 10px;}
.showbox h2{font-size: 12px; color: #aaaaaa; margin-top: 10px;}
.showbox .textblock{font-size: 14px; color: #aaaaaa; margin-top: 15px; padding-top: 15px; border-top: 1px solid #aaa;}
</style>



