<template>
  <div class="feedbackbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
      <div class="actionbox">
        <router-link to="/ucp/withdraw/listing" class="text"></router-link>
      </div>
    </header>
    <div class="formbox">
      <h2>
        <label></label>
        <em @click="showSelectBox">{{ selectRow ? selectRow.cardnum : ''}}</em>
      </h2>
      <h3></h3>
      <h4>
        <label></label>
        <input type="text" value="" placeholder="0.00" v-model="withdraw_amount">
      </h4>
      <h5> {{ max2rmb }} <em @click="withdrawAll(max2rmb)"></em></h5>
      <h6>{{ topupmin ? topupmin : '100' }}248</h6>
    </div>
    <h3 class="subbox"><input type="submit" value="" class="submit" @click="submit"></h3>


    <mt-popup v-model="selectVisible" position="bottom" class="cardrowlist">
      <h1> <span @click="showCreateBox(0)"></span></h1>
      <ul>
        <li v-for="cardrow in cardrows" :key="cardrow.cardid" @click="selectCardRow(cardrow.cardid)">
          <h2>{{ cardrow.bankname }} <cite>{{ cardrow.name }}</cite></h2>
          <h3>{{ cardrow.cardnum }}</h3>
          <span>
            <a href="#" class="modify" @click.stop.prevent="showCreateBox(cardrow.cardid)"></a>
            <a href="#" class="remove" @click.stop.prevent="removeRow(cardrow.cardid)"></a>
          </span>
        </li>
      </ul>
    </mt-popup>

    <mt-popup v-model="createVisible" position="bottom" class="createBox">
      <h1>{{ name=='' ? '' : '' }} <span @click="saveCardRow"></span></h1>
      <ul>
        <li><input type="text" value="" placeholder="" v-model="name"></li>
        <li><input type="text" value="" placeholder="" v-model="cardnum"></li>
        <li>
          <label><input type="checkbox" name="isdef" value="1" v-model="isdef" :checked="isdef"><i></i></label>
        </li>
      </ul>
      <h6></h6>
    </mt-popup>

  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'withdraw-index',
  data() {
    return {
      user: globalData.user,
      selectVisible: false,
      createVisible: false,
      topupmin: null,
      max2rmb: null,
      cardrows: [],
      withdraw_amount: 0,
      selectRow: null,
      modifyRow: null,
      name: '',
      cardnum: '',
      isdef: 0,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    submit(){
      var self = this;
      if(!self.selectRow){
        return self.$toast('');
      }
      if(self.withdraw_amount==0){
        return self.$toast('');
      }
      HttpApi.withdrawCreate(self.selectRow['cardid'], self.withdraw_amount)
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        //
        self.$toast('');
        setTimeout(function(){
          self.$router.replace('/ucp/withdraw/listing');
        }, 1500);
      });
    },
    withdrawAll(max2rmb){
      this.withdraw_amount = max2rmb;
    },
    showSelectBox(){
      this.selectVisible = true;
    },
    selectCardRow(cardid){
      this.selectVisible = false;
      var self = this;
      this.cardrows.forEach(function(cardrow){
        if(cardrow['cardid'] == cardid){
          self.selectRow = cardrow;
        }
      });
    },
    showCreateBox(cardid){
      this.createVisible = true;
      var self = this;
      self.modifyRow = null;
      this.cardrows.forEach(function(cardrow){
        if(cardrow['cardid'] == cardid){
          self.modifyRow = cardrow;
        }
      });
      if(self.modifyRow){
        self.name = self.modifyRow['name'];
        self.cardnum = self.modifyRow['cardnum'];
        self.isdef = parseInt(self.modifyRow['isdef']);
      }else{
        self.name = '';
        self.cardnum = '';
        self.isdef = 0;
      }
    },
    saveCardRow(){
      var self = this;
      if(self.name == ''){
        self.$toast('');
        return false;
      }
      if(self.cardnum == ''){
        self.$toast('');
        return false;
      }
      if(self.modifyRow){
        let params = {name: self.name, cardnum: self.cardnum, isdef: self.isdef, cardid: self.modifyRow['cardid']};
        HttpApi.bankcardModify(params)
        .then(function(res){
          if(res.errcode != 0){
            self.$toast('');
            return;
          }
          if(res.json['retcode'] != 0){
            self.$toast(res.json['errmsg']);
            return;
          }
          self.createVisible = false;

          HttpApi.withdrawIndex()
          .then(function(res){
            if(res.errcode != 0 || res.json['retcode'] != 0){
              self.$toast('');
              return;
            }
            self.cardrows = res.json['data']['cardrows'];
            if(self.selectRow){
              self.cardrows.forEach(function(cardrow){
                if(cardrow['cardid'] == self.selectRow['cardid']){
                  self.selectRow = cardrow;
                }
              });
            }
          });
        });
      }else{
        let params = {name: self.name, cardnum: self.cardnum, isdef: self.isdef};
        HttpApi.bankcardCreate(params)
        .then(function(res){
          if(res.errcode != 0){
            self.$toast('');
            return;
          }
          if(res.json['retcode'] != 0){
            self.$toast(res.json['errmsg']);
            return;
          }
          self.createVisible = false;

          HttpApi.withdrawIndex()
          .then(function(res){
            if(res.errcode != 0 || res.json['retcode'] != 0){
              return;
            }
            self.cardrows = res.json['data']['cardrows'];
          });
        });
      }
    },
    removeRow(cardid){
      var self = this;
      self.$messagebox.confirm('?').then(action => { //action = confirm
        HttpApi.bankcardDelete(cardid)
        .then(function(res){
          if(res.errcode != 0){
            self.$toast('');
            return;
          }
          if(res.json['retcode'] != 0){
            self.$toast(res.json['errmsg']);
            return;
          }
          if(self.selectRow && self.selectRow['cardid'] == cardid){
            self.selectRow = null;
          }

          HttpApi.withdrawIndex()
          .then(function(res){
            if(res.errcode != 0 || res.json['retcode'] != 0){
              return;
            }
            self.cardrows = res.json['data']['cardrows'];
          });
        });
      }).catch(function(){ //err = cancel

      });
    }
  },
  mounted() {
    var self = this;
    HttpApi.withdrawIndex()
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.max2rmb = res.json['data']['max2rmb'];
      self.cardrows = res.json['data']['cardrows'];
      self.topupmin = res.json['data']['topupmin'];

      self.cardrows.forEach(function(cardrow){
        if(cardrow['isdef'] == 1){
          self.selectRow = cardrow;
        }
      });
      if(self.cardrows.length == 1){
        self.selectRow = cardrows[0];
      }
    });
  },
}
</script>

<style scoped>
.feedbackbox{color: #fff;}

.formbox{margin-top: 66px;}
.formbox h2{height: 45px; line-height: 45px; position: relative; font-size: 14px; padding: 0 15px; background: #212429;}
.formbox h2 label{display: inline-block; padding-left: 25px; height: 45px; line-height: 45px; color: #fff; background: url('/static/icons/icon_dzfs.png') left center no-repeat; background-size: 18px 18px;}
.formbox h2 em{float: right; color: #ff3d6f; padding-right: 15px; background: url('/static/icons/arrow.png') right center no-repeat; background-size: 7px 13px;}
.formbox h3{margin-top: 10px; height: 45px; line-height: 45px; position: relative; color: #fff; font-size: 14px; padding: 0 15px; background: #212429;}
.formbox h4{height: 45px; line-height: 45px; position: relative; color: #fff; font-size: 30px; padding: 0 15px; background: #212429;}
.formbox h4 label{float: left; width: 1em; height: 45px; line-height: 45px;}
.formbox h4 input{display: block; position: absolute; height: 45px; margin-left: 1em; padding: 0; outline: none; appearance:none; -webkit-appearance: none; border: none; background: none; color: #fff; caret-color: #fff; font-size: 30px;}
.formbox h5{height: 45px; line-height: 45px; margin: 0 15px; color: #aaa; font-size: 14px; background: #212429; border-top: 1px solid #2f3236;}
.formbox h5 em{float: right; color: #ff3d6f;}
.formbox h6{line-height: 22px; padding: 10px 15px; color: #ae4a64; font-size: 14px;}

h3.subbox{height: 40px; padding: 0 40px; margin-top: 15px;}
h3.subbox input.submit{display: block; width: 100%; height: 40px; outline: none; appearance:none; -webkit-appearance: none; background: #ff3d6f; border: none; color: #17191e; text-align: center; font-size: 16px; border-radius: 5px;}

.cardrowlist{width: 100%; max-height: 50%; border-top: 1px solid #795548; background: #1b1e23; overflow-y: scroll; -webkit-overflow-scrolling: touch;}
.cardrowlist h1{height: 60px; line-height: 60px; font-size: 16px; text-indent: 15px; color: #aaa; background: url('/static/icons/icon_zfb.png') 15px center no-repeat; background-size: 16px 16px; padding-left: 20px;}
.cardrowlist h1 span{float: right; padding: 15px 20px; line-height: 30px; font-size: 30px; color: #fff; text-indent: 0;}
.cardrowlist li{position: relative; height: 65px; padding: 10px 15px; border-top: 1px solid #2f3236; line-height: 22px;}
.cardrowlist li h2{font-size: 14px; color: #fff;}
.cardrowlist li h2 cite{margin-left: 10px; font-size: 12px; color: #aaa;}
.cardrowlist li h3{font-size: 12px; color: #aaa;}
.cardrowlist li span{position: absolute; height: 100%; right: 0; top: 0; color: #aaa; line-height: 65px;}
.cardrowlist li a{display: inline-block; height: 100%; padding: 0 15px; color: #aaa; font-size: 14px;}

.createBox{width: 100%; max-height: 50%; border-top: 1px solid #795548; background: #1b1e23; overflow-y: scroll; -webkit-overflow-scrolling: touch;}
.createBox h1{height: 60px; line-height: 60px; font-size: 16px; text-indent: 15px; color: #aaa; background: url('/static/icons/icon_zfb.png') 15px center no-repeat; background-size: 16px 16px; padding-left: 20px;}
.createBox h1 span{float: right; padding: 15px 20px; line-height: 30px; font-size: 16px; color: #fff; text-indent: 0;}
.createBox li{border-top: 1px solid #2f3236; height: 46px;}
.createBox li input{width: 100%; height: 45px; padding: 15px; outline: none; appearance:none; -webkit-appearance: none; border: none; background: none; color: #aaa; caret-color: #aaa; font-size: 14px;}
.createBox label{display: block; padding: 0 15px; height: 100%; flex: 1; position: relative; font-size: 14px; color: #aaa;}
.createBox label input{position: absolute; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; background-color: transparent; border: 0; opacity: 0; cursor: pointer;}
.createBox label input + i{float: left; position: relative; margin-top: 10px; margin-right: 5px; width: 20px; height: 20px; border: 1px solid #545558; border-radius: 50%; white-space: nowrap; overflow: visible; line-height: 20px; text-indent: 23px;}
.createBox label input + i::before{position: absolute; display: none; left: 4px; top: 4px; width: 10px; height: 10px; content: '.'; font-size:0; background: #f00; border-radius: 50%;}
.createBox label input:checked + i{border: 1px solid #ff3d6f; color: #fff;}
.createBox label input:checked + i::before{display: block;}
.createBox h6{border-top: 1px solid #2f3236; font-size: 12px; color: #ae4a64; padding: 15px 15px 30px 15px;}
</style>
<style>
.mint-msgbox-wrapper .mint-msgbox{background-color: #1b1e23;}
.mint-msgbox-wrapper .mint-msgbox-title{color: #aaa;}
.mint-msgbox-wrapper .mint-msgbox-message{color: #aaa;}
.mint-msgbox-wrapper .mint-msgbox-btn{background-color: #1b1e23;}
.mint-msgbox-wrapper .mint-msgbox-content{border-bottom: 1px solid #2f3236;}
.mint-msgbox-wrapper .mint-msgbox-cancel{border-right: 1px solid #2f3236; color: #aaa;}
.mint-msgbox-wrapper .mint-msgbox-confirm{color: #ae4a64;}
</style>



