<template>
  <div class="hotlistbox">
    <header class="navbar">
      <ul class="tabbox">
          <li @click="selectTab(1)">
          <h6 :class="{active: tabIndex==1}"></h6>
        </li>
        <li @click="selectTab(0)">
          <h6 :class="{active: tabIndex==0}"></h6>
        </li>
        
        <li @click="selectTab(2)">
          <h6 :class="{active: tabIndex==2}"></h6>
        </li>
       
      </ul>
    </header>
    <keep-alive>
    <component v-bind:is="currTab" v-keep-scroll-position></component>
    </keep-alive>
  </div>
</template>

<script>

import fenlei from '@/fenlei.vue'
import nvyou from '@/nvyou.vue'
import tags from '@/tags.vue'


var tabs = [nvyou, fenlei,tags];

export default {
  name: 'HotList',
  data() {
    return {
      tabIndex: 1,
      currTab: tabs[1]
    }
  },
  methods: {
    selectTab: function(tabIndex){
      this.tabIndex = tabIndex;
      this.currTab = tabs[tabIndex];
    },
  },

  //  /vod/show/... 
  beforeRouteLeave(to, from, next) {
    if (/^\/vod\/show\//i.test(to.path)) {
      from.meta.keepAlive = true;
    }else{
      from.meta.keepAlive = false;
    }
    next();
  }
}
</script>

<style scoped>
.hotlistbox{color: #fff;}
.hotlistbox header.navbar{z-index: 1;}
</style>



