<template>
  <div class="myfeedback">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <ul class="tabbox">
        <li>
          <!--
          <router-link to="/ucp/payment/feedback">
            <h6></h6>
          </router-link>
          -->
          <h6 @click="goBack"></h6>
        </li>
        <li>
          <h6 class="active"></h6>
        </li>
      </ul>
    </header>

    <div class="listbox">
      <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
        <table>
          <thead>
            <tr><th></th><th></th><th></th></tr>
          </thead>
          <tbody>
            <tr v-for="row in rows" :key="row.id" @click="toDetail(row.id)">
              <td>{{ getType(row.cid) }}</td>
              <td>{{ row.content }}</td>
              <td>{{ row.ctimestamp.split(' ')[0] }}</td>
            </tr>
          </tbody>
        </table>
      </mt-loadmore>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'myfeedback',
  data() {
    return {
      user: globalData.user,
      pageInfo: null,
      rows: [],
      allLoaded: false,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    getType(cid){
      let types = ['', '', '', '', '', '', '', ''];
      return types[cid];
    },
    toDetail(id){
      this.$router.push('/ucp/feedback/detail?id='+id);
    },
    loadTop(){
      var self = this;
      HttpApi.feedbackListing(2, 1)
      .then(function(res){
        self.$refs.loadmore.onTopLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        var newIds = [];
        var keyId = 'id';
        for(let i = 0; i < 20; i++){
          if(i >= self.rows.length){
            break;
          }
          newIds.push(self.rows[i][keyId]);
        }

        let newRows = res.json['data']['rows'].reverse();
        for(let i = 0; i < newRows.length; i++){
          var isNew = true;
          for(let j = 0; j < newIds.length; j++){
            if(newRows[i][keyId] == newIds[j]){
              isNew = false;
            }
          }
          if(isNew){
            self.rows.unshift(newRows[i]);
          }
        }
      });
    },
    loadBottom(){
      var self = this;
      HttpApi.feedbackListing(2, self.pageInfo['page']+1)
      .then(function(res){
        self.$refs.loadmore.onBottomLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.rows.push(...res.json['data']['rows']);
        self.pageInfo = res.json['data']['pageinfo'];

        if(self.pageInfo['page'] == self.pageInfo['totalpage']){
          self.allLoaded = true;
        }
      });
    }
  },
  mounted() {
    var self = this;
    HttpApi.feedbackListing(2, 1)
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.rows = res.json['data']['rows'];
      self.pageInfo = res.json['data']['pageinfo'];

      if(self.pageInfo['page'] == self.pageInfo['totalpage']){
        self.allLoaded = true;
      }
    });
  },
}
</script>

<style scoped>
.myfeedback{color: #fff; height:100vh; overflow-y: scroll; -webkit-overflow-scrolling: touch;}
.listbox{margin-top: 56px;}
.listbox table{width: 100%; min-height: 30px; font-size: 12px; border: none; border-collapse: collapse; border-spacing: 0;}
.listbox table th, .listbox table td{border: none; padding: 10px 10px;}
.listbox table th{text-align: center;}
.listbox thead tr{background: #212429; color: #ff3d6f; font-size: 14px;}
.listbox tbody td{text-align: center; color: #aaa;}
.listbox tbody td.blink{color: #ffa800;}
</style>



