<template>
  <div class="msgbox">
    <div class="pad102"></div>
    
    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
    <div class="listbox">
      <ul v-if="rows.length">
        <li v-for="row in rows" :key="row.artid">
          <router-link :to="'/ucp/announce/detail?artid='+row.artid">
            <dl>
              <dt><img src="/static/icons/icon_wyd.png"></dt>
              <dd>
                <h2>{{ row.title }} <span>{{ row.addtime }}</span></h2>
                <h4>{{ row.intro }}</h4>
              </dd>
            </dl>
          </router-link>
        </li>
      </ul>
    </div>
    </mt-loadmore>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'announce',
  data() {
    return {
      pageInfo: null,
      rows: [],
      allLoaded: false,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    loadTop(){
      var self = this;
      HttpApi.getAnnounceListing(1)
      .then(function(res){
        self.$refs.loadmore.onTopLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        var newIds = [];
        var keyId = 'artid';
        for(let i = 0; i < 20; i++){
          if(i >= self.rows.length){
            break;
          }
          newIds.push(self.rows[i][keyId]);
        }

        let newRows = res.json['data']['rows'].reverse();
        for(let i = 0; i < newRows.length; i++){
          var isNew = true;
          for(let j = 0; j < newIds.length; j++){
            if(newRows[i][keyId] == newIds[j]){
              isNew = false;
            }
          }
          if(isNew){
            self.rows.unshift(newRows[i]);
          }
        }
      });
    },
    loadBottom(){
      var self = this;
      HttpApi.getAnnounceListing(self.pageInfo['page']+1)
      .then(function(res){
        self.$refs.loadmore.onBottomLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.rows.push(...res.json['data']['rows']);
        self.pageInfo = res.json['data']['pageinfo'];

        if(self.pageInfo['page'] == self.pageInfo['totalpage']){
          self.allLoaded = true;
        }
      });
    }
  },
  mounted() {
    var self = this;
    HttpApi.getAnnounceListing(1)
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.pageInfo = res.json['data']['pageinfo'];
      self.rows = res.json['data']['rows'];

      if(self.pageInfo['page'] == self.pageInfo['totalpage']){
        self.allLoaded = true;
      }
    });
  },
}
</script>

<style scoped>
.msgbox{color: #fff; height:100vh; overflow-y: scroll; -webkit-overflow-scrolling: touch;}
.pad102{height: 102px;}

.listbox{min-height: 300px;}
.listbox ul{overflow: hidden; margin: 0 10px;}
.listbox li{padding: 10px 0;}
.listbox dl{height: 50px;}
.listbox dt{width: 50px; height: 50px; float: left;}
.listbox dt img{display: block; width: 100%; height: 100%;}
.listbox dd{margin-left: 60px; min-height: 50px; position: relative;}
.listbox dd::after{content: ' '; position: absolute; right: 0; bottom: -10px; left: 0; height: 1px; background: #2f3236;}
.listbox dd h2{height: 26px; position: relative; padding-right: 70px; line-height: 26px; color: #fff; font-size: 14px; text-align: left; overflow: hidden; white-space: nowrap; text-overflow: ellipsis;}
.listbox dd h4{height: 24px; line-height: 24px; color: #aaaaaa; font-size: 12px; font-weight: 300; overflow: hidden; white-space: nowrap; text-overflow: ellipsis;}
.listbox dd h2 span{position: absolute; right: 0; font-size: 12px; font-weight: 300; color: #aaaaaa;}
</style>



