<template>
  <div class="searchbox">
    <header>
      <ul class="searchform">
        <li class="backbox"><a class="back" @click="goBack"><img src="/static/icons/back.png"></a></li>
        <li class="schinput">
          <input type="text" value="" placeholder="" ref="schinput">
          <i></i>
        </li>
        <li class="text" @click="goToSearch"></li>
      </ul>
    </header>
    <div class="hot-search history">
      <h2></h2>
      <ul>
        <li v-for="(wd, index) in wds" :key="index" @click='toResult(wd)'>{{ wd }}</li>
      </ul>
    </div>
    <div class="hot-search">
      <h2></h2>
      <ul>
        <li @click='toTags(hotword)'  v-for="(hotword, index) in hotwords" :key="index">{{ hotword.name }}</li>
      </ul>
    </div>
    <h2 class="list-caption mtop10">
      <em></em>
    </h2>
    <div class="hotvods">
      <vodItem2 :rows="hotrows" :addmore="false"></vodItem2>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';
import VodItem2 from '@/components/vod-item2.vue';

export default {
  name: 'search-index',
  components: {VodItem2},
  data() {
    return {
      wds: [],
      hotwords: ['','','',''],
      hotrows: [],
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    goToSearch(){
      let schwd = this.$refs.schinput.value;
      if(schwd == ''){
        return;
      }
      window.$catetitle=schwd;
      window.$catepath="videos?q="+schwd
      this.$router.push("/catevideolist");
    },
    toResult(schwd){
       window.$catetitle=schwd;
      window.$catepath="videos?q="+schwd
      this.$router.push("/catevideolist");
    },
    toTags(item){
      window.$catetitle=item.name
      window.$catepath="videos?only="+item.id;
      this.$router.push("/catevideolist");
    }
  },
  mounted() {
    var self = this;
   
   this.hotwords=window.$tags;

	  var wds = localStorage.getItem('wds');
	  self.wds = wds ? wds.split(',') : [];
  },
}
</script>

<style scoped>
.searchbox{color: #fff;}
.searchbox header{height: 50px; background: #26292f;}
.searchform{display: flex; position: fixed; top: 0; left: 0; right: 0; z-index: 1; width: 100%; align-items: center; justify-content: center; background: #26292f;}
.searchform li{height: 50px;}
.searchform li.backbox{width: 45px; height: 50px;}
.searchform li.backbox a.back{display: block; width: 100%; height: 100%;}
.searchform li.backbox a.back img{margin-top: 18px; margin-left: 15px; width: 8px; height: 14px;}

.searchform li.schinput{flex: 1; position: relative;}
.searchform li.schinput input{display: block;  margin: 10px 0; height: 30px; padding-left: 30px; border: none; background: #16191d; width: 100%; border-radius: 10px; font-size: 12px; color: #aaa;}
.searchform li.schinput input::-webkit-input-placeholder{color: #aaa;}
.searchform li.schinput i{position: absolute; left: 10px; top: 17px; width: 16px; height: 16px; background: url('/static/icons/icon_search3.png') center center no-repeat; background-size: 100%;}
.searchform li.text{padding: 0 15px; line-height: 50px; font-size: 12px; font-weight: 300; color: #fff;}

.hot-search{padding: 15px; background: #212429;}
.hot-search h2{height: 22px; line-height: 22px; font-size: 12px; color: #aaa;}
.hot-search ul{overflow: hidden;}
.hot-search li{float: left; margin-right: 10px; margin-top: 8px;display: flex;
    background: brown;
    padding: 5px;}
.hot-search a{display: inline-block; padding: 3px 15px; font-size: 12px; color: #946c4b; background: #302f2d; border-radius: 10px;}
.history{padding-bottom: 0;}
.history a{color: #ae4a64; background: #382730;}

.list-caption{height: 30px; position: relative; font-size: 14px; text-indent: 20px; line-height: 30px; background: url('/static/icons/icon_caption.png') left center no-repeat; background-size: 15px 15px;}
.list-caption em{font-size: 16px}

.hotvods{margin: 5px;}
</style>



