<template>
  <div class="promotionbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox">VIP</div>
    </header>
    <div class="logobox">
      <h1>APP</h1>
    </div>
    <div class="mainbox">
      <div class="qrbox">
        <h2></h2>
        <h3></h3>
        <div class="qrimg"><img ref="img"></div>
      </div>
      <div class="invitebox">
        <h4></h4>
        <h5>{{ user ? user.uniqkey : ''}}</h5>
      </div>
    </div>
    <div class="picbar"></div>
    <div class="bottombar">
      <h5><a href="#" class="save-qrcode" @click.prevent="saveQrcode"></a> <a href="###" class="copy-qrlink" :data-clipboard-text="qrlink"></a></h5>
      <h6><router-link to="/ucp/promotion/help"></router-link></h6>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';
import ClipboardJS from 'clipboard';

export default {
  name: 'promotion-qrpic',
  data() {
    return {
      user: globalData.user,
      qrlink: '',
      copyCount: 0
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    saveQrcode (){
      this.$toast('');
    },
  },
  mounted() {
    var self = this;
    HttpApi.taskQrcode()
    .then(function(res){
      if(res.errcode != 0){
        self.$toast('');
        return;
      }
      let objectURL = URL.createObjectURL(res.blob);
      self.$refs.img.src = objectURL;
    });
    HttpApi.taskQrlink()
    .then(function(res){
      if(res.errcode != 0 && res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.qrlink = res.json['data']['qrlink'];
    });

    let clipboard = new ClipboardJS('.copy-qrlink');
    clipboard.on('success', function(ev) {
      clipboard.destroy();
      ev.trigger.innerHTML = '';
    });
    clipboard.on('error', function(ev) {
      ev.trigger.innerHTML = '';
    });
  }
}
</script>

<style scoped>
.promotionbox{color: #fff; position: absolute; left: 0; top: 0; width: 100%; height: 100%; background: url('/static/icons/qrpic-bg.png') center center no-repeat; background-size: cover;}
header.navbar{background: transparent;}

.logobox{margin-top: 70px; height: 60px; background: url('/static/icons/qrpic-logo.png') top center no-repeat; background-size: 125px 35px;}
.logobox h1{padding-top: 40px; height: 20px; line-height: 20px; font-size: 13px; text-align: center; font-weight: 300; color: #fff;}

.mainbox{margin-top: 10px; padding: 0 22.5%;}
.mainbox .qrbox{padding-bottom: 10px; background: #fff; border-radius: 10px;}
.mainbox .qrbox h2{padding-top: 10px; line-height: 18px; font-size: 14px; color: #333; text-align: center; font-weight: bold;}
.mainbox .qrbox h3{line-height: 18px; font-size: 12px; color: #666; text-align: center; font-weight: 300;}
.mainbox .qrbox .qrimg{position: relative; padding-top: 100%; height: 0;}
.mainbox .qrbox .qrimg img{position: absolute; left: 0; top: 0; width: 100%; height: 100%;}
.mainbox .invitebox{margin-top: 1px; background: #fff; border-radius: 10px;}
.mainbox .invitebox h4{padding-top: 5px; line-height: 18px; font-size: 12px; color: #666; text-align: center; font-weight: 300;}
.mainbox .invitebox h5{padding: 0 0 5px 0; line-height: 24px; font-size: 22px; color: #333; text-align: center; font-weight: bold;}
.picbar{width: 100%; height: 29px; margin-top: 15px; background: url('/static/icons/kqzq.png') center center no-repeat; background-size: 261px 29px;}

.bottombar{position: absolute; bottom: 3%; width: 100%;}
.bottombar h5{width: 100%; text-align: center;}
.bottombar h5 a{display: inline-block; color: #fff; font-size: 16px;}
.bottombar h5 a.save-qrcode{margin-right:10px; width: 129px; height: 45px; line-height: 45px; background: url('/static/icons/bt_bcewm.png') center center no-repeat; background-size: 129px 45px;}
.bottombar h5 a.copy-qrlink{margin-left: 10px; width: 129px; height: 45px; line-height: 45px; background: url('/static/icons/bt_fztglj.png') center center no-repeat; background-size: 129px 45px;}
.bottombar h6{margin-top: 15px; width: 100%; font-size: 14px; color: #ff5354; text-align: center;}
.bottombar h6 a{font-size: 14px; color: #ff5354;}
</style>



