<template>
  <div class="settingbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
      <div class="actionbox">
        <a href="#" class="text" @click.prevent="save"></a>
      </div>
    </header>

    <div class="listbox" v-if="user">
      <ul>
        <li class="inputbox"><label></label><span><input type="password" value="" v-model="password_old" placeholder="" class="txt"></span></li>
        <li class="inputbox"><label></label><span><input type="password" value="" v-model="password" placeholder="" class="txt"></span></li>
        <li class="inputbox"><label></label><span><input type="password" value="" v-model="password_confirm" placeholder="" class="txt"></span></li>
      </ul>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'setting',
  data() {
    return {
      user: null,
      password_old: '',
      password: '',
      password_confirm: '',
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    save(){
      var self = this;
      var inputs = {password_old: self.password_old, password: self.password, password_confirm: self.password_confirm};
      HttpApi.userPasswd(inputs)
      .then(function(res){
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        self.$toast('');
      });
    }
  },
  mounted() {
    var self = this;
    HttpApi.userIndex()
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.user = res.json['data']['user'];
    });
  },
}
</script>

<style scoped>
.settingbox{color: #fff;}

.listbox{margin-top: 66px;}
.listbox ul{margin-top: 10px; background: #212429;}
.listbox li{margin: 0 15px; line-height: 42px; position: relative; color: #aaa; font-size: 14px; overflow: hidden;}
.listbox li.arrow{background: url('/static/icons/arrow.png') right center no-repeat; background-size: 7px 12px;}
.listbox li::before{position: absolute; top: 0; left: 0; right: 0; content: '.'; font-size: 0; height: 1px; background: #2f3236;}
.listbox li:first-child::before{display: none;}
.listbox li>a{display: block; width: 100%; height: 100%; line-height: 42px; color: #aaa;}
.listbox li>a>span{float: right; font-size: 12px; padding-right: 15px;}
.listbox li>a>span.active{color: #ff3d6f;}
.listbox .avatar{padding: 10px 0; overflow: hidden;}
.listbox .avatar dt{float: left; width: 42px; height: 42px;}
.listbox .avatar dt img{width: 42px; height: 42px; border-radius: 50%;}
.listbox .avatar dd{float: right; font-size: 12px; padding-right: 15px;}
.listbox .inputbox>label{float: left; width: 4.5em; height: 42px; line-height: 42px;}
.listbox .inputbox span{display: block; margin-left: 4.5em;}
.listbox .inputbox span input.txt{width: 100%; height: 42px; padding: 0 10px 0 10px; outline: none; appearance:none; -webkit-appearance: none; border: none; background: none; color: #aaa; caret-color: #aaa; font-size: 14px; text-align: right;}
.listbox .inputbox span input.txt:focus{border-color: none;}
.listbox .checkbox>label{float: left; width: 4.5em; height: 42px; line-height: 42px;}
.listbox .checkbox span{display: block; height: 42px; margin-left: 4.5em; text-align: right;}
.listbox .checkbox span label{display: inline-block; position: relative; height: 100%; margin-left: 20px; line-height: 42px;}
.listbox .checkbox span label input{position: absolute; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; background-color: transparent; border: 0; opacity: 0; cursor: pointer;}
.listbox .checkbox span label input + i{float: left; position: relative; margin-top: 10px; margin-right: 5px; width: 20px; height: 20px; border: 1px solid #545558; border-radius: 50%; white-space: nowrap; overflow: visible; line-height: 20px; text-indent: 23px;}
.listbox .checkbox span label input + i::before{position: absolute; display: none; left: 4px; top: 4px; width: 10px; height: 10px; content: '.'; font-size:0; background: #f00; border-radius: 50%;}
.listbox .checkbox span label input:checked + i{border: 1px solid #ff3d6f; color: #fff;}
.listbox .checkbox span label input:checked + i::before{display: block;}

.logout{height: 46px; margin-top: 20px; line-height: 46px; text-align: center; font-size: 14px; color: #ff3d6f; background: #212429;}
</style>



