<template>
  <div class="loginbox">
    <header class="navbar">
      <div class="leadingbox">
        <router-link to="/" class="back"><img src="/static/icons/back.png"></router-link>
      </div>
    </header>
    <div class="middlebox">
      <div>
        <h1></h1>
        <div class="formbox">
          <h2></h2>
          <form @submit.prevent="submit">
          <ul>
            <li><em class="user"></em><input type="text" name="username" v-model="username" value="" placeholder="" class="text" autocomplete="off"></li>
            <li><em class="pass"></em><input type="password" name="password" v-model="password" value="" placeholder="" class="text"></li>
            <li><input type="submit" value="" class="submit"></li>
            <li><a href="#"></a> <router-link to="/register" replace></router-link></li>
          </ul>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'login',
  components: {},
  data() {
    return {
      username: "",
      password: "",
      mobiprefix: ""
    }
  },
  beforeRouteEnter (to, from, next) {
    if(globalData.user.uid > 0){
      next(vm => {
        vm.$router.push('/');
      })
    }else{
      next();
    }
  },
  methods: {
    submit(){
      var self = this;
      HttpApi.login(this.username, this.password, this.mobiprefix)
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        globalData.user = res.json['data']['user'];
        if(self.$route.query.fw){
          self.$router.replace(self.$route.query.fw)
        }else{
          self.$router.replace('/');
        }
      });
    },
  },
  mounted() {

  },
}
</script>

<style scoped>
.loginbox{position: absolute; left: 0; top: 0; right: 0; bottom: 0; width: 100%; height: 100%; color: #fff; background-image: url('/static/icons/login_bg.png'); background-repeat: no-repeat; background-size: 100%;}
header.navbar{background: transparent;}

.middlebox{position: absolute; left: 0; top: 0; right: 0; bottom: 0; width: 100%; height: 100%; display: flex; align-items: center; justify-content: center;}
.middlebox>div{width: 100%; margin-top: -100px;}
.middlebox h1{width: 100%; height: 90px; color: #fff; font-size: 16px; padding-top: 60px; line-height: 30px; font-weight: 300; text-align: center; background: url('/static/icons/logo_login.png') top center no-repeat; background-size: 62px 60px;}
.middlebox .formbox{margin-top: 30px; padding: 0 50px;}
.middlebox .formbox h2{width: 100%; height: 40px; color: #fff; font-size: 18px; padding-left: 30px; line-height: 40px; background: url('/static/icons/icon_hy.png') left center no-repeat; background-size: 21px 18px;}
.middlebox .formbox li{margin-top: 10px; height: 40px; position: relative;}
.middlebox .formbox li em{position: absolute; left: 10px; top: 10px; display: block; width: 21px; height: 21px;}
.middlebox .formbox li em.user{background: url('/static/icons/icon_user.png') left center no-repeat; background-size: 21px 21px;}
.middlebox .formbox li em.pass{background: url('/static/icons/icon_key.png') left center no-repeat; background-size: 21px 21px;}
.middlebox .formbox input.text{width: 100%; height: 40px; padding: 0 10px 0 40px; outline: none; appearance:none; -webkit-appearance: none; border: 1px solid #1f2128; background: #1f2128; color: #fff; caret-color: #ff3d6f; font-size: 14px; border-radius: 5px;}
.middlebox .formbox input.text:focus{border-color: #ff3d6f;}
.middlebox .formbox input.submit{width: 100%; height: 40px; outline: none; appearance:none; -webkit-appearance: none; background: #ff3d6f; border: none; color: #17191e; text-align: center; font-size: 16px; border-radius: 5px;}
.middlebox .formbox li a{display: inline-block; line-height: 40px; color: #8d2d49;}
.middlebox .formbox li a:last-child{float: right;}
</style>



