import Vue from 'vue'
import Vuex from 'vuex'
import Api from '@/api.js'
Vue.use(Vuex)


const state = {

    adlist: [],
    adlist1: [],
    adlist2: [],
    adlist3: [],
    adlist4: [],
    ad1:null,
    ad2:null,
    ad3:null,
    ad4:null,
    actors: [],
    categorys: [],
    tags: [],
    lastest_videos: null,
    top_videos: null,
    topics: null,
    Q: [],
    VersionCode: '179',
    screen_ad_countdown: 8,
    player_ad_countdown: 8,
    gonggao: '',

}

const mutations = {
    setHomeInfo(state, obj) {

        state.top_videos = obj.top_videos;
        state.latest_videos = obj.latest_videos;
        state.topics = obj.topics;
    },
    setCategorys(state, obj) {

        state.categorys = obj.categorys;
        state.tags = obj.genres;
    },
    setConfig(state, obj) {
        state.screen_ad_countdown = obj.screen_ad_countdown;
        state.player_ad_countdown = obj.player_ad_countdown;
        state.gonggao = obj.gonggao;
        state.freecount = obj.freecount;
        state.adlist = obj.adlist;
        state.adlist1 = state.adlist.filter((item) => { return item.atype == "" });
        state.adlist2 = state.adlist.filter((item) => { return item.atype == "" });
        state.adlist3 = state.adlist.filter((item) => { return item.atype == "" });
        state.adlist4 = state.adlist.filter((item) => { return item.atype == "" });
        state.ad1=state.adlist1[0];
        state.ad2=state.adlist2[0];
        state.ad3=state.adlist3[0];
        state.ad4=state.adlist4[0];
        window.initApp();
    },

    randomAd()
    {
        state.ad1=state.adlist1.sort((a,b)=>{return Math.random()>0.5})[0];
        state.ad2=state.adlist2.sort((a,b)=>{return Math.random()>0.5})[0];
        state.ad3=state.adlist3.sort((a,b)=>{return Math.random()>0.5})[0];
        state.ad4=state.adlist4.sort((a,b)=>{return Math.random()>0.5})[0];
    }
}
const actions = {
    getHomeinfo({ commit, state }) {
        Api.getHomeinfo().then((res) => {
            commit('setHomeInfo', res.data);
        })
    },
    getCategorys({ commit, state }) {
        Api.getCategorys().then((res) => {
            commit("setCategorys", res.data.data);
        })
    },
    getConfig({ commit, state }) {
        Api.getConfig().then((res) => {
            commit("setConfig", res.data);
        });
    }
}

const getters = {

}

export default new Vuex.Store({
    state,
    mutations,
    actions,
    getters
})


