<script>
import HttpApi from '@/httpapi.js';
import api from '@/api.js';
import Hls from 'hls.js';
import globalData from '@/global-data.js';
import ClipboardJS from 'clipboard';

export default {
  name: 'VodShow',
  components: {},
  data() {
    return {
      user: globalData.user,
      vodrow: {},
      playurl: null,
      playPermCode: 0, //0 0
      playErrmsg: '',
      similarrows: [],
      type: 0,
      commentRows: [],
      

      pageInfo: null,
      loading: false,

      adRow0: null,
      countdown: -1,
      adRow1: null,
      
      isfavorite: 0,
      sharetext: "",
      sharePopupVisible: false,

      timer: null
    }
  },

  beforeRouteUpdate (to, from, next) {
    if(this.timer){
      clearTimeout(this.timer);
    }
    this.fetchData();
    next();
    this.$nextTick(function(){
      window.scrollTo(0, 0);
    });
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    fetchData(){
        var self=this;
            this.adRow0 = this.$store.state.ad4
            this.adRow1= this.$store.state.ad3
            this.countdown = 0;
        
       api.getVideoById(this.$route.params.vodid).then((res)=>{
              
             this.playurl=res.data.hls;

             this.vodrow=res.data;
             this.similarrows=res.data.recommend_videos;
             this.countdown=0;
             this.playPermCode=0;
             this.adCountdown(5)
            //this.play();
       })
      
    },
    play(){
      var self = this;
     
      var playurl = self.playurl;
      //var playurl = 'https://video-dev.github.io/streams/x36xhzz/x36xhzz.m3u8';
      var video = self.$refs.video;
      if(Hls.isSupported()){ //HLS
        var hls = new Hls();
        hls.loadSource(playurl);
        hls.attachMedia(video);
        hls.on(Hls.Events.MANIFEST_PARSED,function() {
          video.play();
        });
      }else if(video.canPlayType('application/vnd.apple.mpegurl')){
        video.src = playurl;
        video.addEventListener('loadedmetadata', function() {
          video.play();
        });
      }

      //X5
      /*
      if(playurl.indexOf('?') != -1){ //
        var preview_sec = parseInt(playurl.split('?')[1]);
        if(preview_sec > 0){
          var hasTips = 0;
          video.ontimeupdate = function(e){
            if(e.target.currentTime > preview_sec){
              video.pause();
              if(hasTips == 0){
                alert(preview_sec+'');
                exitFullscreen();
              }
              hasTips = 1;
            }
          }
        }
      }
      */

    },

    showVod(vodid){
      this.$router.replace('/vod/show/'+vodid);
    },
    search(tagname){
      let wd = encodeURIComponent(tagname);
      this.$router.push('/search/listing?wd='+wd);
    },

    vodUp(e){
      var self = this;
      HttpApi.vodUp(self.vodrow['vodid'])
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        let upnum = parseInt(e.target.innerText);
        e.target.innerText = upnum + 1;
      });
    },
    vodDown(e){
      var self = this;
      HttpApi.vodDown(self.vodrow['vodid'])
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        let upnum = parseInt(e.target.innerText);
        e.target.innerText = upnum + 1;
      });
    },
    favAdd(){
      var self = this;
      HttpApi.favAdd(self.vodrow['vodid'])
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
      });
    },

    showCommentBox(parentid){
      var self = this;
      this.$messagebox.prompt(parentid ? '' : '').then(({ content, action }) => {
        if(action != 'confirm' || content == ''){
          return;
        }
        HttpApi.commentPost(self.vodrow['vodid'], parentid ? parentid : 0, content)
        .then(function(res){
          if(res.errcode != 0){
            self.$toast('');
            return;
          }
          if(res.json['retcode'] != 0){
            self.$toast(res.json['errmsg']);
            return;
          }
          HttpApi.getCommentListing(self.vodrow['vodid'], self.type, 1)
          .then(function(res){
            if(res.errcode != 0 || res.json['retcode'] != 0){
              return;
            }
            self.commentRows = res.json['data']['rows'];
            self.pageInfo = res.json['data']['pageinfo'];
          });
        });
      }).catch(() => null);
    },

    changeType(type){
      var self = this;
      if(type == self.type){
        return;
      }
      self.type = type;
      HttpApi.getCommentListing(self.vodrow['vodid'], self.type, 1)
      .then(function(res){
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.commentRows = res.json['data']['rows'];
        self.pageInfo = res.json['data']['pageinfo'];
      });
    },
    commentUp(id, e){
      var self = this;
      HttpApi.commentUp(id)
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        let upnum = parseInt(e.target.innerText);
        e.target.innerText = upnum + 1;
      });
    },
    loadMore(){
      var self = this;
      self.loading = true;
      HttpApi.getCommentListing(self.vodrow['vodid'], self.type, self.pageInfo['page']+1)
      .then(function(res){
        self.loading = false;
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.commentRows.push(...res.json['data']['rows']);
        self.pageInfo = res.json['data']['pageinfo'];
      });
    },

    adCountdown(sec){
      var self = this;
      self.countdown = sec;
      self.timer = setTimeout(function(){
        if(sec > 0){
          self.adCountdown(sec-1);
        }else{
          self.play();
        }
      }, 1000);
    },
    jumpToComment(){
      var headerbox = document.querySelector('.headerbox');
      var commentform = document.querySelector('.commentform');
      var realTop = commentform.offsetTop - headerbox.offsetHeight; //
      window.scrollTo(0, realTop);
    }
  },
  mounted() {
    var self = this;
    
    self.fetchData();

    this.clipboard = new ClipboardJS('.share');
    this.clipboard.on('success', function(ev) {
      self.sharePopupVisible = true;
    });
    this.clipboard.on('error', function(ev) {
      ev.trigger.innerHTML = '';
    });


    /*
    window.onscroll = function(){
      var headerbox = document.querySelector('.headerbox');
      var commentform = document.querySelector('.commentform');
      var offsetHeight = headerbox.offsetHeight; //
      var offsetTop = commentform.offsetTop //

      if(document.documentElement.scrollTop > offsetTop){
        commentform.style.position = 'fixed';
        commentform.style.top = offsetHeight+'px';
      }else{
        commentform.style.position = 'inherit';
        commentform.style.top = '0px';
      }
    };
    */

  },
  destroyed(){
    this.clipboard.destroy();
    if(this.timer){
      clearTimeout(this.timer);
    }
  }
}

function exitFullscreen(){
	if (document.exitFullscreen) {
		document.exitFullscreen();
	} else if (document.msExitFullscreen) {
		document.msExitFullscreen();
	} else if (document.mozCancelFullScreen) {
		document.mozCancelFullScreen();
	} else if(document.oRequestFullscreen){
		document.oCancelFullScreen();
	}else if (document.webkitExitFullscreen){
		document.webkitExitFullscreen();
	}
}
</script>

