<template>
  <div class="feedbackbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <ul class="tabbox">
        <li>
          <h6 class="active"></h6>
        </li>
        <li>
          <router-link to="/ucp/payment/myfeedback">
            <h6></h6>
          </router-link>
        </li>
      </ul>
    </header>
    <div class="formbox">
      <h2>
        <label><input type="radio" name="cid" value="5" checked="checked" v-model="cid"><i></i></label>
        <label><input type="radio" name="cid" value="6" v-model="cid"><i></i></label>
        <label><input type="radio" name="cid" value="7" v-model="cid"><i></i></label>
      </h2>
      <ul class="inputlist">
        <li>
          <label></label>
          <span><em @click="showPayrow">{{ selectRow ? selectRow.payid : '' }}</em></span>
        </li>
        <li>
          <label></label>
          <span>{{ selectRow ? selectRow.itemname : '' }}</span>
        </li>
        <li>
          <label></label>
          <span>{{ selectRow ? (selectRow.paidtime ? selectRow.paidtime : selectRow.createtime) : '' }}</span>
        </li>
        <li>
          <label></label>
          <span><input type="text" value="" placeholder="" v-model="payname"></span>
        </li>
        <li>
          <label></label>
          <span><input type="text" value="" placeholder="" v-model="payaccount"></span>
        </li>
      </ul>
      <div class="inputbox">
        <h6></h6>
        <textarea placeholder="" v-model="content"></textarea>
      </div>
      <div class="uploadbox">
        <div class="previewbox" :class="{show: imgSrc}" @click="removeImg"><img :src="imgSrc" v-if="imgSrc"></div>
        <div class="picker">
          <input type="file" name="upfile" value="" accept="image/*" @change="selectFile">
        </div>
      </div>
      <div class="uploadtips">
        <h6><sup>*</sup></h6>
      </div>
    </div>

    <div class="footbar-pad"></div>
    <div class="footbar">
      <h3><input type="submit" value="" class="submit" @click="submit"></h3>
    </div>

    <mt-popup v-model="popupVisible" position="bottom" class="payrowlist">
      <h1></h1>
      <ul>
        <li v-for="payrow in payrows" :key="payrow.payid" @click="selectPayrow(payrow.payid)">
          <h2> {{ payrow.payid }} <span>{{ payrow.ispaid == 1 ? '' : (payrow.ispaid == 2 ? '' : '') }}</span></h2>
          <dl>
            <dt></dt>
            <dd>
              <h3>{{ payrow.itemname }} <span>{{ payrow.pay_amount }}</span></h3>
              <h4>{{ payrow.paidtime ? payrow.paidtime : payrow.createtime }}</h4>
            </dd>
          </dl>
        </li>
      </ul>
    </mt-popup>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'payment-feedback',
  data() {
    return {
      user: globalData.user,
      popupVisible: false,
      payrows: [],
      selectRow: null,
      cid: 5,
      payname: '',
      payaccount: '',
      content: '',
      imgSrc: null,
      upfile: null,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    showPayrow(){
      this.popupVisible = true;
    },
    selectPayrow(payid){
      var self = this;
      this.popupVisible = false;
      this.payrows.forEach(function(payrow){
        if(payrow['payid'] == payid){
          self.selectRow = payrow;
        }
      });
    },
    selectFile(e){
      var self = this;
      let upfile = e.target.files[0];
      if(upfile.size > 1024*1024*5){
          self.$toast('');
          return false;
      }
      if(!/\.(jpg|png|jpeg)$/i.test(upfile.name)){
          self.$toast('');
          return false;
      }

      var reader = new FileReader();
      reader.readAsDataURL(upfile);
      reader.onload = function (e) {
          self.imgSrc = e.target.result;
          self.upfile = upfile;
      }
    },
    removeImg(){
      var self = this;
      self.imgSrc = null;
      self.upfile = null;
    },

    submit(){
      var self = this;
      if(!self.selectRow){
        self.$toast('');
        return false;
      }
      if(!self.cid){
        self.$toast('');
        return false;
      }
      if(!self.payname){
        self.$toast('');
        return false;
      }
      if(!self.payaccount){
        self.$toast('');
        return false;
      }
      if(!self.content){
        self.$toast('');
        return false;
      }
      if(!self.upfile){
        self.$toast('');
        return false;
      }

      var formData = new FormData();
      formData.append('cid', self.cid);
      formData.append('content', self.content);
      formData.append('payid', self.selectRow['payid']);
      formData.append('payname', self.payname);
      formData.append('payaccount', self.payaccount);
      formData.append('upfiles[]', self.upfile);

      HttpApi.feedbackCreate(formData)
      .then(function(res){
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        self.$router.replace('/ucp/payment/myfeedback');
      });
    },
  },
  mounted() {
    var self = this;
    HttpApi.paymentFeedback()
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        elf.$toast('');
        return;
      }
      self.payrows = res.json['data']['payrows'];
    });
  },
}
</script>

<style scoped>
.feedbackbox{color: #fff;}

.formbox{margin-top: 56px;}
.formbox h2{padding: 15px; display: flex;}
.formbox h2 label{display: block; flex: 1; position: relative; margin: 0 5px; font-size: 14px;}
.formbox h2 label input{position: absolute; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; background-color: transparent; border: 0; opacity: 0; cursor: pointer;}
.formbox h2 label input + i{display: block; padding: 5px 15px; width: 100%; height: 100%; text-align: center; border: 1px solid #8d2e49; background: #382730; color: #ff3d6f; border-radius: 20px; overflow: hidden;}
.formbox h2 label input:checked + i{border: 1px solid #ff3d6f; color: #fff; background: #ff3d6f;}
.inputlist{padding: 0 15px; background: #212429;}
.inputlist li{overflow: hidden; font-size: 14px; border-top: 1px solid #2f3236;}
.inputlist li:first-child{border-top: 0;}
.inputlist li label{float: left; width: 4.5em; height: 45px; line-height: 45px; color: #fff;}
.inputlist li span{display: block; margin-left: 4.5em; height: 45px; line-height: 45px; color: #aaa; text-align: right;}
.inputlist li span em{display: inline-block; padding-right: 10px; background: url('/static/icons/arrow.png') right center no-repeat; background-size: 7px 13px;}
.inputlist li span input{width: 100%; height: 40px; padding: 0 10px 0 10px; outline: none; appearance:none; -webkit-appearance: none; border: none; background: none; color: #aaa; caret-color: #aaa; font-size: 14px; text-align: right;}
.inputlist li span input:focus{border-color: none;}

.inputbox{margin-top: 10px;}
.inputbox h6{height: 36px; line-height: 36px; font-size: 12px; color: #ff3d6f; text-indent: 15px;}
.inputbox textarea{padding: 15px; width: 100%; height: 100px; outline: none; -webkit-appearance: none; background: transparent; background-color: #212429; border: 0; color: #fff; font-size: 14px;}

.uploadbox{margin-top: 10px; overflow: hidden;}
.uploadbox .previewbox{width: 100px; height: 100px; float: left; margin-left: 15px; display: none;}
.uploadbox .previewbox.show{display: block;}
.uploadbox .previewbox img{width: 100%; height: 100%;}
.uploadbox .picker{width: 100px; height: 100px; float: left; margin-left: 15px; border: 1px solid #ff3d6f; background: #382730; overflow: hidden; position: relative; cursor: pointer;}
.uploadbox .picker::before{width: 60%; height: 2px; position: absolute; top: 50%; left: 50%; margin-top: -1px;  margin-left: -30%; background: #ff3d6f; content: "."; font-size: 0;}
.uploadbox .picker::after{width: 2px; height: 60%; position: absolute; top: 50%; left: 50%; margin-top: -30%; margin-left: -1px; background: #ff3d6f; content: "."; font-size: 0;}
.uploadbox .picker input{position: absolute; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; background-color: transparent; border: 0; opacity: 0; cursor: pointer;}
.uploadtips{margin-top: 10px;}
.uploadtips h6{height: 32px; padding: 0 15px; font-size: 12px; line-height: 32px; background: #2d262d; color: #d83861;}
.uploadtips h6 sup{font-size: 12px; line-height: 18px;}

.footbar-pad{height: 56px;}
.footbar{width: 100%; height: 56px; position: fixed; z-index: 99; bottom: 0; font-size: 14px; line-height: 56px; background: #26292f;}
.footbar h3{height: 40px; padding: 0 40px; margin-top: 8px;}
.footbar h3 input.submit{display: block; width: 100%; height: 40px; outline: none; appearance:none; -webkit-appearance: none; background: #ff3d6f; border: none; color: #17191e; text-align: center; font-size: 16px; border-radius: 5px;}


.payrowlist{width: 100%; max-height: 70%; border-top: 1px solid #795548; background: #1b1e23; overflow-y: scroll; -webkit-overflow-scrolling: touch;}
.payrowlist h1{font-size: 16px; height: 46px; line-height: 46px; color: #fff; text-indent: 15px;}
.payrowlist li:first-child{margin-top: 0;}
.payrowlist li{color: #fff; margin-top: 10px; background: #212429;}
.payrowlist h2{height: 42px; line-height: 42px; margin-left: 15px; border-bottom: 1px solid #2f3236; font-size: 14px; }
.payrowlist h2 span{position: absolute; right: 15px; color: #aaa;}
.payrowlist dl{padding: 10px 0;}
.payrowlist dt{width: 40px; height: 40px; margin-left: 15px; border-radius: 50%; float: left; background: #4d2937 url('/static/icons/icon_zgvip.png') center center no-repeat; background-size: 25px 25px;}
.payrowlist dd{margin-left: 70px; height: 40px;}
.payrowlist dd h3{height: 20px; line-height: 20px; font-size: 14px; color: #fff; position: relative;}
.payrowlist dd h4{height: 20px; line-height: 20px; font-size: 12px; color: #aaa;}
.payrowlist dd h3 span{position: absolute; right: 15px; color: #ff3d6f;}

</style>



