<template>
  <div class="settingbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
      <div class="actionbox">
        <a href="#" class="text" @click.prevent="save"></a>
      </div>
    </header>

    <div class="listbox" v-if="user">
      <ul>
        <li class="arrow">
          <dl class="avatar">
            <dt><img :src="imgSrc ? getAvatar(imgSrc) : getAvatar(user.avatar_url)"></dt>
            <dd @click="changeAvatar">
              <!-- <input type="file" name="upfile" value="" accept="image/*" @change="selectFile"> -->
            </dd>
          </dl>
        </li>
        <li class="inputbox"><label></label><span><input type="text" value="" v-model="nickname" placeholder="" class="txt"></span></li>
        <li class="checkbox">
          <label></label>
          <span>
            <label><input type="radio" name="gender" value="1" v-model="gender" :checked="gender==1"><i></i></label>
            <label><input type="radio" name="gender" value="2" v-model="gender" :checked="gender==2"><i></i></label>
          </span>
        </li>
      </ul>

      <ul>
        <li class="arrow">
          <a href="#"> <span></span></a>
        </li>
      </ul>

      <ul>
        <li class="arrow">
          <router-link to="/ucp/setting/passwd"></router-link>
        </li>
      </ul>

      <h2 class="logout" @click="logout"></h2>
    </div>

    <mt-popup v-model="selectAvatarVisible" position="bottom" class="createBox">
      <h1> <span @click="saveAvatar"></span></h1>
      <ul v-if="gender==2">
        <li v-for="n in [1,2,3,4,5,6,7,8,9]" :key="n">
          <label><input type="radio" name="selectedAvatar" :value="n" v-model="selectedAvatar" :checked="selectedAvatar==n ? true : false"><i></i> <img :src="'/static/avatar/00'+n+'.png'"></label>
        </li>
      </ul>
      <ul v-else>
        <li v-for="n in [1,2,3,4,5,6,7,8,9]" :key="n">
          <label><input type="radio" name="selectedAvatar" :value="n" v-model="selectedAvatar" :checked="selectedAvatar==n ? true : false"><i></i> <img :src="'/static/avatar/0'+n+'.png'"></label>
        </li>
      </ul>
    </mt-popup>

  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';
import Util from '@/util.js';

export default {
  name: 'setting',
  data() {
    return {
      user: null,
      nickname: '',
      gender: 0,
      imgSrc: null,
      selectAvatarVisible: false,
      selectedAvatar: 0,
    }
  },
  methods: {
    getAvatar(avatar_url){
      return Util.getAvatar(avatar_url);
    },
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    logout(){
      var self = this;
      HttpApi.logout()
      .then(function(){
        HttpApi.cleanAuth();
        globalData.user = globalData.guestUser;
        self.$router.replace('/');
      });
    },
    save(){
      var self = this;
      var inputs = {nickname: self.nickname, gender: self.gender};
      HttpApi.userProfile(inputs)
      .then(function(res){
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        self.$toast('');
      });
    },
    selectFile(e){
      var self = this;
      let upfile = e.target.files[0];

      if(upfile.size > 1024*1024*5){
          self.$toast('');
          return false;
      }
      if(!/\.(jpg|png|jpeg)$/i.test(upfile.name)){
          self.$toast('');
          return false;
      }

      var formData = new FormData();
      formData.append('upfile', upfile);
      HttpApi.userAvatar(formData)
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        
        var reader = new FileReader();
        reader.readAsDataURL(upfile);
        reader.onload = function (e) {
            self.imgSrc = e.target.result;
        }
      });
    },
    changeAvatar(e){
      var self = this;
      self.selectAvatarVisible = true;
    },
    saveAvatar(){
      var self = this;
      var avatarid = self.gender == 2 ? '00'+self.selectedAvatar : '0'+self.selectedAvatar;
      HttpApi.userAvatarid(avatarid)
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        self.imgSrc = avatarid;
        self.selectAvatarVisible = false;
      });
    }
  },
  mounted() {
    var self = this;
    HttpApi.userIndex()
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.user = res.json['data']['user'];
      self.nickname = self.user['nickname'];
      self.gender = self.user['gender'];
      self.selectedAvatar = parseInt(self.user['avatar_url']);
    });
  },
}
</script>

<style scoped>
.settingbox{color: #fff;}

.listbox{margin-top: 66px;}
.listbox ul{margin-top: 10px; background: #212429;}
.listbox li{margin: 0 15px; line-height: 42px; position: relative; color: #aaa; font-size: 14px; overflow: hidden;}
.listbox li.arrow{background: url('/static/icons/arrow.png') right center no-repeat; background-size: 7px 12px;}
.listbox li::before{position: absolute; top: 0; left: 0; right: 0; content: '.'; font-size: 0; height: 1px; background: #2f3236;}
.listbox li:first-child::before{display: none;}
.listbox li>a{display: block; width: 100%; height: 100%; line-height: 42px; color: #aaa;}
.listbox li>a>span{float: right; font-size: 12px; padding-right: 15px;}
.listbox li>a>span.active{color: #ff3d6f;}
.listbox .avatar{padding: 10px 0; overflow: hidden;}
.listbox .avatar dt{float: left; width: 42px; height: 42px;}
.listbox .avatar dt img{width: 42px; height: 42px; border-radius: 50%;}
.listbox .avatar dd{float: right; font-size: 12px; padding-right: 15px;}
.listbox .avatar dd input{position: absolute; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; background-color: transparent; border: 0; opacity: 0; cursor: pointer;}

.listbox .inputbox>label{float: left; width: 4.5em; height: 42px; line-height: 42px;}
.listbox .inputbox span{display: block; margin-left: 4.5em;}
.listbox .inputbox span input.txt{width: 100%; height: 42px; padding: 0 10px 0 10px; outline: none; appearance:none; -webkit-appearance: none; border: none; background: none; color: #aaa; caret-color: #aaa; font-size: 14px; text-align: right;}
.listbox .inputbox span input.txt:focus{border-color: none;}
.listbox .checkbox>label{float: left; width: 4.5em; height: 42px; line-height: 42px;}
.listbox .checkbox span{display: block; height: 42px; margin-left: 4.5em; text-align: right;}
.listbox .checkbox span label{display: inline-block; position: relative; height: 100%; margin-left: 20px; line-height: 42px;}
.listbox .checkbox span label input{position: absolute; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; background-color: transparent; border: 0; opacity: 0; cursor: pointer;}
.listbox .checkbox span label input + i{float: left; position: relative; margin-top: 10px; margin-right: 5px; width: 20px; height: 20px; border: 1px solid #545558; border-radius: 50%; white-space: nowrap; overflow: visible; line-height: 20px; text-indent: 23px;}
.listbox .checkbox span label input + i::before{position: absolute; display: none; left: 4px; top: 4px; width: 10px; height: 10px; content: '.'; font-size:0; background: #f00; border-radius: 50%;}
.listbox .checkbox span label input:checked + i{border: 1px solid #ff3d6f; color: #fff;}
.listbox .checkbox span label input:checked + i::before{display: block;}

.logout{height: 46px; margin-top: 20px; line-height: 46px; text-align: center; font-size: 14px; color: #ff3d6f; background: #212429;}

.createBox{width: 100%; max-height: 50%; border-top: 1px solid #795548; background: #1b1e23; overflow-y: scroll; -webkit-overflow-scrolling: touch;}
.createBox h1{height: 60px; line-height: 60px; font-size: 16px; text-indent: 15px; color: #aaa;}
.createBox h1 span{float: right; padding: 15px 20px; line-height: 30px; font-size: 16px; color: #fff; text-indent: 0;}
.createBox li{border-top: 1px solid #2f3236; height: 46px;}
.createBox li input{width: 100%; height: 45px; padding: 15px; outline: none; appearance:none; -webkit-appearance: none; border: none; background: none; color: #aaa; caret-color: #aaa; font-size: 14px;}
.createBox label{display: block; padding: 0 15px; height: 100%; flex: 1; position: relative; font-size: 14px; color: #aaa;}
.createBox label input{position: absolute; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; background-color: transparent; border: 0; opacity: 0; cursor: pointer;}
.createBox label input + i{float: left; position: relative; margin-top: 10px; margin-right: 5px; width: 20px; height: 20px; border: 1px solid #545558; border-radius: 50%; white-space: nowrap; overflow: visible; line-height: 20px; text-indent: 23px;}
.createBox label input + i::before{position: absolute; display: none; left: 4px; top: 4px; width: 10px; height: 10px; content: '.'; font-size:0; background: #f00; border-radius: 50%;}
.createBox label input:checked + i{border: 1px solid #ff3d6f; color: #fff;}
.createBox label input:checked + i::before{display: block;}
.createBox li img{width: 30px; height: 30px; vertical-align: middle; margin-top: 6px; margin-left: 10px;}
.createBox h6{border-top: 1px solid #2f3236; font-size: 12px; color: #ae4a64; padding: 15px 15px 30px 15px;}
</style>



