<template>
  <div class="searchbox">
    <header>
      <ul class="searchform">
        <li class="backbox"><a class="back" @click="goBack"><img src="/static/icons/back.png"></a></li>
        <li class="schinput">
          <input type="text" :value="schwd" placeholder="" ref="schinput">
          <i></i>
        </li>
        <li class="text" @click="goToSearch"></li>
      </ul>
    </header>

    <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
    <div class="listbox">
      <h2 class="noresult" v-if="pageInfo && pageInfo.total==0"><em>{{ schwd }}</em></h2>
      <h2 v-if="vodrows.length"><em>{{ schwd }}</em>{{ pageInfo.total }}</h2>
      <div class="resultbox" v-if="vodrows.length">
        <ul>
          <li v-for="row in vodrows" :key="'vod'+row.vodid" @click="showVod(row.vodid)">
            <dl>
              <dt><img :src="row.coverpic"><i>{{ row.duration }}</i></dt>
              <dd>
                <h2>{{ row.title }}</h2>
                <h3><em v-for="(tag, index) in row.tags" v-bind:key="index" @click.stop.prevent="search(tag.tagname)">{{ tag.tagname }}</em></h3>
                <h4><em>{{ row.upnum }}</em> <cite>{{ row.playcount_total }}</cite></h4>
              </dd>
            </dl>
          </li>
        </ul>
      </div>
    </div>
    </mt-loadmore>

  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'search-listing',
  data() {
    return {
      pageInfo: null,
      vodrows: [],
      schwd: '',
      allLoaded: false
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    showVod(vodid){
      this.$router.push('/vod/show/'+vodid);
    },
    search(tagname){
      let wd = tagname;
      this.$router.replace('/search/listing?wd='+encodeURIComponent(wd));
      this.schwd = wd;
      this.fetchData(wd);
    },
    goToSearch(){
      this.schwd = this.$refs.schinput.value;
      this.fetchData(this.schwd);
    },
    fetchData(schwd, page){
      if(schwd == ''){
        return;
      }
      var self = this;
      HttpApi.searchListing(schwd, page ? page : 1)
      .then(function(res){
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.vodrows = res.json['data']['vodrows'];
        self.pageInfo = res.json['data']['pageinfo'];
      });

      var wds = localStorage.getItem('wds');
      wds = wds ? wds.split(',') : [];
      wds.push(schwd);
      wds = [...new Set(wds)]; //es6
      wds = wds.slice(-20);
      localStorage.setItem('wds', wds.join(','));
    },
    loadBottom(){
      var self = this;
      HttpApi.searchListing(self.schwd, self.pageInfo['page'] + 1)
      .then(function(res){
        self.$refs.loadmore.onBottomLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.pageInfo = res.json['data']['pageinfo'];
        self.vodrows.push(...res.json['data']['vodrows']);

        if(self.pageInfo['page'] == self.pageInfo['totalpage']){
          self.allLoaded = true;
        }
      });
    },
  },
  mounted() {
    var self = this;
    self.schwd = self.$route.query.wd ? self.$route.query.wd: '';
    self.fetchData(self.schwd);
  },

  //  /vod/show/... 
  beforeRouteLeave(to, from, next) {
    if (/^\/vod\/show\//i.test(to.path)) {
      from.meta.keepAlive = true;
    }else{
      from.meta.keepAlive = false;
    }
    next();
  }
}
</script>

<style scoped>
.searchbox{color: #fff; height:100vh; overflow-y: scroll; -webkit-overflow-scrolling: touch;}
.searchbox header{height: 50px; background: #26292f;}
.searchform{display: flex; position: fixed; top: 0; left: 0; right: 0; z-index: 1; width: 100%; align-items: center; justify-content: center; background: #26292f;}
.searchform li{height: 50px;}
.searchform li.backbox{width: 45px; height: 50px;}
.searchform li.backbox a.back{display: block; width: 100%; height: 100%;}
.searchform li.backbox a.back img{margin-top: 18px; margin-left: 15px; width: 8px; height: 14px;}

.searchform li.schinput{flex: 1; position: relative;}
.searchform li.schinput input{display: block;  margin: 10px 0; height: 30px; padding-left: 30px; border: none; background: #16191d; width: 100%; border-radius: 10px; font-size: 12px; color: #aaa;}
.searchform li.schinput input::-webkit-input-placeholder{color: #aaa;}
.searchform li.schinput i{position: absolute; left: 10px; top: 17px; width: 16px; height: 16px; background: url('/static/icons/icon_search3.png') center center no-repeat; background-size: 100%;}
.searchform li.text{padding: 0 15px; line-height: 50px; font-size: 12px; font-weight: 300; color: #fff;}


.listbox{margin-top: 10px; min-height: 300px;}
.listbox>h2{height: 40px; line-height: 40px; padding: 0 15px; font-size: 12px;}
.listbox>h2 em{color: #c7375d;}
.listbox>h2.noresult{margin-top: 100px; color: #aaa; text-align: center;}

.resultbox{overflow: hidden;}
.resultbox ul{overflow: hidden; margin: 0 10px;}
.resultbox li{margin-top: 15px;}
.resultbox li:first-child{margin-top: 15px;}
.resultbox dl{height: 100px; overflow: hidden;}
.resultbox dt{width: 160px; height: 100px; position: relative; float: left;}
.resultbox dt img{display: block; width: 100%; height: 100%;  border-radius: 15px;}
.resultbox dt i{position: absolute; bottom: 5px; right: 5px; height: 18px; line-height: 18px; padding: 0 6px; background-color: rgba(0, 0, 0, 0.5); color: #fff; font-size: 12px; border-radius: 9px 0 9px 0;}
.resultbox dd{margin-left: 170px;}
.resultbox dd h2{height: 22px; line-height: 22px; color: #fff; font-size: 16px; text-align: left; overflow: hidden; white-space: nowrap; text-overflow: ellipsis;}
.resultbox dd h3{height: 60px; overflow: hidden;}
.resultbox dd h3 em{display: inline-block; margin: 3px 5px; padding: 3px 15px; font-size: 12px; border-radius: 15px; background: #35242d; color: #ae4a64;}
.resultbox dd h4{height: 18px; line-height: 18px; color: #aaaaaa; font-size: 12px;}
.resultbox dd h4 em{display: inline-block; padding-left: 18px; background: url('/static/icons/icon_dzrs.png') left center no-repeat; background-size: 15px 14px;}
.resultbox dd h4 cite{display: inline-block; margin-left: 10px; padding-left: 18px; background: url('/static/icons/icon_bfrs.png') left center no-repeat; background-size: 15px 12px;}

</style>



