import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '@/home.vue'
import VodList from '@/vodlist.vue'
//import VodShow from '@/vodshow.vue'
import VodShow from '@/player.vue'
import SpecialDetail from '@/special-detail.vue'
import SearchIndex from '@/search-index.vue'
import SearchListing from '@/search-listing.vue'
import Login from '@/login.vue'
import Register from '@/register.vue'
import CateVideoList from '@/CateVideoList.vue'

import Playlog from '@/playlog.vue'
import Favorite from '@/favorite.vue'
import Message from '@/ucp/message.vue'
import AnnounceDetail from '@/ucp/announce-detail.vue'
import MsgShow from '@/ucp/msg-show.vue'
import Feedback from '@/ucp/feedback.vue'
import FeedbackDetail from '@/ucp/feedback-detail.vue'
import Task from '@/ucp/task.vue'
import VippkgIndex from '@/ucp/vippkg-index.vue'
import VippkgCoinorder from '@/ucp/vippkg-coinorder.vue'
import PaymentListing from '@/ucp/payment-listing.vue'
import PaymentFeedback from '@/ucp/payment-feedback.vue'
import PaymentMyFeedback from '@/ucp/payment-my-feedback.vue'
import PaymentHelp from '@/ucp/payment-help.vue'
import PaymentSafepaylog from '@/ucp/payment-safepaylog.vue';

import WithdrawIndex from '@/ucp/withdraw-index.vue';
import WithdrawListing from '@/ucp/withdraw-listing.vue';

import AccountIndex from '@/ucp/account-index.vue';

import CoinlogBonuslog from '@/ucp/coinlog-bonuslog.vue';

import PromotionIndex from '@/ucp/promotion-index.vue';
import PromotionQrpic from '@/ucp/promotion-qrpic.vue';
import PromotionHelp from '@/ucp/promotion-help.vue';

import TaskboxIndex from '@/ucp/taskbox-index.vue';
import Affcenter from '@/ucp/affcenter.vue';
import Myaff from '@/ucp/myaff.vue';

import SettingIndex from '@/ucp/setting-index.vue';
import SettingUser from '@/ucp/setting-user.vue';
import SettingPasswd from '@/ucp/setting-passwd.vue';

import globalData from '@/global-data.js';

Vue.use(VueRouter)

const routes = [
    {path: '/', component: Home, meta: {keepAlive: true}}, //{keepAlive: true}
    {path: '/vod/listing-:params', component: VodList, meta: {keepAlive: true}},
    {path: '/vod/show/:vodid',name:'player',component: VodShow, meta: {}},
    {path: '/special/detail/:spid', component: SpecialDetail},
    {path: '/search/index', component: SearchIndex, meta: {}},
    {path: '/search/listing', component: SearchListing, meta: {keepAlive: true}},

    {path: '/login', component: Login, meta: {}},
    {path: '/register', component: Register, meta: {}},
    {path: '/playlog/listing', component: Playlog, meta: {requiresAuth: true}},
    {path: '/favorite/listing', component: Favorite, meta: {requiresAuth: true}},

    {path:'/catevideolist',name:"catevideolist",component:CateVideoList,meta:{}},

    {path: '/ucp/task', component: Task, meta: {requiresAuth: true}},
    {path: '/ucp/message', component: Message, meta: {requiresAuth: true}},
    {path: '/ucp/announce/detail', component: AnnounceDetail, meta: {requiresAuth: true}},
    {path: '/ucp/msg/show', component: MsgShow, meta: {requiresAuth: true}},
    {path: '/ucp/feedback', component: Feedback, meta: {requiresAuth: true}},
    {path: '/ucp/feedback/detail', component: FeedbackDetail, meta: {requiresAuth: true}},
    {path: '/ucp/vippkg/index', component: VippkgIndex, meta: {requiresAuth: true}},
    {path: '/ucp/vippkg/coinorder', component: VippkgCoinorder, meta: {requiresAuth: true}},
    {path: '/ucp/payment/listing', component: PaymentListing, meta: {requiresAuth: true}},
    {path: '/ucp/payment/feedback', component: PaymentFeedback, meta: {requiresAuth: true}},
    {path: '/ucp/payment/myfeedback', component: PaymentMyFeedback, meta: {requiresAuth: true}},
    {path: '/ucp/payment/help', component: PaymentHelp, meta: {requiresAuth: true}},
    {path: '/ucp/payment/safepaylog', component: PaymentSafepaylog, meta: {requiresAuth: true}},

    {path: '/ucp/withdraw/index', component: WithdrawIndex, meta: {requiresAuth: true}},
    {path: '/ucp/withdraw/listing', component: WithdrawListing, meta: {requiresAuth: true}},

    {path: '/ucp/account/index', component: AccountIndex, meta: {requiresAuth: true}},

    {path: '/ucp/coinlog/bonuslog', component: CoinlogBonuslog, meta: {requiresAuth: true}},

    {path: '/ucp/promotion/index', component: PromotionIndex, meta: {}},
    {path: '/ucp/promotion/qrpic', component: PromotionQrpic, meta: {requiresAuth: true}},
    {path: '/ucp/promotion/help', component: PromotionHelp, meta: {requiresAuth: true}},

    {path: '/ucp/taskbox/index', component: TaskboxIndex, meta: {}},
    {path: '/ucp/affcenter', component: Affcenter, meta: {requiresAuth: true}},
    {path: '/ucp/myaff', component: Myaff, meta: {requiresAuth: true}},

    {path: '/ucp/setting/index', component: SettingIndex, meta: {requiresAuth: true}},
    {path: '/ucp/setting/user', component: SettingUser, meta: {requiresAuth: true}},
    {path: '/ucp/setting/passwd', component: SettingPasswd, meta: {requiresAuth: true}},
];

const router = new VueRouter({
    //mode: 'history',
    routes,
});

router.beforeEach((to, from, next) => {
    next();
    return;
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (!globalData.user || globalData.user.uid == 0) {
            next({ path: '/register', query: { fw: to.fullPath } })
        } else {
            next()
        }
    } else {
        next() //  next()
    }
})

export default router


