<template>
  <div class="feedbackbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
    </header>
    <div class="pad76"></div>

    <div class="formbox">
      <dl>
        <dt>
          <h1></h1>
          <h2>
            <label><input type="radio" name="cid" value="1"><i></i></label>
            <label><input type="radio" name="cid" value="2"><i></i></label>
            <label><input type="radio" name="cid" value="3"><i></i></label>
            <label><input type="radio" name="cid" value="4"><i></i></label>
          </h2>
        </dt>
        <dd>
          <textarea placeholder="..."></textarea>
        </dd>
      </dl>
      <h3><input type="submit" value="" class="submit"></h3>
    </div>

    <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
    <div class="listbox">
      <h2></h2>
      <ul>
        <li v-for="row in rows" :key="row.id">
          <router-link :to="'/ucp/feedback/detail?id='+row.id">
          <dl>
            <dt><img :src="user.avatar_url"></dt>
            <dd>
              <div class="textblock">{{ row.content }}</div>
              <h6>{{ row.ctimestamp }}</h6>
              <blockquote v-if="row.replytime"><label>:</label> {{ row.replytext }}</blockquote>
            </dd>
          </dl>
          </router-link>
        </li>
      </ul>
    </div>
    </mt-loadmore>

  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'feedback',
  data() {
    return {
      user: globalData.user,
      pageInfo: null,
      rows: [],
      payrows: [],
      allLoaded: false,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    loadBottom(){
      var self = this;
      HttpApi.feedbackListing(1, self.pageInfo['page']+1)
      .then(function(res){
        self.$refs.loadmore.onBottomLoaded();
        if(res.errcode != 0 || res.json['retcode'] != 0){
          self.$toast('');
          return;
        }
        self.rows.push(...res.json['data']['rows']);
        self.pageInfo = res.json['data']['pageinfo'];

        if(self.pageInfo['page'] == self.pageInfo['totalpage']){
          self.allLoaded = true;
        }
      });
    }
  },
  mounted() {
    var self = this;
    HttpApi.feedbackIndex()
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.payrows = res.json['data']['payrows'];
    });

    HttpApi.feedbackListing(1, 1)
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.pageInfo = res.json['data']['pageinfo'];
      self.rows = res.json['data']['rows'];

      if(self.pageInfo['page'] == self.pageInfo['totalpage']){
        self.allLoaded = true;
      }
    });

  },
}
</script>

<style scoped>
.feedbackbox{color: #fff; height:100vh; overflow-y: scroll; -webkit-overflow-scrolling: touch;}
.pad76{height: 76px;}

.formbox{}
.formbox dt{background: #212429; padding: 10px 15px;}
.formbox dd{background: #212429; margin-top: 10px;}
.formbox h1{height: 30px; line-height: 30px; font-size: 12px; color: #aaa;}
.formbox h2{height: 40px; line-height: 40px; font-size: 14px; color: #aaa; display: flex;}
.formbox h2 label{display: block; flex: 1; position: relative;}
.formbox h2 label input{position: absolute; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; background-color: transparent; border: 0; opacity: 0; cursor: pointer;}
.formbox h2 label input + i{float: left; position: relative; margin-top: 10px; margin-right: 5px; width: 20px; height: 20px; border: 1px solid #545558; border-radius: 50%; white-space: nowrap; overflow: visible; line-height: 20px; text-indent: 23px;}
.formbox h2 label input + i::before{position: absolute; display: none; left: 4px; top: 4px; width: 10px; height: 10px; content: '.'; font-size:0; background: #f00; border-radius: 50%;}
.formbox h2 label input:checked + i{border: 1px solid #ff3d6f; color: #fff;}
.formbox h2 label input:checked + i::before{display: block;}
.formbox dd textarea{padding: 15px; width: 100%; height: 160px; outline: none; -webkit-appearance: none; background: transparent; border: 0; color: #fff; font-size: 14px;}
.formbox h3{height: 40px; padding: 0 40px; margin-top: 40px;}
.formbox h3 input.submit{width: 100%; height: 40px; outline: none; appearance:none; -webkit-appearance: none; background: #ff3d6f; border: none; color: #17191e; text-align: center; font-size: 16px; border-radius: 5px;}

.listbox{margin-top: 40px; min-height: 300px;}
.listbox > h2{height: 26px; line-height: 26px; text-indent: 15px; font-size: 14px; color: #aaa;}
.listbox ul{margin-top: 10px; background: #212429;}
.listbox li{padding: 0 15px;}
.listbox li dl{padding: 15px 0; position: relative;}
.listbox li dl::before{position: absolute; width: 100%; height: 1px; top: 0; left: 0; right: 0; bottom: 0; content: '.'; font-size:0; background: #2f3236;}
.listbox li:first-child dl::before{display: none;}
.listbox li dt{width: 50px; height: 50px; float: left;}
.listbox li dt img{width: 100%; height: 100%; border-radius: 50%;}
.listbox li dd{margin-left: 60px; font-size: 14px; line-height: 22px;}
.listbox li dd .textblock{font-size: 14px; color: #fff;}
.listbox li dd h6{height: 22px; line-height: 22px; font-size: 12px; color: #aaa;}
.listbox li dd blockquote{margin-top: 5px; padding: 5px 10px; color: #ff3d6f; background: #272a30; border-radius: 5px;}
</style>



