<template>
  <div class="taskbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
    </header>
    <div class="tasklist">
      <ul>
        <li class="V1">
          <h3>10{{ play10 ? play10.daynum : 0 }}</h3>
          <h4>{{ play10 ? play10.limit : 0 }}</h4>
          <span><i>{{ play10 ? play10.coinnum : 0 }}/{{ play10 ? play10.limit : 0 }}</i> <router-link to="/"></router-link></span>
        </li>
        <li class="V2">
          <h3>5{{ favorite ? favorite.daynum : 0 }}</h3>
          <h4>{{ favorite ? favorite.limit : 0 }}</h4>
          <span><i>{{ favorite ? favorite.coinnum : 0 }}/{{ favorite ? favorite.limit : 0 }}</i> <router-link to="/"></router-link></span>
        </li>
        <li class="V3">
          <h3>{{ adviewclick ? adviewclick.daynum : 0 }}</h3>
          <h4>{{ adviewclick ? adviewclick.limit : 0 }}</h4>
          <span><i>{{ adviewclick ? adviewclick.coinnum : 0 }}/{{ adviewclick ? adviewclick.limit : 0 }}</i> <router-link to="/"></router-link></span>
        </li>
        <li class="V4">
          <h3>{{ comment ? comment.daynum : 0 }}</h3>
          <h4>{{ comment ? comment.limit : 0 }}</h4>
          <span><i>{{ comment ? comment.coinnum : 0 }}/{{ comment ? comment.limit : 0 }}</i> <router-link to="/"></router-link></span>
        </li>
        <li class="V5">
          <h3></h3>
          <h4>{{ saveqrcode ? saveqrcode.daynum : 0 }}</h4>
          <span><i>{{ saveqrcode ? saveqrcode.coinnum : 0 }}/{{ saveqrcode ? saveqrcode.limit : 0 }}</i> <router-link to="/ucp/promotion/qrpic"></router-link></span>
        </li>
      </ul>
    </div>

  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'affcenter',
  data() {
    return {
      share: null,
      comment: null,
      favorite: null,
      play10: null,
      saveqrcode: null,
      adviewclick: null,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
  },
  computed: {
    
  },
  mounted() {
    var self = this;
    HttpApi.taskIndex()
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.share = res.json['data']['share'];
      self.comment = res.json['data']['comment'];
      self.favorite = res.json['data']['favorite'];
      self.play10 = res.json['data']['play10'];
      self.saveqrcode = res.json['data']['saveqrcode'];
      self.adviewclick = res.json['data']['adviewclick'];
    });
  },
}
</script>

<style scoped>
.taskbox{color: #fff;}

.tasklist{margin-top: 56px;}
.tasklist li{position: relative; height: 80px; padding-left: 75px; padding-top: 15px; padding-bottom: 15px; min-height: 50px; }
.tasklist li::after{position: absolute; left: 75px; bottom: 0; right: 0; height: 1px; content: '.'; font-size: 0; background: #2f3236;}
.tasklist .V1{background: url('/static/icons/icon_gk.png') 15px center no-repeat; background-size: 50px 50px;}
.tasklist .V2{background: url('/static/icons/icon_scx.png') 15px center no-repeat; background-size: 50px 50px;}
.tasklist .V3{background: url('/static/icons/icon_dj.png') 15px center no-repeat; background-size: 50px 50px;}
.tasklist .V4{background: url('/static/icons/icon_pl.png') 15px center no-repeat; background-size: 50px 50px;}
.tasklist .V5{background: url('/static/icons/icon_ewm.png') 15px center no-repeat; background-size: 50px 50px;}
.tasklist li h3{font-size: 14px; color: #fff; line-height: 25px;}
.tasklist li h4{font-size: 12px; color: #aaa; line-height: 25px;}
.tasklist li em{color: #ff3d6f;}
.tasklist li span{position: absolute; right: 15px; top: 0; bottom: 0; line-height: 70px;}
.tasklist li span i{font-size: 12px; color: #aaa;}
.tasklist li span a{margin-left: 10px; display: inline-block; padding: 0 10px; line-height: 22px; font-size: 12px; border: 1px solid #90314c; border-radius: 15px; color: #ff3d6f;}
.tasklist li span a.done{color: #ae4a64; background: #32212b; border-color: #32212b;}
</style>



