<template>
  <ul class="list-item-2" v-if="rows.length">
    <li v-for="row in rows" :key="row.vodid" :class="{paster: row.isad}">
      <div class="pasterbox" v-if="row.isad">
        <a :href="row.url" target="_blank"><img :src="row.cover"></a>
      </div>
      <router-link :to="'/vod/show/' + row.code" v-else>
        <dl>
          <dt>
            <img v-lazy="row.cover">
            <i>{{ row.duration }}</i>
            <cite :class="{ p720: row.definition==3, p1080: row.definition==4 }"></cite>
            <strong v-if="row.limit_free"></strong>
          </dt>
          <dd>
            <h3>{{ row.title }}</h3>
            <h4 v-if="addmore"><span>{{ row.scorenum }}  {{ row.areaname }}  {{ row.catename }}</span></h4>
          </dd>
        </dl>
      </router-link>
    </li>
  </ul>
</template>

<script>

export default {
  name: 'VodItem2',
  props: ['rows', 'addmore'],
  data() {
    return {

    }
  }
}
</script>

<style scoped>
.list-item-2{overflow: hidden;}
.list-item-2 li{width: 50%; float: left; margin-top: 15px;}
.list-item-2 li:nth-child(-n+2){margin-top: 0;}
.list-item-2 dl{margin: 0 5px; overflow: hidden;}
.list-item-2 dt{width: 100%; height: 0; float: none; overflow: visible; padding-top: 61%; position: relative;}
.list-item-2 dt img{width: 100%; height: 100%; position: absolute; top: 0; left: 0; border-radius: 15px;}
.list-item-2 dt i{position: absolute; bottom: 5px; right: 5px; height: 18px; line-height: 18px; padding: 0 6px; background-color: rgba(0, 0, 0, 0.5); color: #fff; font-size: 12px; border-radius: 9px 0 9px 0;}
.list-item-2 dt cite{position: absolute; left: 0; top: 0; width: 50px; height: 17px;}
.list-item-2 dt cite.p720{background: url('/static/icons/720p.png') right center no-repeat; background-size: 50px 17px;}
.list-item-2 dt cite.p1080{background: url('/static/icons/1080p.png') right center no-repeat; background-size: 50px 17px;}
.list-item-2 dt strong{position: absolute; right: 0; top: 0; width: 38px; height: 38px; background: url('/static/icons/label_xm.png') right center no-repeat; background-size: 38px 38px;}
.list-item-2 dt em{position: absolute; left: 50%; top: 50%; width: 35px; height: 35px; transform: translate(-50%, -50%); background: url('/static/icons/icon_play.png') center center no-repeat; background-size: 35px 35px;}
.list-item-2 dd{margin-top: 5px;}
.list-item-2 dd h3{height: 22px; line-height: 22px; color: #fff; font-size: 14px; text-align: center; overflow: hidden; white-space: nowrap; text-overflow: ellipsis;}
.list-item-2 dd h4{height: 18px; text-align: center;}
.list-item-2 dd h4 span{display: inline-block; height: 20px; padding: 0 0 0 15px; line-height: 22px; font-size: 12px; background-color: #14161a; background: url('/static/icons/icon_pf.png') left center no-repeat; background-size: 12px 11px; border-radius: 10px; color: #929397; text-align: center;}

.list-item-2 li.paster{width: 100%; margin-top: 0;}
.pasterbox{padding: 10px 0;}
.pasterbox img{width: 100%; border-radius: 10px;}
</style>


