<template>
  <div class="hotlistbox">
    <header class="navbar">
      <ul class="tabbox">
        <li @click="selectTab(0)">
          <h6 :class="{active: tabIndex==0}"></h6>
        </li>
        <li @click="selectTab(1)">
          <h6 :class="{active: tabIndex==1}"></h6>
        </li>
        <li @click="selectTab(2)">
          <h6 :class="{active: tabIndex==2}"></h6>
        </li>
      </ul>
    </header>
    <keep-alive>
    <VideoList ref="vlist"></VideoList>
    </keep-alive>
  </div>
</template>

<script>

import VideoList from '@/videolist.vue';

//var tabs = [TabRecommendList, TabHotList, TabDiscoverList];

var tabs=["multi/actors/25699,2299,11070,9432,27901,27678,27837,27763,27640/videos","genres/402/videos","short/genres/36/videos"]

export default {
  name: 'HotList',
  data() {
    return {
      tabIndex: 0,
      currTab: tabs[0]
    }
  },
  components:{
    VideoList
  },
  methods: {
    selectTab: function(tabIndex){
      this.tabIndex = tabIndex;
      this.currTab = tabs[tabIndex];
      this.$refs.vlist.updatePath(this.currTab);
      console.log(this.$refs.vlist);
    },
    
  },
  mounted(){
     this.$refs.vlist.updatePath(this.currTab);
  },

  //  /vod/show/... 
  beforeRouteLeave(to, from, next) {
    if (/^\/vod\/show\//i.test(to.path)) {
      from.meta.keepAlive = true;
    }else{
      from.meta.keepAlive = false;
    }
    next();
  }
}
</script>

<style scoped>
.hotlistbox{color: #fff;}
.hotlistbox header.navbar{z-index: 1;}
</style>



