<template>
  <div class="safepaylog">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
    </header>
    <div class="listbox">
      <ul v-if="rows.length">
        <li v-for="row in rows" :key="row.payid">
          <dl>
            <dt> {{ row.payid }} <span>{{ row.ispaid == 1 ? '' : (row.ispaid == 2 ? '' : '') }}</span></dt>
            <dd>
              <h2><img src="/static/icons/icon_gfcz.png">
                <p class="first">{{ row.itemname }}</p>
                <p>{{ row.paidtime ? row.paidtime.split(' ')[0] : row.createtime.split(' ')[0] }}</p>
                <sup>{{ row.pay_amount }}</sup>
              </h2>
              <h3><span><em class="copyinfo" :data-clipboard-text="''+row.payid+'\n'+row.itemname+' '+row.pay_amount+' :'+(row.paidtime ? row.paidtime.split(' ')[0] : row.createtime.split(' ')[0])"></em></span></h3>
            </dd>
          </dl>
        </li>
      </ul>

      <h6>710</h6>
    </div>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';
import ClipboardJS from 'clipboard';

export default {
  name: 'payment-safepaylog',
  data() {
    return {
      rows: [],
      clipboard: null,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
  },
  mounted() {
    var self = this;
    HttpApi.paymenSafepaylog()
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.rows = res.json['data']['payrows'];
    });

    this.clipboard = new ClipboardJS('.copyinfo');
    this.clipboard.on('success', function(ev) {
      ev.trigger.innerHTML = '';
    });
    this.clipboard.on('error', function(ev) {
      ev.trigger.innerHTML = '';
    });
  },
  destroyed(){
    this.clipboard.destroy();
  }
}
</script>

<style scoped>
.safepaylog{color: #fff;}

.listbox{margin-top: 56px;}
.listbox ul{overflow: hidden;}
.listbox li{margin-top: 10px; padding: 0 15px; background: #212429; font-size: 14px;}
.listbox dt{position: relative; width: 100%; height: 42px; line-height: 42px; border-bottom: 1px solid #2f3236;}
.listbox dt span{position: absolute; right: 0;}
.listbox dd{padding: 10px 0;}
.listbox dd h2{position: relative; height: 50px; font-size: 14px; overflow: hidden; white-space: nowrap; text-overflow: ellipsis;}
.listbox dd h2 img{float: left; width: 61px; height: 50px;}
.listbox dd h2 p{margin-left: 70px; font-size: 14px; color: #aaa; line-height: 25px;}
.listbox dd h2 p.first{font-size: 16px; color: #fff; line-height: 25px;}
.listbox dd h2 sup{position: absolute; top: 0; right: 0; font-size: 18px; color: #ff3d6f;}
.listbox dd h3{height: 28px; color: #fff; font-size: 14px; text-align: center;}
.listbox dd h3 span{float: right; }
.listbox dd h3 span em{display: inline-block; line-height: 25px; padding: 0 15px; border: 1px solid #bd365a; color: #bd365a; border-radius: 15px;}
.listbox h6{padding: 20px 15px; text-align: center; font-size: 12px; color: #aaa;}
</style>



