<template>
<div>
    <div>
        <keep-alive>
         
            <component v-bind:is="currTab"></component>
          
        </keep-alive>

        <footer class="tabbar">
            <ul>
                <li @click="selectTab(0)">
                    <img :src="tabIndex==0 ? '/static/icons/tab_sy_sel.png' : '/static/icons/tab_sy.png'">
                    <h6 :class="{active: tabIndex==0}"></h6>
                </li>
                <li @click="selectTab(1)">
                    <img :src="tabIndex==1 ? '/static/icons/tab_pd_sel.png' : '/static/icons/tab_pd.png'">
                    <h6 :class="{active: tabIndex==1}"></h6>
                </li>
                <li @click="selectTab(2)">
                    <img :src="tabIndex==2 ? '/static/icons/tab_rd_sel.png' : '/static/icons/tab_rd.png'">
                    <h6 :class="{active: tabIndex==2}"></h6>
                </li>
                <li @click="selectTab(3)" v-if="false">
                    <img :src="tabIndex==3 ? '/static/icons/tab_wd_sel.png' : '/static/icons/tab_wd.png'">
                    <h6 :class="{active: tabIndex==3}"></h6>
                </li>
            </ul>
        </footer>
    </div>
    <transition name="custom-classes-transition" enter-active-class="" leave-active-class="animated slideOutLeft">
        <div class="fullscreen-box" v-if="countdown>0 && adRow">
            <a :href="adRow.clickurl" target="_blank"><img :src="adRow.img"></a>
            <span>{{ countdown }}</span>
        </div>
    </transition>
    <mt-popup v-model="saveVisible" position="bottom" class="saveBox">
        <div class="popbox" v-if="countdown==0">
            <h6><img src="/static/icons/homelogo.png" class="homelogo"> <img src="/static/icons/homescreen.png"></h6>
            <cite></cite>
        </div>
    </mt-popup>
    <mt-popup  style="z-index:100000;" v-model="showGonggao" class="ggbox">
      
          <div class="gtitle"></div>
          <div class="gcontent">{{this.$store.state.gonggao}}</div>
          <div class="gaction"><mt-button  size="small" type="danger" @click="showGonggao=false;"></mt-button></div>
      
    </mt-popup>
</div>
</template>

<script>
import Index from '@/index.vue';
import Special from '@/pingdao.vue';
import HotList from '@/hotlist.vue';
import My from '@/my.vue';
import globalData from '@/global-data.js';

var tabs = [Index, Special, HotList, My];

import api from '@/api.js'

export default {
    data: function () {
        return {
            tabIndex: 0,
            currTab: tabs[0],
            saveVisible: false,
            countdown:-1,
            first:true,
            showGonggao:false
            
        }
    },
    methods: {
        selectTab: function (tabIndex) {
            this.tabIndex = tabIndex;
            this.currTab = tabs[tabIndex];
        },
        adCountdown(sec) {
            var self = this;
            self.countdown = sec;
            setTimeout(function () {
                if (sec > 0) {
                    self.adCountdown(sec - 1);
                }
            }, 1000);
        },
    },
    computed:{
        adRow(){
          return this.$store.state.ad1;
        },
       
   
    },
    mounted() {
        var self = this;
        this.showGonggao=this.$store.state.gonggao.length>0&&this.first
        this.first=false;
        this.countdown=this.$store.state.screen_ad_countdown;
        this.adCountdown(this.countdown);
        //self.saveVisible = self.$route.query.start ? false : true;

        //if(globalData.isInStandaloneMode && globalData.launchedCountdown == 0 && globalData.allData){

    }
}
</script>

<style scoped>
footer.tabbar {
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 999;
    width: 100%;
    height: 56px;
    background: #26292f;
}

footer.tabbar ul {
    display: flex;
}

footer.tabbar li {
    flex: 1;
    position: relative;
    text-align: center;
    line-height: 0;
}

footer.tabbar li img {
    display: inline-block;
    margin-top: 6px;
    width: 25px;
    height: 25px;
    font-size: 0;
}

footer.tabbar li h6 {
    margin-top: 3px;
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    color: #797a7c;
}

footer.tabbar li h6.active {
    color: #ff3d6f;
    background-color: transparent;
}

.fullscreen-box {
    position: absolute;
    z-index: 9999;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
}

.fullscreen-box img {
    width: 100%;
    height: 100%;
}

.fullscreen-box span {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 30px;
    height: 30px;
    line-height: 30px;
    background: #797a7c;
    color: #aaa;
    border-radius: 50%;
    text-align: center;
}

.saveBox {
    width: 100%;
    height: 60px;
}

.saveBox.mint-popup {
    background: transparent;
}

.popbox {
    margin: 0 15px 5px 15px;
    height: 55px;
    position: relative;
    color: #444;
    font-size: 16px;
}

.popbox h6 {
    display: block;
    height: 40px;
    line-height: 40px;
    padding: 0 10px;
    background: #fff;
    border-radius: 10px;
    text-align: left;
}

.popbox cite {
    position: absolute;
    bottom: 8px;
    left: 50%;
    margin-left: -8px;
    display: block;
    width: 15px;
    height: 15px;
    background: #fff;
    transform: rotate(45deg);
}

.popbox img {
    width: 26px;
    height: 26px;
    vertical-align: middle;
}

.popbox img.homelogo {
    margin-right: 10px;
}
.ggbox {
  width:75vw;
  min-height: 50vw;
  padding: 10px;
  border-radius: 10px;
  background-color: white;
  display:flex;
  justify-content: space-between;
  flex-direction: column;
}
.gtitle {
   text-align: left;
   color: black;
}
.gcontent {
   display: flex;
   justify-content: center;
   align-items: center;
   height: 100%;
   color: #444;
}
.gaction {
   display: flex;
   justify-content: flex-end;
   align-self: flex-end;
}
</style>



