<template>
  <div class="vippkgbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
      <div class="actionbox">
        <router-link to="/ucp/payment/listing" class="text"></router-link>
      </div>
    </header>
    <div class="planbox">
      <dl>
        <dt><h1><em>VIP</em><cite></cite></h1></dt>
        <dd>
          <ul>
            <li v-for="pkgrow in pkgrows" :key="pkgrow.pkgid">
              <a href="#">
                <input type="radio" name="pkgid" :value="pkgrow.pkgid" :checked="pkgid==pkgrow.pkgid" v-model="pkgid">
                <i>
                  <span>
                    <p>{{ pkgrow.pkgname }}</p>
                    <p>{{ pkgrow.rmbprice.replace('.00', '') }}</p>
                  </span>
                </i>
              </a>
              <em v-if="pkgrow.recommend"></em>
            </li>
          </ul>
        </dd>
      </dl>
    </div>

    <div class="paybox">
      <h2>
        <a href="#" :class="{active: paytype == 'ol'}" @click.prevent="chPayWay('ol')"></a>
        <a href="#" :class="{active: paytype == 'cs'}" @click.prevent="chPayWay('cs')"></a>
        <span><router-link to="/ucp/payment/help"></router-link></span>
      </h2>
      <div class="olpay" :class="{show: paytype == 'ol'}">
        <ul v-if="olpays.length">
          <li v-for="(payway, index) in olpays" :key="index">
            <dl>
              <dt><img :src="payway.paylogo"></dt>
              <dd>
                {{ payway.payname }}
              </dd>
            </dl>
            <input type="radio" name="olpaycode" :value="payway.paycode" v-model="olPayCode"><i></i>
          </li>
        </ul>
        <div class="olintro" v-else></div>
      </div>
      <div class="cspay" :class="{show: paytype == 'cs'}" v-if="cspays.length">
        <ul>
          <li v-for="(payway, index) in cspays" :key="index">
            <dl>
              <dt><img src="/static/icons/icon_gfkf.png"></dt>
              <dd>
                
              </dd>
            </dl>
            <input type="radio" name="cspaycode" :value="payway.paycode" v-model="csPayCode"><i></i>
          </li>
        </ul>
        <div class="csintro">
          <p>1.1V1</p>
          <p>2.</p>
          <p>3.:</p>
        </div>
      </div>
    </div>

    <h2 class="list-caption mtop10">
      <em></em>
      <span><router-link to="/ucp/payment/feedback">24</router-link></span>
    </h2>

    <div class="paybar-pad"></div>
    <div class="paybar" v-if="currPkg()">
      <dl v-if="paytype == 'ol'">
        <dt>
          <h3> <em>{{ currPkg()['rmbprice'].replace('.00', '') }}</em></h3>
          <h4>{{ currPkg()['pkgname'] }}</h4>
        </dt>
        <dd><a href="#" class="submit" @click.prevent="submit"></a></dd>
      </dl>
      <ul v-else>
        <li><router-link to="/ucp/payment/safepaylog"><em></em></router-link></li>
        <li><router-link to="/ucp/payment/feedback"><cite></cite></router-link></li>
        <li><a href="https://szzero.livechatvalue.com/chat/chatClient/chatbox.jsp?companyID=1147655&configID=64536&jid=5719809332&s=1" class="submit" target="_blank"></a></li>
      </ul>
    </div>

    <mt-popup v-model="payurlPopupVisible" :closeOnClickModal="false" position="center" class="payurlbox">
      <div class="payurl-confirm">
        <a :href="pay_url" @click="payurlClose" target="_blank"><em></em></a>
      </div>
    </mt-popup>

    <mt-popup v-model="paybackPopupVisible" :closeOnClickModal="false" position="center" class="paybackbox">
      <dl class="payback-confirm">
        <dt><a href="#" @click.prevent="payFailed"></a></dt>
        <dd><a href="#" @click.prevent="paySuccess"></a></dd>
      </dl>
    </mt-popup>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'vippkg-index',
  data() {
    return {
      user: globalData.user,
      pkgrows: [],
      pkgid: 0,
      olpays: [],
      cspays: [],
      paytype: '',
      olPayCode: '',
      csPayCode: '',
      paybackPopupVisible: false,
      payurlPopupVisible: false,
      pay_url: '',
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    chPayWay (chan){
      this.paytype = chan;
    },
    currPkg(){
      for(let i = 0; i < this.pkgrows.length; i++){
        if(this.pkgrows[i]['pkgid'] == this.pkgid){
          return this.pkgrows[i];
        }
      }
      return null;
    },
    submit(){
      var self = this;
      var pkgid = self.pkgid;
      var paycode = '';
      if(self.paytype == 'ol'){
        paycode = self.olPayCode;
      }else{
        paycode = self.csPayCode;
      }
      if(pkgid == 0 || paycode == ''){
        return self.$toast('');
      }
      if(paycode == 'safepay.safepay'){ //
        return;
      }

      HttpApi.vippkgPlaceorder(pkgid, paycode)
      .then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        let payid = res.json['data']['payid'];
        return HttpApi.paymentReqpay(payid);
      }).then(function(res){
        if(res.errcode != 0){
          self.$toast('');
          return;
        }
        if(res.json['retcode'] != 0){
          self.$toast(res.json['errmsg']);
          return;
        }
        let pay_url = res.json['data']['pay_url'];
        self.pay_url = pay_url;
        self.payurlPopupVisible = true;
      });
    },
    payFailed(){
      var self = this;
      self.paybackPopupVisible = false;
      self.$toast('');
    },
    paySuccess(){
      var self = this;
      self.paybackPopupVisible = false;
      self.$toast('');
      setTimeout(function(){
        self.$router.go(-1);
      }, 1500);
    },
    payurlClose(){
      var self = this;
      self.payurlPopupVisible = false;
      self.paybackPopupVisible = true;
    }
  },
  mounted() {
    var self = this;
    HttpApi.vippkgIndex(this.$route.query.id)
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.pkgrows = res.json['data']['pkgrows'];
      self.pkgrows.forEach(function(pkgrow){
        if(pkgrow['recommend']){
          self.pkgid = pkgrow['pkgid'];
        }
      });
      res.json['data']['payments'].forEach(function(payment){
        payment['payways'].forEach(function(payway){
          if(payway['paycode'] == 'safepay.safepay'){
            self.cspays.push(payway);
          }else{
            self.olpays.push(payway);
          }
        });
      });
      if(self.olpays.length){
        self.olPayCode = self.olpays[0]['paycode'];
      }
      if(self.cspays.length){
        self.csPayCode = self.cspays[0]['paycode'];
      }
      self.paytype = self.olpays.length > 0 ? 'ol' : 'cs';
    });
  },
}
</script>

<style scoped>
.vippkgbox{color: #fff;}
.planbox{margin: 66px 0 0 0;}
.planbox dl{margin-top: 10px; padding-bottom: 10px; background: #212429;}
.planbox dt{padding: 15px 15px;}
.planbox dt h1{height: 28px; line-height: 28px; white-space: nowrap; font-size: 14px; color: #fff; padding-left: 35px; background: url(/static/icons/icon_zgvip.png) left center no-repeat; background-size: 28px 22px;}
.planbox dt h1 em{display: inline-block; line-height: 32px; font-style: italic;}
.planbox dt h1 cite{display: inline-block; margin-left: 10px; padding-left: 5px; width: 206px; height: 28px; font-size: 12px; line-height: 28px; font-weight: 300; background: url(/static/icons/vip_plan_bg.png) left center no-repeat; background-size: 206px 20px;}
.planbox dt h2{height: 18px; line-height: 18px; font-size: 12px; color: #aaaaaa; margin-top: 10px;}
.planbox dt .textblock{padding: 5px 0 10px 0; line-height: 22px; font-size: 14px; color: #fff;}
.planbox dd{padding: 0 10px;}
.planbox dd ul{overflow: hidden;}
.planbox dd li{width: 33.33%; height: 0; padding-top: 33.33%; position: relative; float: left;}
.planbox dd li a{display: block; position: absolute; left: 5px; top: 5px; bottom: 5px; right: 5px;}
.planbox dd li input{width: 100%; height: 100%; position: absolute; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; background-color: transparent; border: 0; opacity: 0; cursor: pointer;}
.planbox dd li input + i{display: flex; align-items: center; justify-content: center; position: absolute; top: 0; left: 0; right: 0; bottom: 0; background-color: #382730; cursor: pointer; border: 1px solid #90314c;}
.planbox dd li input:checked + i{border: 0; background: url('/static/icons/vip_plan_sel.png') center center no-repeat; background-size: 100% 100%;}
.planbox dd li span{display: block; font-size: 14px; color: #FFF;}
.planbox dd li span p{font-size: 18px;}
.planbox dd li span p:last-child{margin-top: 10px; font-size: 24px; color: #ff3d6f; font-weight: bold;}
.planbox dd li em{position: absolute; top: 0; left: 0; display: inline-block; width: 44px; height: 19px; background: url('/static/icons/vip_tj.png') center center no-repeat; background-size: 44px 19px;}

.paybox{margin-top: 10px; background: #212429;}
.paybox h2{height:46px; line-height: 40px; color: #fff; background: #212429; overflow: hidden; border-bottom: 1px solid #2f3236;}
.paybox h2 a{float: left; height:46px; line-height: 40px; padding: 0 15px; color: #aaaaaa; font-weight: 300; position: relative;}
.paybox h2 a.active{color: #fff; font-weight: 500;}
.paybox h2 a.active::after{position: absolute; left: 50%; bottom: 5px; content: '.'; font-size: 0; width: 30px; height: 3px; margin-left: -15px; background: #ff3d6f; border-radius: 1px;}
.paybox h2 span{float: right; height:46px; line-height: 46px;}
.paybox h2 span a{color: #f53c6b; line-height: 46px; font-size: 12px; background: url(/static/icons/icon_czbz.png) left center no-repeat; background-size: 14px 14px;}
.paybox .olpay, .paybox .cspay{display: none;}
.paybox .olpay.show, .paybox .cspay.show{display: block;}
.paybox li{padding: 0 15px; position: relative;}
.paybox dl{overflow: hidden; font-size: 14px;}
.paybox dt{float: left; width: 40px; height: 60px;}
.paybox dt img{margin-top: 10px; width: 40px; height: 40px; border-radius: 50%;}
.paybox dd{position: relative; height: 60px; margin-left: 50px; line-height: 60px;}
.paybox dd::after{position: absolute; left: 0; bottom: 0; width: 100%; height: 1px; background: #2f3236; content: '.'; font-size: 0;}
.paybox li:last-child dd::after{display: none;}
.paybox li input{position: absolute; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; z-index: 1; background-color: transparent; border: 0; opacity: 0; cursor: pointer;}
.paybox li input + i{position: absolute; top: 20px; right: 15px; width: 20px; height: 20px; border: 1px solid #545558; border-radius: 50%;}
.paybox li input + i::before{position: absolute; display: none; left: 4px; top: 4px; width: 10px; height: 10px; content: '.'; font-size:0; background: #f00; border-radius: 50%;}
.paybox li input:checked + i{border: 1px solid #ff3d6f; color: #fff;}
.paybox li input:checked + i::before{display: block;}
.paybox .olintro{padding: 15px; font-size: 12px; line-height: 16px; color: #aaa;}
.paybox .csintro{padding: 15px; font-size: 12px; line-height: 16px; color: #aaa;}

.list-caption{height: 50px; position: relative; font-size: 14px; text-indent: 15px; line-height: 50px; background: #212429;}
.list-caption em{font-size: 14px; color: #fff;}
.list-caption span{position: absolute; right: 10px;}
.list-caption span a{color: #f53c6b; padding: 0 10px; font-size: 12px; font-weight: 300; background: url('/static/icons/arrow.png') right center no-repeat; background-size: 7px 12px;}

.paybar-pad{height: 56px;}
.paybar{width: 100%; height: 56px; position: fixed; z-index: 99; bottom: 0; font-size: 14px; line-height: 56px; background: #26292f;}
.paybar dl{overflow: hidden;}
.paybar dt{width: 50%; float: left; padding-left: 15px;}
.paybar dd{width: 50%; float: left;}
.paybar dt h3{margin-top: 8px; height:20px; line-height: 20px; font-size: 16px; color: #fff;}
.paybar dt h3 em{color: #f53c6c; font-size: 16px;}
.paybar dt h4{height:20px; line-height: 20px; font-size: 14px; color: #aaa;}
.paybar dd a.submit{display: block; line-height: 56px; text-align: center; font-size: 16px; color: #fff; background: linear-gradient(to right, #e90fa8, #fc6b49);}

.paybar ul{display: flex;}
.paybar li{flex: 1; color: #fff; text-align: center;}
.paybar li a{color: #fff;}
.paybar li em, .paybar li cite{display: inline-block; padding-left: 18px;}
.paybar li em{background: url('/static/icons/icon_ddcx.png') left center no-repeat; background-size: 15px 16px;}
.paybar li cite{background: url('/static/icons/icon_czfk.png') left center no-repeat; background-size: 15px 16px;}
.paybar li a.submit{display: block; line-height: 56px; text-align: center; font-size: 16px; color: #fff; background: linear-gradient(to right, #e90fa8, #fc6b49);}

.paybackbox{width: 100%; background: transparent;}
.payback-confirm{overflow: hidden; margin: 0 50px;}
.payback-confirm dt, .payback-confirm dd{width: 50%; float: left; text-align: center;}
.payback-confirm a{display: inline-block;; border-radius: 20px; line-height: 30px; color: #fff;}
.payback-confirm dt a{margin-right: 10px; background: #9E9E9E; padding: 5px 20px;}
.payback-confirm dd a{margin-left: 10px; background: #673ab7; padding: 5px 20px;}

.payurlbox{width: 100%; height: 100%; background: transparent;}
.payurl-confirm{overflow: hidden; height: 100%; text-align: center;}
.payurl-confirm a{display: block; height: 100%; display: flex; justify-content: center; align-items: center;}
.payurl-confirm a em{display: inline-block; border-radius: 20px; line-height: 30px; color: #fff; background: #f53c6b; padding: 10px 20px;}

</style>



