<template>
  <div class="accountbox">
    <header class="navbar">
      <div class="leadingbox">
        <a class="back" @click="goBack"><img src="/static/icons/back.png"></a>
      </div>
      <div class="titlebox"></div>
    </header>
    <div class="headerbox">
      <dl>
        <dt>
          <h6></h6>
          <h2> {{ max2rmb }}</h2>
        </dt>
        <dd><span></span><em><i></i>{{ goldcoin }}</em></dd>
      </dl>
    </div>
    <div class="tipsbox">10:1101100101000100...</div>
    <ul class="listbox">
      <li><router-link to="/ucp/withdraw/index" class="a1"></router-link></li>
      <li><router-link to="/ucp/coinlog/bonuslog" class="a2"></router-link></li>
      <li><router-link to="/ucp/vippkg/coinorder" class="a3"></router-link></li>
      <li><router-link to="/ucp/promotion/index" class="a4"></router-link></li>
      <li><router-link to="/ucp/taskbox/index" class="a5"> !</router-link></li>
    </ul>
  </div>
</template>

<script>
import HttpApi from '@/httpapi.js';
import globalData from '@/global-data.js';

export default {
  name: 'account-index',
  data() {
    return {
      rows: [],
      max2rmb: 0,
      goldcoin: 0,
    }
  },
  methods: {
    goBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
  },
  mounted() {
    var self = this;
    HttpApi.accountIndex()
    .then(function(res){
      if(res.errcode != 0 || res.json['retcode'] != 0){
        self.$toast('');
        return;
      }
      self.account = res.json['data']['account'];
      self.max2rmb = res.json['data']['max2rmb'];
      self.goldcoin = res.json['data']['goldcoin'];
    });
  },
}
</script>

<style scoped>
.accountbox{color: #fff;}
header.navbar{background: transparent;}

.headerbox{width: 100%; height: 200px; padding: 56px 15px 0 15px; background: url('/static/icons/account-bg.png') center top no-repeat; background-size: cover;}
.headerbox dt{height: 80px;}
.headerbox dt h6{font-size: 14px; line-height: 32px;}
.headerbox dt h2{margin-top: 10px; font-size: 32px; line-height: 32px;}
.headerbox dd{margin-top: 14px; height: 50px; line-height: 50px; font-size: 14px;}
.headerbox dd span{display: inline-block; height: 100%; vertical-align: top;}
.headerbox dd em{margin-left: 10px; display: inline-block; height: 100%; font-size: 18px; padding-left: 23px; background: url('/static/icons/icon_bi.png') left center no-repeat; background-size: 20px 20px;}
.headerbox dd em i{font-size: 20px; font-weight: 300; color: #ccc; padding-right: 2px;}

.tipsbox{padding: 10px 15px; color: #ae4a64; font-size: 14px;}
.listbox{font-size: 14px; color: #fff;}
.listbox li{height: 56px; position: relative; margin-top: 10px; line-height: 56px; background: #212429;}
.listbox li::after{position: absolute; content: '.'; font-size: 0; width: 7px; height: 56px; top: 0; right: 15px; background: url('/static/icons/arrow.png') right center no-repeat; background-size: 7px 13px;}
.listbox li a{display: block; width: 100%; height: 100%;  line-height: 56px; padding: 0 15px 0 45px; font-size: 15px; color: #fff;}
.listbox li a.a1{background: url('/static/icons/icon_xjtx2.png') 15px center no-repeat; background-size: 20px 20px;}
.listbox li a.a2{background: url('/static/icons/icon_syjl.png') 15px center no-repeat; background-size: 20px 20px;}
.listbox li a.a3{background: url('/static/icons/icon_hydh2.png') 15px center no-repeat; background-size: 20px 20px;}
.listbox li a.a4{background: url('/static/icons/icon_kstg.png') 15px center no-repeat; background-size: 20px 20px;}
.listbox li a.a5{background: url('/static/icons/icon_zrwkbx.png') 15px center no-repeat; background-size: 20px 20px;}

</style>



